import React from 'react'

import defaults from '../_defaults/components'
import componentOptions from '../componentOptions'


let OPTIONS = {}

/*
 * BODY
 *
 */
// LevelBody
OPTIONS.LevelBody = componentOptions.LevelBody;
function LevelBody(props) {
	const Level = defaults.body.LevelBody;
	
	return (
		<Level {...props} />
	);
}


export default {
	_options: OPTIONS,
	body: {
		LevelBody
	},
	action: {
	}
}