import React, { useState, useEffect, useRef, useCallback } from 'react'
import clsx from 'clsx'

import CircularProgress from '@material-ui/core/CircularProgress'
import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import Hidden from '@material-ui/core/Hidden'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import Icon from '../../components/Icon'
import Dialog from '../../components/Dialog'

import { joinConfig } from '../../helpers/utils'
import helpers from '../../helpers/timeConverter'
import Connection from '../../controller/Connection'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	centered: {
		margin: '0 auto'
	},
	temperature: {
		textAlign: 'center'
	},
	currentTemperature: {
		fontSize: '2.4rem'
	},
	forecast: {
		marginTop: 32
	},
	Day: {
		textAlign: 'center',
		fontSize: '0.9rem',
		borderLeft: '1px solid ' + theme.palette.divider,
		
		'&:hover': {
			backgroundColor: theme.palette.background.default,
			cursor: 'pointer'
		}
	},
	noBorder: {
		border: 0
	}
}));


// preload icons
let ICONS = {}

function get() {
['set1','set2','set3','set4','set5','set5-white'].forEach(set => {
	ICONS.weather = ICONS.weather || {};
	ICONS.weather[set] = ICONS.weather[set] || {};
	
	for (let icon = 1; icon<=22; icon++) {
		try {
			ICONS.weather[set][icon] = ICONS.weather[set][icon] || null;
			ICONS.weather[set][icon] = require('./icons/weather/' + set + '/' + icon + '.png').default;
		}
		catch(err) {
			//Log._('Weather', i18n.t(err.message), 'warn', true, err.stack);
		}
	}
});

['set1','set2','set5-white'].forEach(set => {
	ICONS.wind = ICONS.wind || {};
	ICONS.wind[set] = ICONS.weather[set] || {};
	
	for (let icon = 1; icon<=104; icon++) {
		try {
			ICONS.wind[set][icon] = ICONS.weather[set][icon] || null;
			ICONS.wind[set][icon] = require('./icons/wind/' + set + '/' + icon + '.png').default;
		}
		catch(err) {
			//Log._('Weather', i18n.t(err.message), 'warn', true, err.stack);
		}
	}
});
}

function getIcon(icon, set, type = 'weather') {
	
	try {
		return ICONS[type] && ICONS[type][set] && ICONS[type][set][icon] ? ICONS[type][set][icon] : null;
	}
	catch(err) {
		Log._('Weather', i18n.t(err.message), 'error', true, err.stack);
	}
	
	return null;
}

export default function Weather(props) {
	const classes = useStyles();
	let config = joinConfig(props.config, require('./Weather.config.js').default);
	
	get();
	
	// get localidad / affiliate_id from url
	if (config.apikey.indexOf('http') > -1) {
		const configKeys = {
			'localidad': 'localid',
			'affiliate_id': 'apikey'
		}
		
		const params = config.apikey.substr(config.apikey.indexOf('?')+1).split('&');
		params.forEach(param => {
			const [ key, value ] = param.split('=');
			if (configKeys[key]) {
				config[configKeys[key]] = value;
			}
		});
	}
	
	const [data,setData] = useState(null);
	const [day,setDay] = useState(false);
	const timer = useRef(null);
	
	const setDayData = useCallback(day => {
		setDay(data.day[day]);
	}, [data]);
	
	useEffect(() => {
		
		if (config.apikey && config.localid) {
			
			const options = {
				'url': 'http://api.daswetter.com/index.php?v=3.0&api_lang=de&localidad={localid}&affiliate_id={apikey}'.replace('{apikey}', config.apikey).replace('{localid}', config.localid),
				'responseType': 'json'
			}
			
			function refresh() {
				Connection.getConnection.getRemoteResource(options)
					.then(data => {
						
						if (data.error !== undefined) {
							Log._('Weather', i18n.t(data.error), 'error');
							return false;
						}
						
						setData(data);
						timer.current = setTimeout(refresh, 60*60*1000); // refresh once an hour
					})
					.catch(err => {
						timer.current = setTimeout(refresh, 60*60*1000); // refresh once an hour
						Log._('Weather', i18n.t(err.message), 'error', true, err.stack);
					});
			}
			
			refresh();
			return () => clearTimeout(timer.current);
		}
		
	}, [config.apikey, config.localid]);
	
	if (!data) {
		return (
			<div style={{ margin: '0 auto' }}>
				<CircularProgress size={32} />
			</div>
		);
	}
	else if (!data.day || !data.day[1]) {
		return <Typography>{i18n.t('Incorrect localidad or affiliate_id given! Please provide exact localidad and affiliate_id in module config!')}</Typography>
	}
	
	const today = data.day[1];
	const now = today.hour[parseInt(new Date().getHours()/3)];
	const forecast = [ ...Array(5).keys() ].map((item, day) => {
		return (
			<Day
				key={'Day-' + (day+1)}
				{...data.day[(day+1)]}
				day={(day+1)}
				config={config}
				setDay={setDayData}
				/>
		);
	});
	
	const dayView = day && day.hour.map((hour, index) => (
		<ListItem divider key={'hour-' + hour.interval + '-' + index}>
			<ListItemIcon>
				<strong style={{ margin: 'auto 0' }}>{hour.interval}</strong><br />
				<img src={getIcon(hour.symbol_value, config.iconSetWeather)} alt="" height="30" style={{ margin: '0 32px 0 16px' }} />
			</ListItemIcon>
			<ListItemText
				primary={hour.symbol_description}
				secondary={[
					<span key={'hour-' + hour.interval + '-' + index + '-A'}><Icon small icon="mdi-thermometer" style={{ marginLeft: 8 }} />{hour.temp} {today.units.temp}</span>,
					<span key={'hour-' + hour.interval + '-' + index + '-B'}><Icon small icon="mdi-umbrella" style={{ marginLeft: 8 }} />{hour.rain} {today.units.rain}</span>,
					<span key={'hour-' + hour.interval + '-' + index + '-C'}><Icon small icon="mdi-cloud" style={{ marginLeft: 8 }} /> {hour.clouds}</span>
				]}
				/>
			
			<Hidden xsDown>
			<ListItemSecondaryAction >
				<Grid container>
					<Grid item>
						<div style={{ lineHeight: '42px' }}>
							<img src={getIcon(hour.wind.symbolB, config.iconSetWind, 'wind')} alt="" height="20" style={{ margin: '0 8px 0 16px' }} />
						</div>
					</Grid>
					<Grid item>
						{i18n.t('WindDirection#' + hour.wind.dir) + ' (' + hour.wind.dir + ')'}<br />
						{hour.wind.speed}-{hour.wind.gusts} {today.units.wind}
					</Grid>
				</Grid>
			</ListItemSecondaryAction>
			</Hidden>
		</ListItem>
	));
	
	return (
		<>
			<Dialog
				open={day !== false}
				onClose={() => {setDay(false)}}
				header={i18n.t('Weather Forecast')}
				disableBackdropClick={false}
				disableEscapeKeyDown={false}
				dense
				fullWidth
				maxWidth="sm"
				>
				
				<List dense>
					{dayView}
				</List>
			</Dialog>
			
			{(!config.display || config.display === 'both' || config.display === 'overview') && <Grid container>
				<Grid container item xs={8} alignContent="center">
					<Grid item xs={6}>
						<div style={{ textAlign: 'center', padding: '0 8px' }}>
							{now.symbol_description}<br />
							<img src={getIcon(now.symbol_value, config.iconSetWeather)} alt="" height="60" />
						</div>
					</Grid>
					<Grid item xs={6}>
						<div className={classes.currentTemperature}>{now.temp} {today.units.temp}</div>
						Feels like {now.temp} {today.units.temp}
					</Grid>
				</Grid>
				<Grid item xs={1}>
					<Divider orientation="vertical" classes={{ root: classes.centered }}/>
				</Grid>
				<Grid item xs={3}>
					<div style={{ textAlign: 'left' }}>
						<Tooltip title={i18n.t('Rain')} arrow placement="left"><Typography variant="caption" display="block"><Icon icon="mdi-umbrella" small/> {now.rain} {today.units.rain}</Typography></Tooltip>
						<Tooltip title={i18n.t('Wind')} arrow placement="left"><Typography variant="caption" display="block"><Icon icon="mdi-weather-windy" small/> {now.wind.speed}-{now.wind.gusts} {today.units.wind}</Typography></Tooltip>
						<Tooltip title={i18n.t('Pressure')} arrow placement="left"><Typography variant="caption" display="block"><Icon icon="mdi-shore" small/> {now.pressure} {today.units.pressure}</Typography></Tooltip>
						<Tooltip title={i18n.t('Snowline')} arrow placement="left"><Typography variant="caption" display="block"><Icon icon="mdi-snowflake-melt" small/> {now.snowline} {today.units.snowline}</Typography></Tooltip>
					</div>
				</Grid>
			</Grid>}
			
			{((config.display === 'both' || config.display === 'preview') && (config.forecastSunRain !== false || config.forecastTemperature !== false || config.forecastWind !== false)) && <Grid container justify="space-between" className={clsx((!config.display || config.display === 'both' || config.display === 'overview') && classes.forecast)} spacing={1}>
				{forecast}
			</Grid>}
		</>

	);
}

function Day(props) {
	const classes = useStyles();
	const { day, date, config } = props;
	
	
	const NAME = day === 1 ? i18n.t('today') : (day === 2 ? i18n.t('tomorrow') : helpers.parseDate(date, 'EEEE'))
	const ABBR = day === 1 ? i18n.t('TDY') : (day === 2 ? i18n.t('TMRW') : helpers.parseDate(date, 'E'))
	
	const DayRef = useRef();
	const [name,setName] = useState(NAME);
	useEffect(() => {
		setName(NAME);
	}, [NAME]);
	
	const responsive = useCallback(el => {
		DayRef.current = el;
		
		if (el && el.offsetWidth <= 80 && name === NAME) {
			setName(ABBR);
		}
		else if (el && el.offsetWidth > 80 && name === ABBR) {
			setName(NAME);
		}
	}, [NAME, ABBR, name]);
	
	const ref = useCallback(el => responsive(el), [responsive]);
	
	useEffect(() => {
		const listener = () => responsive(DayRef.current);
		window.addEventListener('resize', listener);
		
		return () => window.removeEventListener('resize', listener);
		
	}, [responsive]);
	
	return (
		<Grid
			classes={{ root: clsx(classes.Day, day === 1 && classes.noBorder) }}
			onClick={() => props.setDay(day)}
			item
			style={{ width: '20%' }}
			>
			
			<Typography ref={ref} variant="overline" display="block" style={{ lineHeight: 'inherit' }}><strong>{name}</strong></Typography>
			<Typography variant="body2" display="block">{helpers.parseDate(date, 'd. MMM')}</Typography>
			
			{config.forecastTemperature !== false && <div style={{ margin: '8px 0 4px 0' }}>
				<Typography variant="body1" style={{ display: 'inline-block' }} noWrap><Icon small icon="mdi-thermometer" /> {props.tempmax + ' ' + props.units.temp} </Typography>
				<Typography variant="body2" style={{ display: 'inline-block' }} noWrap>&nbsp;/ {props.tempmin + ' ' + props.units.temp}</Typography>
				<br />
			</div>}
			
			{config.forecastSunRain !== false && <>
				<img src={getIcon(props.symbol_value, props.config.iconSetWeather)} alt="" height="50" />
				<br />
			
				<Typography variant="caption" display="block">{props.rain > 0 ? (props.rain + ' ' + props.units.rain) : '-'}</Typography>
				<br />
			</>}
			
			{config.forecastWind !== false && <>
				<img src={getIcon(props.wind.symbol, props.config.iconSetWind, 'wind')} alt="" height="25" />
				<br />
				
				<Typography variant="caption" display="block">
					{props.wind.speed + ' - ' + props.wind.gusts}<br />
					{props.units.wind}
				</Typography>
			</>}
			
		</Grid>
	);
}