/*
 * Code by MATERIAL-UI
 * @see https://material-ui.com/components/tables/#sorting-amp-selecting
 *
 */
import React, { useEffect } from 'react'

import { makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableContainer from '@material-ui/core/TableContainer'
import TablePagination from '@material-ui/core/TablePagination'

import EnhancedTableToolbar from '../components/EnhancedTableToolbar'
import EnhancedTableHead from '../components/EnhancedTableHead'
import EnhancedTableBody from '../components/EnhancedTableBody'


const useStyles = makeStyles(theme => ({
	table: {
	}
}));


/*
 *
 */
function descendingComparator(a, b, orderBy, orderByCase) {
	
	let orderA = '', orderB = '';
	
	//
	if (typeof orderBy === 'string' && orderBy.indexOf('.') === -1) {
		orderA = a[orderBy] || '';
		orderB = b[orderBy] || '';
	}
	
	//
	else if (typeof orderBy === 'string' && orderBy.indexOf('.') > -1) {
		let tmpA = { ...a };
		let tmpB = { ...b };
		let key = orderBy;
		let index;
		
		while (key.indexOf('.') > -1) {
			
			try {
				index = key.substr(0, key.indexOf('.'));
				key = key.substr(key.indexOf('.')+1);
				tmpA = tmpA[index];
				tmpB = tmpB[index];
			}
			catch(err) {
				console.error(err.message);
			}
		}
		
		orderA = tmpA[key] || '';
		orderB = tmpB[key] || '';
	}
	
	//
	if (orderByCase !== true) {
		orderA = typeof orderA === 'object' ? JSON.stringify(orderA) : (typeof orderA === 'string' ? orderA.toLowerCase() : orderA);
		orderB = typeof orderB === 'object' ? JSON.stringify(orderB) : (typeof orderB === 'string' ? orderB.toLowerCase() : orderB);
	}
	
	// cast types
	orderA = orderA.replace(/:/, '');
	orderB = orderB.replace(/:/, '');
	
	orderA = Number.isNaN(parseFloat(orderA)) ? orderA : parseFloat(orderA);
	orderB = Number.isNaN(parseFloat(orderB)) ? orderB : parseFloat(orderB);
	
	return orderA === orderB ? 0 : (orderA > orderB ? -1 : 1);
}

/*
 *
 */
function getComparator(order, orderBy, orderByCase) {
	return order === 'desc'
		? (a, b) => descendingComparator(a, b, orderBy, orderByCase)
		: (a, b) => -descendingComparator(a, b, orderBy, orderByCase);
}

/*
 *
 */
function stableSort(array, comparator) {
	array.sort((a, b) => comparator(a, b));
	return array;
}

/*
 *
 */
export default function EnhancedTable(props) {
	const { title, headCells, size, CustomTableBody, rows, toolbar, selectable, pagination, defaultOrder, defaultOrderBy, orderByCase, defaultSelected, defaultPage, defaultRowsPerPage, toolbarRightActionButton } = props;
	const classes = useStyles();
	
	const [order, setOrder] = React.useState(defaultOrder || 'asc');
	const [orderBy, setOrderBy] = React.useState(defaultOrderBy || headCells[0].orderBy || headCells[0].id || headCells[0].label || headCells[0]);
	const [selected, setSelected] = React.useState(defaultSelected || []);
	const [page, setPage] = React.useState(defaultPage || 0);
	const [rowsPerPage, setRowsPerPage] = React.useState(pagination ? (defaultRowsPerPage || 5) : rows.length);

	useEffect(() => setRowsPerPage(pagination ? (defaultRowsPerPage || 5) : rows.length), [rows.length, pagination, defaultRowsPerPage]);


  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = event => {
    if (event.target.checked) {
      const newSelecteds = rows.map(n => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const selectRow = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const TableBody = CustomTableBody || EnhancedTableBody;
  return (
	<React.Fragment>
		{toolbar !== false &&
        <EnhancedTableToolbar
		  title={title}
          rowCount={rows.length}
		  numSelected={selected.length || 0}
		  rightActionButton={toolbarRightActionButton}
		  />}
		
        <TableContainer>
          <Table
            className={classes.table}
			size={size}
			stickyHeader={true}
          >
            <EnhancedTableHead
              classes={classes}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
			  {...props}
            />
			
			<TableBody
				columns={headCells}
				sortedRows={stableSort(rows, getComparator(order, orderBy, orderByCase)).slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)}
				emptyRows={rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage)}
				onClick={selectable !== false && selectRow}
				selected={selected}
				{...props}
				/>
			
          </Table>
        </TableContainer>
		{pagination && <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />}
	</React.Fragment>
	
  );
}