import React, { useRef, useState, useEffect, useCallback } from 'react'
import clsx from 'clsx'

import CircularProgress from '@material-ui/core/CircularProgress'
import Icon from '../../components/Icon'
import IconButton from '@material-ui/core/IconButton'
import Fab from '@material-ui/core/Fab'
import Tooltip from '@material-ui/core/Tooltip'

import PlayIcon from '@material-ui/icons/PlayArrow'
import PauseIcon from '@material-ui/icons/Pause'
import StopIcon from '@material-ui/icons/Stop'
import PreviousIcon from '@material-ui/icons/SkipPrevious'
import NextIcon from '@material-ui/icons/SkipNext'

import Menu from '../../components/Menu'

import { allSettled, joinConfig } from '../../helpers/utils'
import helpers from '../../helpers/timeConverter'
import Cache from '../../controller/Cache'
import Connection from '../../controller/Connection'
import i18n from '../../controller/i18n'
//import Log from '../../controller/Log'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	container: {
		width: '100%'
	},
	noMargin: {
		margin: theme.spacing(0, -2),
		width: 'calc(100% + ' + theme.spacing(4) + 'px)',
		
		[theme.breakpoints.down('md')]: {
			margin: 0,
			width: '100%',
		}
	},
	image: {
		//height: '200px',
		display: 'flex',
		alignItems: 'flex-end',
		backgroundPosition: 'center',
		//backgroundSize: 'auto 100%',
		backgroundRepeat: 'no-repeat'
	},
	noImage: {
		backgroundColor: theme.palette.primary.main,
		display: 'flex'
	},
	information: {
		display: 'flex',
		backgroundColor: theme.palette.primary.main + '66',
		color: '#fff',
		padding: theme.spacing(),
		margin: 0,
		width: '100%',
		//minHeight: '45px'
	},
	informationLeft: {
		width: '80%',
		margin: 0,
		textAlign: 'left'
	},
	informationRight: {
		width: '19%',
		margin: 'auto 0 0 0',
		textAlign: 'right',
		
		[theme.breakpoints.down('md')]: {
			width: 'auto',
		}
	},
	length: {
		backgroundColor: theme.palette.primary.main,
		width: '100%',
		height: '7px',
		//cursor: 'pointer'
	},
	lengthBar: {
		backgroundColor: theme.palette.secondary.main,
		height: '100%',
	},
	controls: {
		display: 'flex',
		alignItems: 'stretch',
		padding: theme.spacing(0, 2)
	},
	controlsBox: {
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		paddingTop: theme.spacing(2)
	},
	controlsBoxLeft: {
		flex: 1,
		flexDirection: 'column',
		alignItems: 'baseline'
	},
	controlsBoxCenter: {
		flex: 3
	},
	controlsBoxRight: {
		flex: 1,
		justifyContent: 'end'
	},
	controlButton: {
		margin: '0 5px'
	},
	controlButtonSmall: {
		margin: '0 10px',
		fontSize: '1.2rem',
		height: '32px',
		width: '32px'
	}
}));


export default function MediaControl(props) {
	const classes = useStyles();
	let config = joinConfig(props.config, require('./MediaControl.config.js').default);
	
	const [lastUpdatedStateKey,setLastUpdatedStateKey] = useState(null);
	
	const [player,setPlayer] = useState(null);
	const [media,setMedia] = useState({});
	
	const players = useRef([]);
	const adapter = useRef({});
	const adapterStates = useRef({});
	
	try {
		adapter.current = require('./adapters/' + (config.adapter.substr(0, config.adapter.indexOf('.'))) + '.js');
		adapterStates.current = adapter.current.default;
	}
	catch(err) {
		console.error('No adapter selected');
	}
	
	// get player node
	const getPlayerNode = useCallback((state = '') => {
		return !player ? null : ((config.adapter + adapterStates.current._namespace + state).replace('{PLAYER}', player.id || ''));
		
	}, [config.adapter, player]);
	
	// get player
	useEffect(() => {
		adapter.current.getPlayers(config.adapter).then(p => {
			players.current = p;
			
			// set player
			if (p.length > 0) {
				const tmpPlayerId = media.player || Cache.get('MediaControl.player.' + props.id) || p[0].id;
				setPlayer(p[p.findIndex(pl => pl.id === tmpPlayerId)] || p[0].id);
			}
			
			// no player found
			else {
				setLastUpdatedStateKey(true);
			}
		});
		
	}, [config.adapter, media.player, props.id]);
	
	// get player states
	useEffect(() => {
		const socket = Connection.connection;
		let subscribedStates = [];
		
		if (player) {
			Cache.set('MediaControl.player.' + props.id, player.id);
			
			try {
				// get current player states
				let promises = [];
				for (let stateKey in adapterStates.current) {
					let s = adapterStates.current[stateKey];
					s = typeof s === 'string' ? { 'state': s } : s;
					
					if (s && s.state) {
						s.stateKey = stateKey;
						
						// get initial states
						promises.push(new Promise((resolve, reject) => {
							socket.getState(getPlayerNode(s.state))
								.then(state => resolve({ 'val': s.convert ? s.convert(state.val) : state.val, 'stateKey': stateKey }))
								.catch(reject);
						}));
						
						// subscribe changes
						subscribedStates.push(s.state);
						if (stateKey !== 'progress') {
							socket.subscribeState(getPlayerNode(s.state), (state, value) => {
								
								if (value && value.val !== undefined) {
									setLastUpdatedStateKey(stateKey);
									setMedia(prevPlayer => {
										let nextPlayer = { ...prevPlayer || {} }
										nextPlayer[stateKey] = s.convert ? s.convert(value.val) : value.val;
										return nextPlayer;
									});
								}
							});
						}
					}
				}
				
				allSettled(promises).then(result => {
					let p = {}
					result.forEach(res => {
						if (res.status === 'fulfilled' && res.value && res.value.val !== undefined) {
							p[res.value.stateKey] = res.value.val;
						}
					});
					
					setLastUpdatedStateKey(true);
					setMedia(p);
				});
			}
			catch(err) {
				console.error(err);
			}
		}
		
		return () => socket.unsubscribeStates(subscribedStates);
		
	}, [config.adapter, player, getPlayerNode, props.id]);
	
	// set actions
	const set = useCallback((stateKey, val = true) => {
		
		if (adapterStates.current[stateKey] && adapterStates.current[stateKey].action) {
			const node = getPlayerNode(adapterStates.current[stateKey].action);
			if (node) {
				const socket = Connection.connection;
				socket.setState(node, adapterStates.current[stateKey].setter ? adapterStates.current[stateKey].setter(val, getPlayerNode) : val)
					.catch(console.error);
			}
		}
	}, [getPlayerNode]);
	
	// no data retrieved (so far)
	//console.log(config, player);
	if (!lastUpdatedStateKey) {
		return (
			<div style={{ margin: '0 auto' }}>
				<CircularProgress size={32} />
			</div>
		);
	}
	
	// no player found
	else if (lastUpdatedStateKey && player === null) {
		return (
			<p>{i18n.t('No player found') + '!'}</p>
		);
	}
	
	// Actions
	let playElements = [];
	
	// Action - Pause
	if (media.play && adapterStates.current.pause && adapterStates.current.pause.action) {
		playElements.push({ action: 'pause', el: <PauseIcon  /> });
	}
	// Action - Stop
	if (media.play && adapterStates.current.stop && adapterStates.current.stop.action) {
		playElements.push({ action: 'stop', el: <StopIcon /> });
	}
	// Action - Play
	if (!media.play && adapterStates.current.play && adapterStates.current.play.action) {
		playElements.push({ action: 'play', el: <PlayIcon /> });
	}
	
	// icons
	const icons = {
		'repeat': {
			'off': 'repeat-off',
			'item': 'repeat-once',
			'all': 'repeat',
		},
		'shuffle': {
			'off': 'shuffle-disabled',
			'on': 'shuffle-variant',
		},
		'volume': {
			'off': 'volume-variant-off',
			'_default': 'volume-high'
		}
	}
	
	return (
		<div className={clsx(classes.container, props.widgetConfig.fullscreen !== true && classes.noMargin)}>
			<div className={clsx(media.cover ? classes.image : classes.noImage)} style={media.cover ? { height: config.coverHeight + 'px', backgroundImage: 'url(' + (config.coverUrl || '') + media.cover + ')', backgroundSize: (config.coverMode ? 'auto 100%' : '100% auto') } : {}}>
				
				<div className={classes.information}>
					<p className={classes.informationLeft}>
						{media.artist ? <>{media.artist}<br /></> : ''}
						{(media.album ? media.album + (media.year ? ' (' + media.year + ')' : '') + ' - ' : '') + (media.track || '') + (!media.album && media.year ? ' (' + media.year + ')' : '')}
					</p>
					{(media.progress || media.duration) ? (
					<p className={classes.informationRight}>
						<Timer s={{ ...adapterStates.current.progress, 'state': getPlayerNode(adapterStates.current.progress.state) }} timer={media.progress} isPlaying={media.play} max={media.duration} set={set} divider={media.duration} />
						{media.duration && <Timer s={{ ...adapterStates.current.duration, 'state': getPlayerNode(adapterStates.current.duration.state) }} timer={media.duration} isPlaying={false} set={set} />}
					</p>) : null}
				</div>
			</div>
			{media.progress !== undefined && media.duration ? (
			<Timer
				s={{ ...adapterStates.current.progress, 'state': getPlayerNode(adapterStates.current.progress.state) }}
				timer={media.progress}
				isPlaying={media.play}
				showProgressBar={media.duration}
				set={set}
				/>) : null}
			
			<div className={classes.controls}>
				<div className={clsx(classes.controlsBox, classes.controlsBoxLeft)}>
					{players.current.length > 0 && (
					<Menu
						icon="monitor-cellphone-star"
						_onChange={p => {
							if (p) {
								adapter.current.setPlayer && adapter.current.setPlayer(getPlayerNode);
								setPlayer(p);
							}
						}}
						options={players.current.map(player => ({ ...player, 'label': player.name }))}
						selected={player && player.id}
						/>)}
					
					{/*<MediaIcon {...props} icons={icons.volume} player={player} set={set} stateKey='volume' s={adapterStates.current.volume || {}} />*/}
				</div>
				<div className={clsx(classes.controlsBox, classes.controlsBoxCenter)}>
					
					{media.type !== 'RADIO' && adapterStates.current.previous && adapterStates.current.previous.action && (
					<Fab className={classes.controlButton} size="small" color="secondary" onClick={() => set('previous')}>
						<PreviousIcon />
					</Fab>)}
					
					{playElements.map((playElement, i) => 
					<Fab key={'icon-' + playElement.action} className={classes.controlButton} size="large" color="primary" onClick={() => set(playElement.action)}>
						{playElement.el}
					</Fab>
					)}
					
					{media.type !== 'RADIO' && adapterStates.current.next && adapterStates.current.next.action && (
					<Fab className={classes.controlButton} size="small" color="secondary" onClick={() => set('next')}>
						<NextIcon />
					</Fab>)}
					
				</div>
				<div className={clsx(classes.controlsBox, classes.controlsBoxRight)}>
					<MediaIcon {...props} icons={icons.repeat} media={media} set={set} stateKey='repeat' s={adapterStates.current.repeat || {}} modes={['off', 'item', 'all']} />
					<MediaIcon {...props} icons={icons.shuffle} media={media} set={set} stateKey='shuffle' s={adapterStates.current.shuffle || {}} />
				</div>
			</div>
		</div>
	);
}

/*
 *
 */
function MediaIcon(props) {
	const { media, icons, s, stateKey, set, modes } = props;
	const classes = useStyles();
	
	
	// get state
	let state = null;
	let icon = icons._default || icons.off;
	
	for (let key in s.display) {
		state = s.display[key];
		
		if (state === media[stateKey]) {
			icon = icons[key];
			state = key;
			break;
		}
	}
	
	// get next state
	let next = null;
	if (modes) {
		const index = modes.indexOf(state);
		next = modes[(index+1)] || modes[0];
	}
	else {
		next = state === 'off' ? 'on' : 'off';
	}
	
	// no state given
	if (!s || !s.state) {
		return null;
	}
	
	// no action
	else if (s && !s.action) {
		return (
			<Icon icon={icon} />
		);
	}
	
	// setter
	const setter = () => set(stateKey, s.display[next] || next);
	
	// state including action
	return (
		<Tooltip title={i18n.t(stateKey + ' ' + state)}>
			<IconButton size="small" className={classes.controlButtonSmall} onClick={setter}>
				<Icon icon={icon} />
			</IconButton>
		</Tooltip>
	);
}

/*
 *
 */
function Timer(props) {
	const { s, set, isPlaying, showProgressBar, max, divider } = props;
	const classes = useStyles();
	
	
	const [timer,setTimer] = useState(props.timer);
	const timerRef = useRef();
	const ref = useRef();
	
	// subscribe to changes
	useEffect(() => {
		const socket = Connection.connection;
		
		function addSecond(max = null) {
			clearTimeout(timerRef.current);
			
			if (isPlaying) {
				setTimer(prevTimer => max === null || prevTimer < max ? (++prevTimer) : prevTimer);
				timerRef.current = setTimeout(() => addSecond(max), 1000);
			}
		}
		
		if (s.state) {
			socket.subscribeState(s.state, (state, value) => {
				if (value && value.val !== undefined) {
					setTimer(s.convert ? s.convert(value.val) : value.val);
					
					// start timer
					if (!timerRef.current) {
						addSecond(max);
					}
				}
			});
		}
		
		addSecond(max);
		return () => {
			clearTimeout(timerRef.current);
			//socket.unsubscribeState(s.state);
		}
		
	}, [s, isPlaying, max]);
	
	//
	const isProgressModifiable = s.action !== undefined && s.action !== null;
	const setProgress = useCallback(e => {
		set('progress', Math.round((e.pageX-ref.current.offsetParent.offsetLeft) / ref.current.offsetWidth * showProgressBar));
		
	}, [set, showProgressBar]);
	
	//
	if (showProgressBar === undefined) {
		return helpers.timeToHoursMinutesSeconds(max ? (timer <= max ? timer : max) : timer) + (divider ? ' / ' : '');
	}
	
	return (
		<div className={classes.length} ref={ref} onClick={isProgressModifiable ? setProgress : (() => {})} style={{ cursor: isProgressModifiable ? 'pointer' : '' }}>
			<div key={'Timer-' + showProgressBar} className={classes.lengthBar} style={!ref.current ? { width: 0 } : { width: !timer || !timer ? 0 : (timer <= showProgressBar ? (Math.round(timer / showProgressBar * ref.current.offsetWidth) + 'px') : '100%') }}></div>
		</div>
	);
}
