import React from 'react'
import clsx from 'clsx'
import i18n from '../../controller/i18n'

import AddIcon from '@material-ui/icons/AddCircle'
import Box from '@material-ui/core/Box'
import CircularProgress from '@material-ui/core/CircularProgress'
import InputButton from '@material-ui/core/Button'

import Field from './Field'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	InputButton: {
		width: '100%'
	},
	color: props => ({
		color: !props.variant || props.variant === 'contained' ? '#fff' : (theme.palette[props.color] ? theme.palette[props.color].main : theme.palette.primary.main),
		backgroundColor: !props.variant || props.variant === 'contained' ? (theme.palette[props.color] ? theme.palette[props.color].main : theme.palette.primary.main) : undefined,
		
		'&:hover': {
			backgroundColor: !props.variant || props.variant === 'contained' ? (theme.palette[props.color] || theme.palette.primary).dark : undefined,
			
			// Reset on touch devices, it doesn't add specificity
			'@media (hover: none)': {
				backgroundColor: 'transparent',
			}
		}
	})
}));


/*
 *
 */
export default function Button(props) {
	const { id, label, variant, disabled, loading, startIcon, onClick, className, gapLeft, gapRight } = props;
	const classes = useStyles(props);
	
	const icon = variant !== 'text' && startIcon === undefined ? <AddIcon /> : startIcon;
	
	
	
	return (
		<>
			{gapLeft && <Box flexGrow={1} />}
			<Field {...props} width={variant === 'text' ? 'auto' : props.width}>
				<InputButton
					key={id}
					classes={{ root: clsx(classes.color, classes.InputButton, className) }}
					variant={variant || 'contained'}
					color={props.color && ['primary', 'secondary'].indexOf(props.color) > -1 ? props.color : 'primary'}
					disabled={disabled}
					startIcon={loading ? <CircularProgress size={24} /> : (icon || null)}
					onClick={onClick}
					>
					
					{i18n.t(label)}
				</InputButton>
			</Field>
			{gapRight && <Box flexGrow={1} />}
		</>
	)
}