import React from 'react'
import clsx from 'clsx'
import { InlineIcon } from '@iconify/react-with-api';

/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	defaultListIcon: styles => ({
		textAlign: 'center',
		...((theme.components && theme.components.icon) || {})
	}),
	styledListIcon: styles => styles,
	small: {
		fontSize: '1.2rem !important'
	}
}));


export default React.forwardRef((props, ref) => {
	const { icon, noDefault, small, styles, color, className, ...otherProps } = props;
	const classes = useStyles({ 'color': color, ...styles });
	
	// no icon given
	if (!icon) {
		return null;
	}
	
	// base64 image as icon
	if (icon.indexOf('data:image') > -1 && icon.indexOf('base64') > -1) {
		return (
			<img ref={ref} widh="32" height="32" className={clsx(classes.defaultListIcon, classes.styledListIcon, className)} src={icon} alt="" {...otherProps} />
		);
	}
	
	// icon
	const prefix = icon.indexOf(':') > -1 ? icon.substr(0, icon.indexOf(':')) : 'mdi';
	const iconWithoutPrefix = icon.indexOf(':') > -1 ? icon.substr(icon.indexOf(':')+1) : icon;
	const [ iconify, options ] = iconWithoutPrefix.split(' ');
	
	return (
		<span ref={ref}>
			<InlineIcon
				{...otherProps}
				
				className={clsx('icon', prefix + ':' + iconify.replace('mdi-', ''), noDefault !== true && classes.defaultListIcon, classes.styledListIcon, small && classes.small, className, options)}
				icon={prefix + ':' + iconify.replace('mdi-', '')}
				/>
		</span>
	);
});
