import React, { useState, useEffect, useRef, useCallback } from 'react'
import ReactDOMServer from 'react-dom/server'
import clsx from 'clsx'
import { v4 as uuid } from 'uuid'

import L from 'leaflet'
import { Map as LeafletMap, TileLayer, Marker as LeafletMarker, Tooltip as LeafletTooltip } from 'react-leaflet'

import Avatar from '@material-ui/core/Avatar'
import Box from '@material-ui/core/Box'
import Divider from '@material-ui/core/Divider'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import Icon from '../../components/Icon'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Settings from '../../controller/Settings'
import Log from '../../controller/Log'

import { joinConfig } from '../../helpers/utils'
import useWindowDimensions from '../../helpers/useWindowDimensions'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	map: {
		width: '100%',
		height: '100%',
		position: 'relative'
	},
	mapOverview: {
		color: '#666',
		position: 'absolute',
		left: 0,
		bottom: 0,
		width: '100%',
		height: '60px',
		zIndex: 999,
		display: 'flex',
		alignItems: 'center',
		marginLeft: theme.spacing(2),
		
		'& > .AvatarContainer': {
			margin: theme.spacing(1),
			cursor: 'pointer',
		},
	},
	mapIconContainer: {
	},
	mapIconParent: style => ({
		backgroundColor: '#fff',
		boxShadow: '0 0 0 8px rgba(255, 255, 255, 0.3)',
		borderRadius: '50%',
		width: '100%',
		height: '100%',
		...style
	}),
	mapIcon: style => ({
		fontSize: '2rem',
		color: theme.palette.primary.main,
		display: 'block',
		height: '40px',
		margin: 'auto',
		...style,
		backgroundColor: 'transparent',
		
		'&:after': {
			content: '""',
			display: 'block',
			padding: '50% 0%'
		}
	}),
	mapTooltip: {
	},
	Divider: {
		width: 2,
		height: '80%',
		backgroundColor: 'rgba(0, 0, 0,0.2)',
		margin: theme.spacing(0, 0.5, 1.5, 0.5),
	},
	AvatarContainer: {
	},
	Avatar: {
		margin: '0 auto'
	},
	AvatarName: {
		margin: 0,
		fontSize: '0.8rem',
		textAlign: 'center'
	}
}));


function getPosition(pos) {
	
	// json formatted
	if (typeof pos === 'string' && pos.indexOf('{') > -1 && pos.indexOf('}') > -1) {
		
		try {
			pos = JSON.parse(pos);
			pos = [ pos.lat || pos.latitude, pos.lon || pos.lng || pos.longitude ];
		}
		catch(err) {
			Log._('Modules (Map)', err.message, 'error', true, err.stack);
			pos = null;
		}
	}
	
	// string
	else if (pos && (typeof pos === 'string' || Array.isArray(pos))) {
		pos = Array.isArray(pos) ? pos : pos.split(',');
	}
	
	// wrong type
	else {
		pos = '';
	}
	
	return pos;
}

export default function Map(props) {
	const { devices } = props;
	const classes = useStyles();
	
	// get config
	const config = joinConfig(props.config, require('./Map.config.js').default);
	
	// get from settings
	const settingsLatitude = Settings.get('iobroker').latitude;
	const settingsLongitude = Settings.get('iobroker').longitude;
	
	// set states
	const [markers, setMarkers] = useState({});
	const [position, setPosition] = useState(getPosition(config.defaultPosition));
	
	useEffect(() => { // useEffect necessary because settings are retrieved async from backend
		if (!position && settingsLatitude && settingsLongitude) {
			setPosition([ settingsLatitude, settingsLongitude ]);
		}
		
	}, [ position, settingsLatitude, settingsLongitude ]);
	
	// get map height based on viewport
	const { height: windowHeight } = useWindowDimensions();
	const [mapHeight, setMapHeight] = useState(windowHeight);
	
	useEffect(() => {
		const AppBarHeight = document.getElementById('AppBar').offsetHeight;
		setMapHeight(windowHeight - AppBarHeight);
		
	}, [windowHeight]);
	
	// map instance
	const map = useRef();
	const memoizedMap = useCallback(function initMap() {
		map.current = this;
	}, []); // needs to be "function" for 'this' to be accessible
	
	// Marker click in overview
	const memoizedHandleClick = useCallback(flyTo => map.current.flyTo(Array.isArray(flyTo) ? flyTo : flyTo.split(',')), []);
	
	// get gps position coordinates
	useEffect(() => {
		let callbacks = [];
		
		// request device state and attach listener for every device
		Devices.forEachDevice(devices, (device, index, options) => {
			
			// device
			if (device._isDevice) {
				const primaryStateKey = options.primaryStateKey || 'position';
				
				// state change
				callbacks[index] = (stateKey, state, device) => {
					if (stateKey === primaryStateKey) {
						setMarkers(prevMarkers => {
							const marker = prevMarkers[device.id];
							if (!marker || marker[stateKey] !== state.val) {
								return { ...prevMarkers, [device.id]: { stateKey, device } };
							}
							
							return prevMarkers;
						});
					}
				}
				
				// listen for updates
				device.listen(callbacks[index], false);
				
				// request device state
				device.requestDeviceState(primaryStateKey).catch(error => {
					Log._('Modules (Map)', error.message, 'error', true, error.stack);
				});
			}
			
			// divider
			else {
				setMarkers(prevMarkers => ({ ...prevMarkers, [uuid()]: { stateKey: 'divider', device: null }}));
			}
		});
		
		// componentWillUnmount
		return () => Devices.forEachDevice(devices, (device, index, options) => callbacks[index] && device.unlisten(callbacks[index]));
		
	}, [devices]);
	
	
	// add markers
	let mapMarkers = [], mapOverview = [];
	if (devices.length > 0 && devices.length === Object.keys(markers).length) {
		
		for (let key in markers) {
			const { stateKey, device } = markers[key];
			
			//
			if (stateKey === 'divider') {
				mapOverview.push(<Divider key={key} orientation="vertical" classes={{ root: classes.Divider }} />);
			}
			
			//
			else if (device !== null && device.states[stateKey] && device.states[stateKey].state && device.states[stateKey].state.value && device.states[stateKey].state.value.val) {
				const markerPosition = getPosition(device.states[stateKey].state.value.val);
				if (markerPosition) {
					const icon = device.states[stateKey].icon || device.icon;
					mapMarkers.push(<Marker key={'marker-' + device.id} stateKey={stateKey} position={markerPosition} icon={icon} device={device} config={config} />);
					
					let item = null;
					if (icon && icon.indexOf('data:image') > -1) {
						item = <Avatar key={'overview-' + device.id} alt={device.name} src={icon} onClick={() => memoizedHandleClick(markerPosition)} />
					}
					else {
						const mdiIcon = device.getIcon(stateKey, null, 'map-marker');
						const styles = device.getStyle('icon', stateKey);
						
						item = (
							<Avatar classes={{ root: classes.Avatar }} style={styles} key={'overview-' + device.id} alt={device.name} onClick={() => memoizedHandleClick(markerPosition)}>
								<Icon icon={mdiIcon} style={{ color: '#fff' }} />
							</Avatar>
						);
					}
					
					mapOverview.push(
						<Tooltip key={'tooltip-' + device.id} title={device.name} aria-label={device.name} arrow>
							<div className={clsx('AvatarContainer', classes.AvatarContainer)}>
								{item}
								<p className={classes.AvatarName}>{device.name}</p>
							</div>
						</Tooltip>
					);
				}
			}
		}
	}
	
	return (
		<div className={classes.map} style={{ height: !props.fullscreen ? (config.height + 'px') : mapHeight }}>
			<Box className={classes.mapOverview}>
				<Typography>{i18n.t('Jump to')}:</Typography>
				{mapOverview}
			</Box>
			<LeafletMap useFlyTo={true} center={position || [53.5505, 9.9925]} zoom={config.defaultZoom <= 18 ? config.defaultZoom : 18} className={classes.map} whenReady={memoizedMap}>
				<TileLayer
					attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
					url='https://{s}.tile.osm.org/{z}/{x}/{y}.png'
					/>
				
				{mapMarkers}
			</LeafletMap>
		</div>
	);
}


function Marker(props) {
	const { position, device, stateKey, config } = props;
	const classes = useStyles(device.getStyle('icon', stateKey, null));
	
	let markerIcon = null, offset = 40;
	//Log._('Modules (Map)', 'Marker for ' + device.function + ' ' + device.name + ' added to map.');
	
	// Marker with avatar / picture
	if (props.icon && props.icon.indexOf('data:image') > -1) {
		markerIcon = L.divIcon({
			className: classes.mapIconContainer,
			html: '<img src="' + props.icon + '" class="' + classes.mapIconParent + '" />',
			iconSize: [ 64, 64 ]
		});
	}
	
	// Marker with Icon
	else {
		offset = 30;
		const mdiIcon = device.getIcon(stateKey, null, device.icon || 'map-marker');
		
		markerIcon = L.divIcon({
			className: classes.mapIconContainer,
			html: ReactDOMServer.renderToString(
				<div className={classes.mapIconParent}>
					<Icon className={classes.mapIcon} icon={mdiIcon} />
				</div>),
			iconSize: [ 40, 40 ]
		});
	}
	
	// Marker MouseOver / MouseOut (bring tooltip to front if markers overlay)
	const memoizedHandleMouseEvent = useCallback(function() {this.closeTooltip().openTooltip()}, [],);

	return (
		<LeafletMarker riseOnHover={true} position={Array.isArray(position) ? position : position.split(',')} icon={markerIcon} onMouseOver={memoizedHandleMouseEvent}>
			<LeafletTooltip className={classes.mapTooltip} direction={config.tooltipDirection} permanent={config.tooltipPermanent} offset={[0,offset]}>
				{device.name}
			</LeafletTooltip>
		</LeafletMarker>
	);
}
