import React, { useState, useEffect, useRef, useCallback } from 'react'
//import { v1 as uuid } from 'uuid'
import { joinConfig, getValueAndUnit } from '../../helpers/utils'


export default function IFrame(props) {
	let config = joinConfig(props.config, require('./iFrame.config.js').default);
	//config.url = config.url && config.url.indexOf('http://') === -1 && config.url.indexOf('https://') === -1 ? 'http://' + config.url : config.url;
	
	//
	const [url, setUrl] = useState(config.url);
	const [height, setHeight] = useState(config.height || '100%');
	const refresher = useRef(null);
	
	// adjust height
	const iFrameComponent = useCallback(el => {
		
		if (el !== null && (props.widgetConfig.minHeight || props.widgetConfig.maxHeight)) {
			const box = el.parentNode;
			const widget = box.parentNode;
			const widgetHeader = widget.getElementsByClassName('MuiCardHeader-root');
			
			const [ h, unit, ] = getValueAndUnit(props.widgetConfig.minHeight || props.widgetConfig.maxHeight);
			setHeight((h - (widgetHeader && widgetHeader[0] ? widgetHeader[0].offsetHeight : 0)) + '' + unit);
		}
	}, [props.widgetConfig.minHeight, props.widgetConfig.maxHeight]);
	
	// mounted
	const mount = () => {
		
		// refresh
		function refresh() {
			setUrl(prevUrl => (prevUrl + ' '));
			refresher.current = setTimeout(() => refresh(), config.refresh*1000);
		}
		
		if (config.url && config.refresh > 0) {
			refresher.current = setTimeout(() => refresh(), config.refresh*1000);
		}
		
		// componentWillUnmount
		return () => refresher.current && clearTimeout(refresher.current);
	}
	useEffect(mount, []);
	
	if (!config.url) {
		return null;
	}
	
	return (
		<iframe
			//id={'iFrame-' + uuid()}
			ref={iFrameComponent}
			style={{ border: 0, display: 'block', height: height, width: '100%' }}
			src={url}
			title={props.title}
			sandbox="allow-modals allow-forms allow-pointer-lock allow-popups allow-same-origin allow-scripts"
			/>
	);
}
