import React, { useState, useEffect, useRef } from 'react'
import { HuePicker, SketchPicker as ColorPicker } from 'react-color'

import helpers from '../../helpers/colorConverter'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import defaults from '../_defaults/components'
import componentOptions from '../componentOptions'


let OPTIONS = {}

/*
 * ACTIONS
 *
 */
// LightColorState
function LightColorState(props) {
	const { stateKey, stateVal = '' } = props;
	
	const [colorSpace, setColorSpace] = useState(stateVal);
	useEffect(() => setColorSpace(stateVal), [stateVal]);
	
	let colors, labels = {};
	if (stateKey === 'rgb' && colorSpace && colorSpace.indexOf(',') > -1) {
		colors = colorSpace.split(',');
		labels = { Red: colors[0], Green: colors[1], Blue: colors[2] };
		
	}
	else if (stateKey === 'hsv' && colorSpace && colorSpace.indexOf(',') > -1) {
		colors = colorSpace.split(',');
		labels = { Hue: colors[0], Saturation: colors[1], Brightness: colors[2] };
	}
	
	const Label = defaults.components.Label;
	let label = [];
	for (let key in labels) {
		label.push(<Label key={key} label={key + ' ' + labels[key]} br={true} />);
	}
	
	return label;
}


/*
 * BODY
 *
 */
// LightTemperatureBody
OPTIONS.LightTemperatureBody = componentOptions.LevelBody;
function LightTemperatureBody(props) {
	const Level = defaults.body.LevelBody;
	
	return (
		<Level {...props} />
	);
}

// LightHueBody
function LightHueBody(props) {
	const { device, stateKey, stateVal = 0, title } = props;
	
	const colorMapper = (stateKey, color) => {
		
		// RGB
		if (stateKey === 'rgb') {
			//return helpers.RgbToHex(color);
		}
		
		// HSV
		else if (stateKey === 'hsv') {
			return color.h || color.hue || 0;
		}
		
		return color || 0;
	}
	
	
	const [hue, setHue] = useState(colorMapper(stateKey, stateVal));
	useEffect(() => {
		setHue(colorMapper(stateKey, stateVal));
	}, [stateKey, stateVal]);
	
	
	const onChange = (color, e) => {
		setHue(Math.round(color.hsl.h));
	}
	
	const onChangeComplete = (color, e) => {
		setHue(Math.round(color.hsl.h));
		device.setDeviceState(stateKey, Math.round(color.hsl.h)).catch(({ error }) => Log._('Function (light)', error.message, 'error', true, error.stack));
	}
	
	const Component = defaults.components.Component;
	const color = helpers.RgbToHex(helpers.HsvToRgb(hue));
	return (
		<Component title={title || stateKey }>
			<HuePicker
				onChange={onChange}
				onChangeComplete={onChangeComplete}
				color={color}
				width='100%'
				/>
				
		</Component>
	);
}

// LightColorBody
function LightColorBody(props) {
	const { device, stateKey, stateVal = '', title } = props;
	
	const colorMapper = (stateKey, color) => {
		
		// RGB
		if (detectColorMode.current === 'rgb-hsv' && stateKey === 'rgb') {
			return helpers.RgbToHex(color);
		}
		
		// HSV
		else if (detectColorMode.current === 'rgb-hsv' && stateKey === 'hsv') {
			return helpers.RgbToHex(helpers.HsvToRgb(color));
		}
		
		// HEX (but not starting # given)
		else if (detectColorMode.current === 'hex') {
			return '#' + color;
		}
		
		return color || '#ffffff';
	}
	
	const detectColorMode = useRef(stateVal.length === 9 && stateVal.indexOf('#') > -1 ? '#hexa' : (stateVal.length === 8 && stateVal.indexOf('#') === -1 ? 'hexa' : (stateVal.length === 7 && stateVal.indexOf('#') > -1 ? '#hex' : (stateVal.length === 6 && stateVal.indexOf('#') === -1 ? 'hex' : 'rgb-hsv'))));
	const [colorSpace, setColorSpace] = useState(colorMapper(stateKey, stateVal));
	useEffect(() => {setColorSpace(colorMapper(stateKey, stateVal));}, [stateKey, stateVal]);
	
	const onChange = color => {
		setColorSpace(color.hex);
		
		// RGB
		if (detectColorMode.current === 'rgb-hsv' && stateKey === 'rgb') {
			color = [color.r, color.g, color.b].join(',');
		}
		
		// HSV
		else if (detectColorMode.current === 'rgb-hsv' && stateKey === 'hsv') {
			color = [color.h, color.s, color.v].join(',');
		}
		
		// HEX (with alpha channel)
		else if (detectColorMode.current === '#hexa' || detectColorMode.current === 'hexa') {
			const alpha = Math.round(color.rgb.a*255/100).toString(16);
			return detectColorMode.current === 'hexa' ? (color.hex.substr(1) + alpha) : (color.hex + alpha);
		}
		
		// HEX
		else if (detectColorMode.current === '#hex' || detectColorMode.current === 'hex') {
			return detectColorMode.current === 'hex' ? color.hex.substr(1) : color.hex;
		}
		
		console.warn(i18n.t('Could not detect color mode! Please use either hex, rgb or hsv and indicate this by naming the state key equivalently (currently named {stateKey})').replace('{stateKey}', stateKey));
		return color.hex;
	}
	
	const onChangeComplete = color => {
		color = onChange(color);
		device.setDeviceState(stateKey, color).catch(({ error }) => Log._('Function (light)', error.message, 'error', true, error.stack));
	};
	
	const Component = defaults.components.Component;
	return (
		<Component title={title || stateKey }>
			<ColorPicker
				onChange={onChange}
				onChangeComplete={onChangeComplete}
				color={colorSpace}
				width='100%'
				/>
				
		</Component>
	);
}


export default {
	_options: OPTIONS,
	body: {
		LightTemperatureBody,
		LightColorBody,
		LightHueBody
	},
	action: {
		LightColorState
	}
}