import Connection from '../../../controller/Connection'


/**
 *
 */
export function setPlayer(playerNode) {
}

/**
 *
 */
export function getPlayers(adapter) {
	let root = adapter;
	
	return new Promise(resolve => {
		const socket = Connection.connection;
		socket.getObjectView('system', 'device', { startkey: root, endkey: root + '.\u9999' }).then(devices => {
			
			let players = [];
			for (let device of devices) {
				players.push({
					'id': device.id.replace(root + '.', ''),
					'name': device.value.common.name
				});
			}
			
			resolve(players);
		});
	});
}

/*
 *
 */
export default {
	"_namespace": ".{PLAYER}",
	
	// information
	"artist": ".netusb.artist",
	"album": ".netusb.album",
	"track": ".netusb.track",
	"year": null,  // nicht verfgbar
	"cover": ".netusb.albumart_url",
	
	// actions
	"play": {
		"state": ".netusb.playback",
		"convert": val => val === 'play',
		"action": ".netusb.playPause"
	},
	"pause": {
		"action": ".netusb.playPause"
	},
	"resume": {
		"action": ".netsub.playPause"
	},
	"stop": {
		"action": ".netsub.stop"
	},
	"previous": {
		"action": ".netsub.prev"
	},
	"next": {
		"action": ".netsub.next"
	},
	
	// attributes
	"progress": {
		"state": null,
		"convert": null,
		"action": null,
		"setter": null
	},
	"duration": {
		"state": ".netusb.total_time"
	},
	
	// options
	"repeat": {
		"state": ".netsub.repeat_stat",
		"action": ".netsub.repeat"
	},
	"shuffle": {
		"state": ".netsub.shuffle_stat",
		"action": ".netsub.shuffle"
	},
	"volume": {
		"state": ".main.volume",
		"action": ".main.volume",
		"display": {
			"off": 0
		}
	}
}
