/*
 * Code by MATERIAL-UI
 * @see https://material-ui.com/components/tables/#sorting-amp-selecting
 *
 */
import React from 'react'
import clsx from 'clsx'
import { format } from 'date-fns'

import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	noWrap: {
		whiteSpace: 'nowrap'
	},
	paddingNone: {
		padding: theme.spacing(0.5)
	}
}));


/*
 *
 * @see https://stackoverflow.com/questions/9382167/serializing-object-that-contains-cyclic-object-value
 */
function nestedObjects(key, val, seen) {
	
	if (val !== null && typeof val === 'object') {
		if (seen.indexOf(val) >= 0) {
			return;
		}
		
		seen.push(val);
	}
	
	return val;
}

/*
 *
 */
function showTableContent(content, type = '') {
	
	if (content === undefined || content === null) {
		return '';
	}
	
	let seen = [];
	switch(type.toLowerCase()) {
		case 'date':
			content = format(content, 'yyyy-MM-dd HH:mm:ss');
			break;
		
		default:
			content = Array.isArray(content) || typeof content === 'object' ? JSON.stringify(content, (key, val) => nestedObjects(key, val, seen)) : content;
			content = typeof content === 'boolean' ? content.toString() : content;
			break;
	}
	
	return content;
}

/*
 *
 */
export default function EnhancedTableBody(props) {
	const { columns, sortedRows, emptyRows } = props;

	return (
		<TableBody>
			{sortedRows.map((row, index) => {
				return (
					<EnhancedTableBodyRow
						key={'EnhancedTableBodyRow' + index}
						{...props}
						index={index}
						row={row}
						/>
				);
			})}
			
			{emptyRows > 0 && (
			<TableRow style={{ height: 53 * emptyRows }}>
				<TableCell colSpan={columns.length} />
			</TableRow>
			)}
			
		</TableBody>
	);
}

/*
 *
 */
function EnhancedTableBodyRow(props) {
	const { columns, index, row, onClick, rowStyles, selected, selectable } = props;
	const classes = useStyles();
	
	const isSelected = id => selected.indexOf(id) !== -1;
	const isItemSelected = isSelected(row.id);
	const labelId = `enhanced-table-checkbox-${index}`;
	
	let styles = {};
	for (let columnId in rowStyles) {
		let rowStyle = rowStyles[columnId];
		
		if (row[columnId] && Object.keys(rowStyle).indexOf(row[columnId]) > -1) {
			styles = { ...styles, ...rowStyle[row[columnId]] };
		}
	}
	
	return (
		<TableRow
			key={'TableRow-' + index}
			hover
			onClick={onClick ? (event => onClick(event, row.id)) : undefined}
			role="checkbox"
			aria-checked={isItemSelected}
			tabIndex={-1}
			selected={isItemSelected}
			>
			
			{selectable !== false &&
			<TableCell
				key={'TableCell-' + index + '-Checkbox'}
				style={styles}
				padding="checkbox"
				>
				
				<Checkbox
					checked={isItemSelected}
					inputProps={{ 'aria-labelledby': labelId }}
					/>
				
			</TableCell>
			}
			
			{columns.map(column => {
				column = typeof column === 'string' ? { 'label': column } : column;
				column.id = column.id || column.label;
				column.disablePadding = column.disablePadding !== undefined ? column.disablePadding : true;
				
				return (
					<TableCell
						key={'TableCell-' + index + '-' + column.id}
						classes={{ root: clsx(column.noWrap && classes.noWrap), paddingNone: classes.paddingNone }}
						align={column.align || 'left'}
						style={styles}
						scope="row"
						padding={column.disablePadding ? 'none' : 'default'}
						>

						{showTableContent(row[column.id], column.type)}
					</TableCell>
				)
			})}

		</TableRow>
	);
}