import React from 'react'
import clsx from 'clsx'

import Log from '../controller/Log'
import Settings from '../controller/Settings'
import Grid from '@material-ui/core/Grid'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	Columns: {
		marginBottom: theme.spacing(),
		paddingLeft: theme.spacing(),
		
		[theme.breakpoints.down('md')]: {
			paddingLeft: theme.spacing(0.4)
		}
	}
}));


export default function Columns(props) {
	const { contents = {} } = props;
	
	const classes = useStyles();
	const columns = Object.keys(contents).length;
	
	let grid = [];
	for (let column = 1; column <= columns; column++) {
		
		if (!contents[column]) {
			Log._('Columns', 'No content for column #' + column + '!', 'warn');
			continue;
		}
		
		grid.push(
			<Grid key={column} item																				// @see https://material-ui.com/customization/breakpoints/
				xs={12}																							//    0 -  600px width
				sm={Math.floor(12/Math.min(Settings.getSetting('columnsOnSmallScreenSizes') || 1, columns))}	//  600 -  960px width
				md={Math.floor(12/Math.min(Settings.getSetting('columnsOnMediumScreenSizes') || 1, columns))}	//  960 - 1280px width
				lg={Math.floor(12/Math.min(Settings.getSetting('columnsOnLargeScreenSizes'), columns))}			// 1280 - 1920px width
				xl={Math.floor(12/Math.min(Settings.getSetting('columnsOnExtraLargeScreenSizes'), columns))}	// 		> 1920px width
				>
				
				{contents[column]}
			</Grid>
		);
	}
	
	return (
		<Grid container spacing={0} className={clsx('jarvis-Columns', classes.Columns)}>
			{grid}
		</Grid>
	);
}
