import React from 'react'
import clsx from 'clsx'

import { format } from 'date-fns'

import Avatar from '@material-ui/core/Avatar'
import CircularProgress from '@material-ui/core/CircularProgress'
import Icon from '../../components/Icon'
import List from '@material-ui/core/List'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import StarIcon from '@material-ui/icons/Star'
import Tooltip from '@material-ui/core/Tooltip'

import Cache from '../../controller/Cache'
import Connection from '../../controller/Connection'
import i18n from '../../controller/i18n'
import { joinConfig } from '../../helpers/utils'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	fullWidth: {
		width: '100%'
	},
	noUpdates: {
		fontStyle: 'italic',
		textAlign: 'right',
		color: 'rgba(0, 0, 0, 0.54)'
	},
	UpdateAvailable: {
		color: '#CCAA00',
	},
	UpdatesListItem: {
		backgroundColor: '#ffd700',
		color: '#fff',
		margin: theme.spacing(0, -2),
		paddingLeft: theme.spacing(2)
	},
	UpdatesIndicator: {
		right: theme.spacing(3.75),
		backgroundColor: '#CCAA00',
		borderRadius: '50%',
		width: theme.spacing(3),
		height: theme.spacing(3),
		color: '#fff',
		textAlign: 'center',
		fontSize: '1rem !important'
	},
	StatusIndicator: {
		fontSize: '1.4rem'
	},
	loading: {
		textAlign: 'center',
		marginTop: '5px'
	},
	small: {
		width: theme.spacing(3),
		height: theme.spacing(3),
	},
	alive: {
		color: '#86d686'
	},
	notAlive: {
		color: '#f7e43b'
	},
	disabled: {
		color: '#bababa'
	}
});


const semver = require('semver');
class AdapterStatus extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			adapterInformation: [],
			adapterAliveStatus: {},
			newUpdates: false,
			updatesJson: '',
			lastUpdateCheck: ''
		}
		
		this.socket = Connection.getConnection;
		this.adapterStates = [];
		this.updates = {};
		this.updateList = [];
		
		// config
		this.config = joinConfig(this.props.config, require('./AdapterStatus.config.js').default);
		this.list = this.config.list ? (Array.isArray(this.config.list) ? this.config.list : this.config.list.replace(/, /g, ',').toLowerCase().split(',')) : null;
		this.blacklist = this.config.blacklist ? (Array.isArray(this.config.blacklist) ? this.config.blacklist : this.config.blacklist.replace(/, /g, ',').toLowerCase().split(',')) : null;
	}
	
	componentDidMount() {
		
		// retrieve updates
		if (this.config.updates !== false) {
			
			// retrieve initial update list
			this.socket.getState('admin.0.info.updatesJson', (err, state) => {
				this.updates = JSON.parse(state.val);
				this._updateAdapterList();
			});
			
			// listen for updates
			this.socket.subscribeState('admin.0.info.updatesJson', (id, update) => {
				this.updates = JSON.parse(update.val);
				this._updateAdapterList();
			});
		}
		
		// trigger initial list
		else {
			this._updateAdapterList();
		}
	}
	
	_updateAdapterList = () => {
		
		// get adapter instances
		const instances = Cache.get('adapters');
		if (instances) {
			let adapters = [];
			this.updateList = [];
			
			// states per instance to retrieve
			instances.forEach((instance, i) => {
				
				// add update
				const availableVersion = (this.updates[instance.value.common.name] && this.updates[instance.value.common.name].availableVersion) || instance.value.common.version;
				instance.update = semver.gt(availableVersion, instance.value.common.version) === true && availableVersion;
				
				// get alive / connected states
				if ((!this.list || (this.list && this.list.indexOf(instance.value.common.name.toLowerCase()) > -1)) && (!this.blacklist || (this.blacklist && this.blacklist.indexOf(instance.value.common.name.toLowerCase()) === -1))) {
					
					adapters.push(instance);
					if (instance.update) {
						this.updateList.push(instance.value.common.name);
					}
					
					// only instances with mode "daemon" may update their status
					if (instance.value.common.mode === 'daemon') {
						this.adapterStates = this.adapterStates.concat([
							instance.id + '.alive',
							instance.id + '.connected',
							instance.id.replace('system.adapter.', '') + '.info.connection'
						]);
					}
				}
			});
			
			// retrieve intial states
			this.socket.getStates(this.adapterStates, (err, adapterAliveStatus) => {
				this.setState({
					adapterAliveStatus,
					adapterInformation: adapters.sort((item1, item2) => {
						if (item1.id > item2.id) return 1;
						else if (item2.id > item1.id) return -1;
						else return 0;
					})
				});
				
				// get state updates
				this.socket.subscribeStates(this.adapterStates, (id, aliveStatus) => {
					
					if (aliveStatus && (!this.state.adapterAliveStatus[id] || (aliveStatus.val !== this.state.adapterAliveStatus[id].val))) {
						this.setState(prevState => ({ adapterAliveStatus: { ...prevState.adapterAliveStatus, [id]: aliveStatus } }));
					}
				});
			});
		}
	}
	
	componentWillUnmount() {
		this.socket.unsubscribeStates([ 'admin.0.info.updatesJson', ...this.adapterStates ]);
	}
	
	render() {
		const { classes } = this.props;
		
		// adapter instances
		let adapterList = <ListItem key='loading' className={classes.loading}><CircularProgress size={20} /></ListItem>
		
		if (this.state.adapterInformation.length > 0) {
			
			adapterList = this.state.adapterInformation.map(adapter => {
				const enabled = adapter.value.common.enabled;
				const alive = (this.state.adapterAliveStatus[adapter.id + '.alive'] && this.state.adapterAliveStatus[adapter.id + '.alive'].val) || false;
				const connected = (this.state.adapterAliveStatus[adapter.id + '.connected'] && this.state.adapterAliveStatus[adapter.id + '.connected'].val) || false;
				const connection = this.state.adapterAliveStatus[adapter.id.replace('system.adapter.', '') + '.info.connection'] ? this.state.adapterAliveStatus[adapter.id.replace('system.adapter.', '') + '.info.connection'].val : true;
				
				const tooltip = i18n.t('Adapter') + ' ' + (enabled ? (alive && connected && connection ? i18n.t('alive') : i18n.t('not alive')) : i18n.t('disabled'));
				return (
					<ListItem key={'adapter-' + adapter.id}>
						<ListItemAvatar>
							<Avatar alt={adapter.value.common.name} src={adapter.value.common.extIcon} className={classes.small} />
						</ListItemAvatar>
						<ListItemText
							primary={adapter.value.common.title}
							secondary={[
								'v' + adapter.value.common.version,
								(adapter.update ? (<span key={'updates-' + adapter.id}>, <span className={classes.UpdateAvailable}>{i18n.t('Update available') + ' v' + adapter.update}</span></span>) : null),
								(adapter.value.common.runAsCompactMode && ', ' + i18n.t('Compact Mode') + ' ' + i18n.t('on') + ' (' + i18n.t('Group') + ' ' + adapter.value.common.compactGroup + ')')
							]}
							/>
						
						<ListItemSecondaryAction className={classes.StatusIndicator}>
							{adapter.value.common.mode === 'daemon' && <>
								<Tooltip title={tooltip} arrow>
									<Icon
										icon={enabled ? (alive && connected && connection ? 'mdi-check-circle' : 'mdi-alert') : 'mdi-close'}
										className={clsx(enabled ? (alive && connected && connection ? classes.alive : classes.notAlive) : classes.disabled)}
										/>
								</Tooltip>
							</>}
						</ListItemSecondaryAction>
					</ListItem>
				)
			});
		}
		
		const updateList = this.updateList.join(', ');
		return (
			<List classes={{ root: classes.fullWidth }} dense>
				{this.updateList.length === 0 ? (this.state.lastUpdateCheck && 
				<ListItem key="updates" classes={{ root: classes.noUpdates }}>
					<ListItemText
						primary={i18n.t('last update') + ': ' + format(new Date(this.state.lastUpdateCheck), 'dd.MM.yyyy HH:mm:ss', { locale: i18n.getLocale() })}
						/>
					
				</ListItem>)
				:
				<Tooltip title={updateList} arrow>
					<ListItem key="updates" classes={{ container: classes.UpdatesListItem }}>
						<ListItemAvatar>
							<StarIcon />
						</ListItemAvatar>
						<ListItemText
							primary={i18n.t('Updates available') + ': ' + (updateList.length > 35 ? updateList.substr(0, 35) + '...' : updateList)}
							/>
						
						<ListItemSecondaryAction className={clsx(classes.StatusIndicator, classes.UpdatesIndicator)}>
							{this.updateList.length}
						</ListItemSecondaryAction>
					</ListItem>
				</Tooltip>
				}
				
				{adapterList}
			</List>
		);
	}
}


export default withStyles(styles)(AdapterStatus);
