import React, { useState, useEffect, useRef } from 'react'

import Connection from '../../controller/Connection'
import { joinConfig, getValueAndUnit } from '../../helpers/utils'


export default function DisplayImage(props) {
	let config = joinConfig(props.config, require('./DisplayImage.config.js').default);
	const [ width, unitWidth ] = getValueAndUnit(config.width);
	const [ height, unitHeight ] = getValueAndUnit(config.height);
	
	//
	const [img,setImg] = useState(getImage(config.url));
	const refresher = useRef(null);
	
	const loaded = () => {
		
		if (img.indexOf('/') > -1 && config.refresh > 0) {
			clearTimeout(refresher.current);
			refresher.current = setTimeout(() => {
				setImg(prevImageUrl => {
					let [ url, params ] = img.split('?');
					
					// remove old timestamp
					if (img.indexOf('?_=') > -1) {
						params = params.substr(params.indexOf('&')+1);
					}
					
					return url + '?_=' + Date.now() + '&' + params;
				});
				
			}, config.refresh*1000);
		}
	}
	
	const mount = () => {
		const socket = Connection.getConnection;
		
		// detect state and retrieve its value
		if (config.url.indexOf('data:image') === -1 && config.url.indexOf('/') === -1) { // && regex.test(config.url)) {
			
			// state change
			const stateChange = (id, state) => id === config.url && state.val && setImg(getImage(state.val));
			
			// subscribe state
			socket.subscribeState(config.url, stateChange);
			
			// request state
			socket.getState(config.url, (err, state) => stateChange(config.url, state));
		}
		
		// componentWillUnmount
		return () => {
			clearTimeout(refresher.current);
			socket.unsubscribeState(config.url);
		}
	}
	useEffect(mount, []);
	
	if (img === null) {
		return null;
	}
	else if (typeof img === 'string' && img.indexOf('<svg') > -1) {
		return (
			<div dangerouslySetInnerHTML={{ __html: img.replace('<svg ', '<svg width="' + (width ? width + unitWidth : '100%') + '" height="' + (height ? height + unitHeight : '100%') + '" ') }} />
		);
	}
	
	return (
		<img
			style={{ display: 'block' }}
			width={width ? width + unitWidth : '100%'}
			height={height ? height + unitHeight : '100%'}
			src={img.trim()}
			title={props.title}
			alt=''
			onLoad={loaded}
			/>
	);
}

function getImage(img) {
	return img.indexOf('data:image') === -1 && img.indexOf('/') === -1 ? null : img;
}