import React, { useState, useEffect } from 'react'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import { joinConfig } from '../../helpers/utils' // stripHtml

import EnhancedTable from '../../components/EnhancedTable'


/*
 *
 */
export default function JsonTable(props) {
	const { id, devices } = props;
	return Devices.forEachDevice(devices, (device, index, options) => <JsonTableElement {...props} key={'JsonTableElement-' + id + '-Device-' + device.id} device={device} options={options} />);
}

/*
 *
 */
function JsonTableElement(props) {
	const { device, options } = props;
	let config = joinConfig(props.config, require('./JsonTable.config.js').default);
	
	
	const [json,setJson] = useState(null);
	useEffect(() => {
		
		// receive state update
		device.listen('stateChange', (stateKey, state, device) => options.primaryStateKey === stateKey && setJson(state.val), false);
		
		// request device state
		device.requestDeviceState(options.primaryStateKey, true).catch(res => {
			Log._('Modules (JsonTable)', res.error.message, 'error', true, res.error.stack);
		});
		
	}, [device, options]);
	
	if (json === null) {
		return null;
	}
	
	let parsedJson = [];
	
	// parse HTML as json
	/*if (json.indexOf('<table') > -1) {
		const tableStart = json.substr(json.indexOf('<table'));
		const tableEnd = tableStart.substr(0, tableStart.indexOf('</table>'));
		const tableOptions = tableEnd.substr(6, tableEnd.indexOf('>')-6);
		const table = tableEnd.substr(tableEnd.indexOf('>')+1);
		
		//console.warn(table);
		//console.warn(tableOptions);
		
		let rows = table.split('<tr');
		rows.shift();
		
		let thead = [];
		let tbody = null;
		
		rows.forEach(row => {
			const columns = row.split('<td');
			columns.shift();
			
			tbody = {};
			columns.forEach((column, i) => {
				column = '<td ' + column;
				
				// set thead
				if (thead.length !== columns.length) {
					thead.push(stripHtml(column));
				}
				
				// add to tbody
				else if (thead[i]) {
					tbody[i] = {
						'val': stripHtml(column),
						'column': thead[i]
					}
				}
			});
			
			if (Object.keys(tbody).length > 0) {
				parsedJson.push(tbody);
			}
		});
	}*/
	
	// parse and validate JSON
	//else {
		try {
			parsedJson = JSON.parse(json);
		}
		catch(err) {
			return i18n.t('Incorrect JSON provided to JsonTable module') + '!';
		}
	//}
	
	// check length
	if (parsedJson.length === 0) {
		return i18n.t('JSON provided to JsonTable module is empty') + '!';
	}
	
	// detect head
	//const head = config.thead || (parsedJson[0].val !== undefined ? { id: } : Object.keys(parsedJson[0]));
	const head = config.thead || Object.keys(parsedJson[0]);
	
	
	return (
		<EnhancedTable
			toolbar={false}
			headCells={head}
			rows={config.max > 0 ? parsedJson.slice(0, config.max) : parsedJson}
			
			defaultOrderBy={config.defaultOrderBy}
			selectable={false}
			sortable={config.sortable}
			stickyHeader
			/>
	);
}
