import Connection from '../controller/Connection'
import Settings from '../controller/Settings'


/**
 * 
 *
 */
export async function encryptAllPasswords(obj) {
	
	return new Promise(resolve => {
		let promise = Promise.resolve();
		
		for (let key in obj) {
			const val = obj[key];
			
			if (typeof val === 'object') {
				encryptAllPasswords(val).then(res => obj[key] = res);
			}
			else if (key === 'pass' || key === 'password') {
				
				promise = new Promise(resolve => {
					Connection.getConnection.getRemoteResource({
						'action': '_encrypt',
						'str': val,
						'secretKey': Settings.getSetting('token')
					})
						.then(res => {
							obj[key] = res;
							resolve(true);
						})
						.catch(console.error);
				});
			}
		}
		
		promise.then(res => {
			resolve(obj);
		});
	});
}

/**
 * 
 *
 */
export function decryptAllPasswords(obj) {
	
	return new Promise(resolve => {
		let promise = Promise.resolve();
		
		for (let key in obj) {
			const val = obj[key];
			
			if (typeof val === 'object') {
				decryptAllPasswords(val).then(res => obj[key] = res);
			}
			else if (key === 'pass' || key === 'password') {
				
				promise = new Promise(resolve => {
					Connection.getConnection.getRemoteResource({
						'action': '_decrypt',
						'hash': val,
						'secretKey': Settings.getSetting('token')
					})
						.then(res => {
							obj[key] = res;
							resolve(true);
						})
						.catch(console.error);
				});
			}
		}
		
		promise.then(res => {
			resolve(obj);
		});
	});
}
