import Cache from './Cache'
//import Connection from './Connection'
import Settings from './Settings'
//import config from '../config/config'


/**
 * Settings
 *
 *
 */
export default class Log {

	/*
	 * holds the log
	 */
	static log = [];

	/**
	 * Add a message to the log
	 *
	 * @param	{String}		module						Module (origin) of log message
	 * @param	{String|Array}	message						Log message
	 * @param	{String}		[criticality=info]			Criticality, one of 'development', 'debug', 'info', 'warn' or 'error'
	 * @param	{Boolean}		[printToConsole=false]		Print log message to console
	 * @param	{Object}		[stack={}]					Stack trace of error message
	 * @return	{Log}
	 */
	static _(module, message, criticality = 'info', printToConsole = true, stack = {}) {
		const params = Cache.get('params');
		
		if ((!params || params.debug != 'true') && (criticality === 'dev' || criticality === 'development')) { // eslint-disable-line eqeqeq
			return false;
		}
		else if (params && params.debug == 'true' && (criticality === 'dev' || criticality === 'development')) { // eslint-disable-line eqeqeq
			criticality = 'debug';
			printToConsole = true;
		}
		
		message = Array.isArray(message) ? message : [message];
		message.forEach(entry => {
			
			const error = {
				time: new Date(),
				module: module,
				criticality: criticality,
				message: entry.toString(),
				stack: stack
			}
			
			Log.log.push(error);
		});
		
		// display in console
		if ((params && params.debug == 'true') || printToConsole) { // eslint-disable-line eqeqeq
			console[criticality].apply(window, [ module + ' #', ...message ]); // @see https://nodejs.org/docs/latest/api/console.html and https://stackoverflow.com/a/11532355
		}
		
		//
		const errorMessage = message.map(entry => typeof entry === 'object' ? JSON.stringify(Object.keys(entry)) : entry).join(' ');
		
		// send to ioBroker log
		/*
		if (Connection.getConnection && config.get('NODE_LOG') && (criticality === 'info' || criticality === 'warn' || criticality === 'error')) {
			const socket = Connection.getConnection;
			
			try {
				socket.setState(config.get('NODE_LOG'), JSON.stringify({ 'criticality': criticality, 'message': errorMessage })).catch(console.error);
			}
			catch(err) {}
		}
		*/
		
		// send error to developer
		const identifier = Cache.get('identifier');
		if (identifier && (criticality === 'warn' || criticality === 'error') && module !== 'Config Data') {
			Settings.sendData({
				'identifier': identifier,
				'module': module,
				'criticality': criticality,
				'error': errorMessage,
				'stack': JSON.stringify(stack, null, 3)
			}, 'error');
		}
	}

	/**
	 * Gets the log
	 *
	 * @param	void
	 * @return	{Array}
	 */
	static getLog() {
		return Log.log;
	}
}
