import React, { useState, useEffect, useRef } from 'react'
import clsx from 'clsx'
import { formatDistanceToNow, differenceInMinutes, lastDayOfMonth, addMinutes, addDays, addMonths, addYears, subDays, format, getWeek, getWeekOfMonth, getDay, parseISO, isAfter, isBefore } from 'date-fns'

import Connection from '../../controller/Connection'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'

import { decryptAllPasswords } from '../../helpers/encryption'
import ucFirst, { joinConfig, allSettled } from '../../helpers/utils'
import { parseDate } from '../../helpers/timeConverter'

import Dialog from '../../components/Dialog'

import Badge from '@material-ui/core/Badge'
import CircularProgress from '@material-ui/core/CircularProgress'
import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import Icon from '../../components/Icon'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import NextIcon from '@material-ui/icons/ChevronRight'
import PreviousIcon from '@material-ui/icons/ChevronLeft'
import PreviewIcon from '@material-ui/icons/DateRange'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';

const size = '30px';
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%'
	},
	centered: {
		textAlign: 'center'
	},
	DaySelected: {
		color: theme.palette.primary.contrastText,
		backgroundColor: theme.palette.primary.light
	},
	Month: {
		display: 'flex',
		justifyContent: 'center',
		marginLeft: size
	},
	MonthlyOverview: {
		marginBottom: theme.spacing(2)
	},
	MonthlyOverviewHeader: {
		lineHeight: '30px',
		margin: theme.spacing(0, 2)
	},
	WeeklyOverviewCw: {
		width: size,
		lineHeight: size,
		
		color: theme.palette.text.primary,
		fontSize: '0.7rem !important',
		textAlign: 'right',
		margin: 'auto 0',
		fontStyle: 'italic',
	},
	WeeklyOverviewCwCurrent: {
	},
	WeeklyOverviewWeekDay: {
		fontSize: '0.9rem',
		width: size,
		lineHeight: size,
	},
	WeeklyOverviewDay: {
		fontSize: '0.9rem',
		width: size,
		lineHeight: size,
		borderLeft: '1px solid ' + theme.palette.background.paper,
		borderTop: '1px solid ' + theme.palette.background.paper,
		
		'&:hover': {
			cursor: 'pointer',
			color: theme.palette.primary.contrastText,
			backgroundColor: theme.palette.primary.main
		}
	},
	WeeklyOverviewToday: {
		fontWeight: 'bold',
		color: theme.palette.primary.contrastText,
		backgroundColor: theme.palette.primary.main
	},
	WeeklyOverviewNextMonth: {
		width: size,
		lineHeight: size,
		
		color: theme.palette.text.secondary,
		fontSize: '0.7rem',
		margin: 'auto 0'
	},
	WeeklyOverviewPrevMonth: {
		width: size,
		lineHeight: size,
		
		color: theme.palette.text.secondary,
		fontSize: '0.7rem',
		margin: 'auto 0'
	},
	Badge: {
		verticalAlign: 'initial'
	},
	badge: {
		minWidth: 14,
		height: 14,
		fontSize: '0.6rem',
		padding: 0,
		right: -3
	},
	horizontalList: {
		display: 'flex',
		flexWrap: 'wrap'
	},
	horizontalListItem: {
		display: 'inline-block',
		textAlign: 'center',
		paddingRight: '16px',
		width: '140px',
		marginTop: '10px'
	},
	horizontalListItemIcon: {
		minWidth: 'auto'
	},
	horizontalListItemText: {
		overflow: 'hidden',
	},
	horizontalListItemAction: {
		textAlign: 'center',
		top: 0,
		right: 0,
		position: 'static',
		transform: 'none',
	},
	pastEvent: {
		'&> *': {
			opacity: 0.2
		}
	},
	iconSize: {
		fontSize: '1.4em'
	}
}));


const _merge = require('deepmerge')
const ical = require('ical');

export default function Calendar(props) {
	let config = joinConfig(props.config, require('./Calendar.config.js').default);
	config.calendars = config.calendars || [];
	
	const [,setUpdated] = useState(Date.now());
	const [calendars,setCalendars] = useState(0);
	const [selectedDate,setSelectedDate] = useState(null);
	const entries = useRef({});
	const recurringEvents = useRef({});
	const events = useRef(null);
	const timer = useRef(null);
	
	useEffect(() => {
		
		const addCalendarEntry = (e, year, month, day, event) => {
			
			e[year] = e[year] || {}
			e[year][month] = e[year][month] || {}
			e[year][month][day] = e[year][month][day] || [];
			e[year][month][day].push(event);
		}
		
		const parseCalendarData = (calendar, data) => {
			let e = {
				'_recurring': []
			}
			
			try {
				data = Object.values(ical.parseICS(data));
				
				// structure events by year, month and day
				data.forEach(item => {
					
					if (item.start) {
						try {
							// parse dates
							item.start = typeof item.start === 'string' ? parseISO(item.start) : item.start;
							item.end = typeof item.end === 'string' ? parseISO(item.end) : item.end;
							
							// recurring
							if (item.rrule && item.rrule.options) {
								e['_recurring'].push({ ...item, 'calendar': calendar });
							}
							
							// single- or multi-day
							else if (item.start) {
								const end = item.end || addDays(item.start, 1);
								
								while (item.start < end) {
									addCalendarEntry(e, item.start.getFullYear(), item.start.getMonth()+1, item.start.getDate(), { ...item, 'calendar': calendar });
									item.start = addDays(item.start, 1);
								}
							}
						}
						catch(err) {
							Log._('Calendar', i18n.t(err.message), 'error', true, err.stack);
							console.debug(item);
						}
					}
				});
			}
			catch(err) {
				Log._('Calendar', i18n.t(err.message), 'error', true, err.stack);
			}
			console.log(e);
			return e;
		}

		// get calendars
		const socket = Connection.getConnection;
		const refresh = () => {
			let promises = [];
			
			config.calendars.forEach(calendar => {
				
				// URL
				if (calendar.url && calendar.url.indexOf('http') > -1) {
					
					// credentials
					let credentials = {}
					if (calendar.user && calendar.pass) {
						credentials = { 'username': calendar.user, 'password': calendar.pass }
					}
					
					promises.push(new Promise((resolve, reject) => {
						decryptAllPasswords(credentials).then(decrypted => {
							
							let options = {
								'url': calendar.url,
								...decrypted
							}
							
							// https
							if (calendar.url.indexOf('https') > -1) {
								options = {
									...options,
									'https': {
										'rejectUnauthorized': calendar.sslignore !== undefined ? (calendar.sslignore === false) : true
									}
								}
							}
							
							// request
							socket.getRemoteResource(options).then(data => resolve({ calendar, data })).catch(reject);
						});
					}));
				}
				
				// local path
				else if (calendar.url && calendar.url.indexOf('http') === -1) {
					promises.push(new Promise((resolve, reject) => {
						socket.sendTo('_readFile', { 'file': calendar.url }).then(data => resolve({ calendar, data })).catch(reject)
					}));
				}
			});
			
			//
			allSettled(promises).then(result => {
				entries.current = {}
				
				result.forEach(res => {
					if (res.status === 'fulfilled' && res.value.data) {
						entries.current = _merge(entries.current, parseCalendarData(res.value.calendar, res.value.data));
					}
					else {
						Log._('Calendar', i18n.t(res.reason), 'error', true);
					}
				});
				
				setCalendars(promises.length);
				setUpdated(Date.now());
			});
			
			//
			clearTimeout(timer.current);
			timer.current = setTimeout(refresh, config.refresh*60*1000);
		}
		
		// refresh initially
		refresh();
		
		// refresh on event
		const updateDay = () => {
			events.current = null;
			setSelectedDate(null);
			refresh();
		}
		
		socket.on('time:midnight', updateDay);
		
		return () => {
			clearTimeout(timer.current);
			socket.off('event', updateDay);
		}
		
	}, [config.calendars, config.refresh]);
	
	// wait until calendars have been retrieved (or failed retrieving)
	if (config.calendars.length > calendars) {
		return (
			<div style={{ margin: '0 auto' }}>
				<CircularProgress size={32} />
			</div>
		);
	}
	
	/**
	 *
	 */
	const indexRecurringEvents = (year, month) => {
		let rEvents = {}
		
		if (entries.current['_recurring']) {
			const endOfMonth = lastDayOfMonth(new Date(year, month-1, 1)).getDate();
			
			for (let recurringEvent of entries.current['_recurring']) {
				const startDate = recurringEvent.rrule.options.dtstart;
				let untilDate = recurringEvent.rrule.options.until;
				const tzOffset = recurringEvent.start.getTimezoneOffset();
				
				// freq (0 = jhrlich, 1 = monatlich, 2 = wchentlich, 3 = tglich)
				const interval = recurringEvent.rrule.options.interval;
				const count = recurringEvent.rrule.options.count;
				const freq = recurringEvent.rrule.options.freq;
				
				if (untilDate === null && count !== null && (freq === 0 || freq.toString().toLowerCase() === 'yearly')) {
					untilDate = addYears(startDate, count) * interval;
				}
				else if (untilDate === null && count !== null && (freq === 1 || freq.toString().toLowerCase() === 'monthly')) {
					untilDate = addMonths(startDate, count) * interval;
				}
				else if (untilDate === null && count !== null && (freq === 2 || freq.toString().toLowerCase() === 'weekly')) {
					untilDate = addDays(startDate, count*7) * interval;
				}
				else if (untilDate === null && count !== null && (freq === 3 || freq.toString().toLowerCase() === 'daily')) {
					untilDate = addDays(startDate, count) * interval;
				}
				
				// steps through month day-by-day
				untilDate = untilDate !== null && new Date((typeof untilDate !== 'number' ? untilDate.getTime() : untilDate) + 0);
				
				for (let day = 1; day <= endOfMonth; day++) {
					const date = new Date(year, month-1, day, startDate.getHours(), startDate.getMinutes());
					//console.log(recurringEvent.summary, recurringEvent, startDate.toISOString(), date.toISOString(), isAfter(startDate, date), day);
					
					// start date beyond date
					if (isAfter(startDate, date)) {
						continue;
					}
					
					// end date given and lower than date
					if (untilDate && isBefore(untilDate, date)) {
						continue;
					}
					
					// date does not match given interval
					if (interval && interval > 1) {
						let startInterval, endInterval;
						
						if (freq === 0 || freq.toString().toLowerCase() === 'yearly') { // yearly
							startInterval = startDate.getFullYear();
							endInterval = year;
						}
						else if (freq === 1 || freq.toString().toLowerCase() === 'monthly') { // monthly
							startInterval = startDate.getMonth();
							endInterval = (month-1);
						}
						else if (freq === 2 || freq.toString().toLowerCase() === 'weekly') { // weekly
							startInterval = getWeek(startDate);
							endInterval = getWeek(date);
						}
						else if (freq === 3 || freq.toString().toLowerCase() === 'daily') { // daily
							startInterval = startDate.getDate();
							endInterval = day;
						}

						// check whether date matches interval
						if ((endInterval-startInterval)%interval !== 0) {
							continue;
						}
					}
					
					//
					const utc = new Date(date.getTime() + (tzOffset*60*1000));
					
					// by month
					const byMonth = recurringEvent.rrule.options.bymonth;
					if (byMonth && Array.isArray(byMonth) && byMonth.length > 0) {
						const _month = freq === 0 || freq.toString().toLowerCase() === 'yearly' ? utc.getMonth()+1 : date.getMonth()+1;
						//console.log(recurringEvent.summary, recurringEvent, byMonth, _month);
						
						if (byMonth.indexOf(_month) === -1) {
							continue;
						}
					}
					
					// by monthday
					const byMonthDay = recurringEvent.rrule.options.bymonthday;
					if (byMonthDay && Array.isArray(byMonthDay) && byMonthDay.length > 0) {
						const _day = freq === 0 || freq.toString().toLowerCase() === 'yearly' ? utc.getDate() : date.getDate();
						//console.log(recurringEvent.summary, recurringEvent, byMonthDay, _day);
						
						if (byMonthDay.indexOf(_day) === -1) {
							continue;
						}
					}
					
					// by nmonthday
					//?
					
					// by weekday
					const byWeekDay = recurringEvent.rrule.options.byweekday; // 0 = Monday
					if (byWeekDay && Array.isArray(byWeekDay) && byWeekDay.length > 0) {
						const _weekDay = getDay(date)-2; // the day of week, 0 represents Sunday (respectively 1 = Monday)
						const origByWeekDay = recurringEvent.rrule.origOptions.byweekday;
						//console.log(recurringEvent.summary, recurringEvent, origByWeekDay, byWeekDay, _weekDay);
						
						if ((origByWeekDay && origByWeekDay.findIndex(wday => wday.weekday === (_weekDay+1 === -1 ? 6 : _weekDay+1)) === -1) || (!origByWeekDay && byWeekDay.indexOf(_weekDay === -2 ? 6 : (_weekDay === -1 ? 5 : _weekDay)) === -1)) {
							continue;
						}
					}
					
					// by nweekday
					const byNWeekday = recurringEvent.rrule.options.bynweekday;
					if (byNWeekday && Array.isArray(byNWeekday) && byNWeekday.length > 0) {
						const _weekDay = getDay(date)-1;
						const _nweekday = [ _weekDay === -1 ? 6 : _weekDay, getWeekOfMonth(date, { locale: i18n.getLocale(), weekStartsOn: 1 }) ];
						
						if (byNWeekday.findIndex(wday => wday.toString() === _nweekday.toString()) === -1) {
							continue;
						}
					}
					
					//
					let multiDayStartDateTime = date;
					const differenceMinutes = differenceInMinutes(recurringEvent.end || recurringEvent.start, recurringEvent.start);
					const multiDayEndDateTime = addMinutes(multiDayStartDateTime, differenceMinutes);
					
					while (multiDayStartDateTime.getMonth() === (month-1) && multiDayStartDateTime < multiDayEndDateTime) {
						rEvents[multiDayStartDateTime.getDate()] = rEvents[multiDayStartDateTime.getDate()] || [];
						rEvents[multiDayStartDateTime.getDate()].push({
							...recurringEvent,
							'start': multiDayStartDateTime,
							'end': multiDayEndDateTime
						});
						
						// persist
						recurringEvents.current[year] = recurringEvents.current[year] || {}
						recurringEvents.current[year][month] = rEvents;
						
						//
						multiDayStartDateTime = addDays(multiDayStartDateTime, 1);
					}
				}
			}
		}
		
		// return
		return rEvents;
	}
	
	/**
	 *
	 */
	const getRecurringEvents = (year, month) => {
		return !recurringEvents.current[year] || !recurringEvents.current[year][month] ? indexRecurringEvents(year, month) : recurringEvents.current[year][month];
	}
	
	/**
	 *
	 */
	const getEvents = (date, dateTo = null) => {
		const [ year, month, day ] = date.split('-');
		const recurring = getRecurringEvents(year, month);
		
		// single day
		if (entries.current && date && !dateTo) {
			const eventsPerMonth = entries.current && entries.current[year] && entries.current[year][month] ? entries.current[year][month] : {};
			return {
				[date]: [ ...eventsPerMonth[day] || [], ...recurring[day] || [] ]
			}
		}
		
		// multiple days
		else if (entries.current && date && dateTo) {
			let periodEvents = {}
			const [ yearTo, monthTo, dayTo ] = dateTo.split('-');
			
			date = new Date(year, month-1, day);
			dateTo = new Date(yearTo, monthTo-1, dayTo);
			const d = new Date();
			const endDate = date.getMonth() === dateTo.getMonth() ? dateTo.getDate() : (date.getMonth() === d.getMonth() ? lastDayOfMonth(d).getDate() : lastDayOfMonth(date).getDate());
			
			// review
			const rYear = date.getFullYear();
			const rMonth = date.getMonth()+1;
			let rEvents = entries.current[rYear] && entries.current[rYear][rMonth] ? entries.current[rYear][rMonth] : {};
			
			for (let rDay = date.getDate(); rDay <= endDate; rDay++) {
				periodEvents = {
					...periodEvents,
					[rYear + '-' + rMonth + '-' + rDay]: [ ...rEvents[rDay] || [], ...recurring[rDay] || [] ]
				}
			}
			
			// preview
			if (endDate !== dateTo.getDate()) {
				const pYear = dateTo.getFullYear();
				const pMonth = dateTo.getMonth()+1;
				
				let pEvents = entries.current[pYear] && entries.current[pYear][pMonth] ? entries.current[pYear][pMonth] : {};
				
				for (let pDay = 1; pDay <= dateTo.getDate(); pDay++) {
					periodEvents = {
						...periodEvents,
						[pYear + '-' + pMonth + '-' + pDay]: [ ...pEvents[pDay] || [], ...recurring[pDay] || [] ]
					}
				}
			}
			
			return periodEvents;
		}
		
		return recurringEvents;
	}
	
	const setDate = (date, dateTo = null) => {
		events.current = date ? getEvents(date, dateTo) : null;
		setSelectedDate(date);
	}
	
	if (events.current === null) {
		events.current = getEvents(format(subDays(new Date(), config.daysReview), 'yyyy-M-d'), format(addDays(new Date(), config.daysPreview), 'yyyy-M-d'));
	}
	
	
	/**
	 *
	 */
	return (
		<>
			{config.calendarPreview === true &&
			<MonthlyOverview
				selectedDays={config.calendarView === 'popupView' ? [] : Object.keys(events.current)}
				setDate={setDate}
				getRecurringEvents={getRecurringEvents}
				entries={entries.current}
				/>}
			
			<DetailView
				open={config.calendarView === 'popupView' && selectedDate !== null}
				onClose={() => setDate(null)}
				events={events.current}
				/>
			
			<Grid item xs={12}>
				{config.calendarView === 'listView' && <ListView events={events.current} />}
				{config.calendarView === 'gridView' && <GridView events={events.current} />}
			</Grid>
		</>
	);
}

/*
 *
 */
const _MONTHS = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

const MonthlyOverview = props => {
	const classes = useStyles();
	
	const d = new Date();
	const today = d.getDate();
	
	const [month,setMonth] = useState(d.getMonth()+1);
	const [year,setYear] = useState(d.getFullYear());
	
	let entriesPerMonth = props.entries && props.entries[year] && props.entries[year][month] ? props.entries[year][month] : {};
	entriesPerMonth = _merge(entriesPerMonth, props.getRecurringEvents(year, month));
	
	/**
	 *
	 */
	const changeMonth = (nYear, nMonth) => {
		nYear !== year && setYear(nYear);
		nMonth !== month && setMonth(nMonth);
	}
	
	/**
	 *
	 */
	const prevMonth = () => month-1 === 0 ? changeMonth(year-1, 12) : changeMonth(year, month-1);
	const nextMonth = () => month+1 > 12 ? changeMonth(year+1, 1) : changeMonth(year, month+1);
	
	/**
	 *
	 */
	const preview = () => {
		props.setDate(null);
		setMonth(d.getMonth()+1);
		setYear(d.getFullYear());
	}
	
	/*
	 *
	 */
	const dMonth = new Date(year, month-1, 1);
	const firstDayOfMonth = dMonth.getDay();
	
	let MonthlyGrid = [];
	let week = {};
	const lastDayInMonth = new Date(year, month, 0).getDate();
	
	for (let day = 1; day <= lastDayInMonth; day++) {
		const dayInWeek = (day + firstDayOfMonth -1) % 7;
		week[dayInWeek || 7] = {
			'year': year,
			'month': month,
			'day': day,
			'type': (year === d.getFullYear() && month === (d.getMonth()+1) && day === today ? 'today' : 'day')
		}
		
		if (dayInWeek === 0 || day === lastDayInMonth) {
			
			// fill in previous / next months
			if (Object.keys(week).length !== 7) {
				
				// previous month
				if (week[1] === undefined) {
					const prevMonth = new Date(month-1 >= 0 ? year : year-1, month-1 >= 0 ? month-1 : 11, 0);
					let lastDayOfPrevMonth = prevMonth.getDate();
					
					for (let prevDay=(firstDayOfMonth || 7)-1; prevDay>=1; prevDay--) {
						week[prevDay] = {
							'type': 'prevMonth',
							'day': lastDayOfPrevMonth,
							'month': prevMonth.getMonth()+1,
							'year': prevMonth.getFullYear()
						}
						
						lastDayOfPrevMonth--;
					}
				}
				
				// next month
				else {
					const nextMonth = new Date(month+1 > 12 ? year+1 : year, month+1 > 12 ? 1 : month, 0);
					const lastDayOfWeek = Object.keys(week).length;
					
					for (let nextDay=1; nextDay<=(7-lastDayOfWeek); nextDay++) {
						week[lastDayOfWeek+nextDay] = {
							'type': 'nextMonth',
							'day': nextDay,
							'month': nextMonth.getMonth()+1,
							'year': nextMonth.getFullYear()
						}
					}
				}
			}
			
			// add calendar week
			let cw = getWeek(new Date(year, month-1, day), { locale: i18n.getLocale(), weekStartsOn: 1 });
			week[0] = {
				'type': 'cw',
				'day': cw,
			}
			
			//
			MonthlyGrid.push(
				<WeeklyOverview
					{...props}
					key={cw}
					items={week}
					entries={entriesPerMonth}
					/>
			);
			
			week = {}
		}
	}
	
	
	return (
		<Grid
			classes={{ root: classes.MonthlyOverview }}
			container
			>
			
			<Grid
				item
				xs={12}
				>
				
				<div className={classes.Month}>
					<IconButton onClick={prevMonth} size="small"><PreviousIcon /></IconButton>
					<IconButton onClick={nextMonth} size="small"><NextIcon /></IconButton>
					<Typography classes={{ root: classes.MonthlyOverviewHeader }} align="center" display="block">{i18n.t(_MONTHS[month-1])} {year}</Typography>
					
					<IconButton onClick={preview} size="small"><PreviewIcon /></IconButton>
				</div>
			</Grid>
			
			<WeeklyOverview header items={['', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Son'].map(day => ({ 'day': i18n.t(day), 'type': 'WeekDay' }))} />
			{MonthlyGrid}
		</Grid>
	);
}

/*
 *
 */
const WeeklyOverview = React.memo(props => {
	const classes = useStyles();
	
	const items = Object.values(props.items).map((item, i) => {
		item = typeof item === 'object' ? item : { 'day': item }
		item.type = item.type || 'day';
		
		let label = props.header === true ? <strong>{item.day}</strong> : item.day;
		if (item.type === 'day' || item.type === 'today') {
			label = (
				<Badge
					classes={{ root: classes.Badge, badge: classes.badge }}
					color="secondary"
					badgeContent={props.entries && props.entries[item.day] ? props.entries[item.day].length : 0}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'right',
					}}
					overlap="circle"
					>
					
					{label}
				</Badge>
			);
		}
		
		const date = item.year + '-' + item.month + '-' + item.day;
		return (
			<Grid
				key={'day-' + (item.type || '') + item.day.toString().toLowerCase()}
				classes={{
					root: clsx(
						(item.type === 'day' || item.type === 'today') && props.selectedDays && props.selectedDays.indexOf(date) > -1 && classes.DaySelected,
						classes.centered,
						(item.type === 'day' || item.type === 'today') && classes.WeeklyOverviewDay,
						item.type && classes['WeeklyOverview' + ucFirst(item.type)]
					)
				}}
				item
				onClick={() => (item.type === 'day' || item.type === 'today') && props.setDate(item.year + '-' + item.month + '-' + item.day)}
				>
				
				{label}
			</Grid>
		)
	});
	
	return (
		<Grid
			classes={{ root: clsx(props.currentWeek && classes.WeeklyOverviewCwCurrent) }}
			container
			item
			xs={12}
			justify="center"
			>
			
			{items}
		</Grid>
	);
});

/*
 *
 */
const ListView = props => {
	const { popup, horizontal } = props;
	const classes = useStyles();
	
	let duplicates = [];
	let events = [];
	
	Object.keys(props.events).sort((a,b) => a.start === b.start ? 0 : (a.start > b.start ? -1 : 1)).forEach(date => {
		if (props.events[date] && props.events[date].length > 0) {
			let entry = 0;
			events.push(Object.values(props.events[date]).map(event => {
				
				if (duplicates.indexOf(event.uid) > -1) {
					return null;
				}
				
				entry++;
				duplicates.push(event.uid);
				return (
					<CalendarEntry
						key={'CalendarEntry-' + event.uid}
						event={event}
						{...props}
						divider={(!popup || (popup && entry !== Object.values(props.events[date]).length))}
						/>
				);
			}));
		}
	});
	
	return !popup && Object.keys(events).length === 0 ? null : (
		<React.Fragment>
			{!popup && <Divider />}
			<List
				disablePadding
				classes={{ root: clsx(classes.fullWidth, horizontal && classes.horizontalList) }}
				dense
				>
				
				{Object.keys(events).length > 0 ? events : (
				<ListItem
					key='noEvents'
					className={clsx(horizontal && classes.horizontalListItem)}
					dense
					>
					
					<ListItemIcon className={clsx(horizontal && classes.horizontalListItemIcon)}>
					</ListItemIcon>
					<ListItemText
						className={clsx(horizontal && classes.horizontalListItemText)}
						primary={i18n.t('No events')}
						/>
					
				</ListItem>
				)}
			</List>
		</React.Fragment>
	);
}

/*
 *
 */
const GridView = props => {
	
	return (
		<ListView
			{...props}
			horizontal
			eventDay
			/>
	);
}

/*
 *
 */
const DetailView = props => {
	
	return (
		<Dialog
			open={props.open}
			onClose={props.onClose}
			header={i18n.t('Calendar Details')}
			disableBackdropClick={false}
			disableEscapeKeyDown={false}
			dense
			fullWidth
			maxWidth="sm"
			noPadding
			>
			
			<ListView popup {...props} />
		</Dialog>
	);
}

/*
 *
 */
const CalendarEntry = props => {
	const { event, divider } = props;
	const classes = useStyles();
	
	// convert to string
	const summary = typeof event.summary === 'object' ? event.summary.val : event.summary;
	const description = typeof event.description === 'object' ? event.description.val : event.description;
	
	// get event day & time
	let eventDay = '';
	let eventTime = '';
	
	try {
		eventDay = format(event.start, 'dd.MM.yyyy');
		eventTime = event.start.toString().indexOf('00:00:00') > -1 && (!event.end || event.end.toString().indexOf('00:00:00') > -1) ? i18n.t('all day') : (format(event.start, 'HH:mm') + (event.end ? '-' + format(event.end, 'HH:mm') : ''));
	}
	catch (err) {
		console.warn(event);
		console.error(err);
		return null;
	}
	
	// get event summary
	const title = props.horizontal ?
					(summary.length > 30 ? <Tooltip arrow placement="top" title={summary}><Typography display='inline'>{summary.substr(0, 29) + '..'}</Typography></Tooltip> : <Typography key='summary' display='inline'>{summary}</Typography>) :
					<Typography key='summary' display='inline'>{summary}</Typography>
	
	// get event icon
	let icon = (event.calendar && event.calendar.icon) || 'mdi-calendar';
	if (description) {
		let matches = description.toString().match(RegExp(/^icon=(.*)$/, 'm'));
		if (matches) {
			[ matches, icon ] = matches;
		}
	}
	
	const today = new Date();
	return (
		<ListItem
			key={'event-' + event.uid}
			className={clsx(props.horizontal && classes.horizontalListItem, (event.end || event.start) < today && classes.pastEvent)}
			divider={divider}
			dense
			>
			
			<ListItemIcon className={clsx(props.horizontal && classes.horizontalListItemIcon)}>
				<Icon icon={icon} className={classes.iconSize} />
			</ListItemIcon>
			<ListItemText
				className={clsx(props.horizontal && classes.horizontalListItemText)}
				primary={
					<Tooltip arrow placement="top" title={eventTime + ', ' + summary}>
						<div>
							<Typography key='eventTime' display='inline'>{eventTime}</Typography>
							{props.horizontal ? <br key='break' /> : <Typography key='comma' display='inline'>, </Typography>}
							{title}
						</div>
					</Tooltip>
				}
				secondary={parseDate(event.start, 'EEE, dd.MM.') + ', ' + formatDistanceToNow(event.start, { 'locale': i18n.getLocale(), 'addSuffix': true })}
				/>
			
			{props.eventDay &&
			<ListItemSecondaryAction className={props.horizontal ? classes.horizontalListItemAction : classes.verticalListItemAction}>
				{eventDay}
			</ListItemSecondaryAction>}
		</ListItem>
	);
}
