import React from 'react'

//import { ChartDataSetConfig } from './Chart.config'

import i18n from '../../controller/i18n'
import Inputs from '../../components/Inputs'

//import IconButton from '@material-ui/core/IconButton'
//import ModifyIcon from '@material-ui/icons/Build'


/*
 * STYLES
 */
/*import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	ModifyIcon: {
		verticalAlign: 'bottom'
	}
}));*/


export default {
	devices: true,
	DeviceActionsConfiguration
}

function DeviceActionsConfiguration(props) {
	const { device, deviceStates, options, onChange } = props;	
	//const classes = useStyles();
	
	return (
		<>
			<Inputs.Select
				id='primaryStateKey'
				width={150}
				
				label='Primary State Key'
				selection={deviceStates}
				val={options.primaryStateKey !== undefined ? options.primaryStateKey : device.primaryStateKey}
				onChange={(props, val) => {
					val = typeof val === 'object' ? val[val._key] : val;
					onChange(props, val);
				}}
				/>
			
			<Inputs.Text
				id='label'
				width={200}
				
				label='Chart Line Label'
				val={options.label ? options.label : (device.name + (options.primaryStateKey ? ' (' + i18n.t(options.primaryStateKey) + ')' : ''))}
				onChange={onChange}
				/>
			
			<Inputs.Text
				id='color'
				width={100}
				
				label='Chart Line Color'
				val={options.color || 'auto'}
				onChange={onChange}
				/>
			
			<Inputs.Select
				id='ack'
				width={200}
				
				info='config#Chart#ack'
				selection={[
					{ "both": "config#Chart#ack#select#both" },
					{ "true": "config#Chart#ack#select#true" },
					{ "false": "config#Chart#ack#select#false" }
				]}

				label='Ack'
				val={options.ack !== undefined ? options.ack : 'both'}
				onChange={onChange}
				/>
			
			{/*
			<IconButton
				id='ChartDataSetConfig'
				classes={{ root: classes.ModifyIcon }}
				color='primary'
				component='span'
				onClick={() => onConfig('ChartDataSetConfig', { ...ChartDataSetConfig || {}, ...options.ChartDataSetConfig || {} })}
				>
				
				<ModifyIcon fontSize='small' />
			</IconButton>
			*/}
		</>
	);
}
