import Socket from './Socket'

/**
 * Connection
 +
 *
 */
export default class Connection {

	/*
	 * Connection
	 */
	static connection = null;

	/*
	 * URL
	 */
	static url = null;
	
	/**
	 *
	 *
	 */
	static connect(url) {
		
		if (Connection.getConnection) {
			return Connection.reconnect();
		}
		
		return new Promise((resolve, reject) => {
			
			// open socket
			let socket = new Socket(url);
			
			// connection successful
			socket.on('connect', (socket, url) => {
				console.log('Connected to ' + url + '.');
				
				Connection.setConnection = socket;
				Connection.url = url;
				
				resolve({
					'socket': socket,
					'url': url
				});
			});
			
			// connection failed
			socket.on('error', err => {
				
				// close socket
				if (socket) {
					console.error('Connecting to {url} unsuccessful.'.replace('{url}', url));
					socket.disconnect();
					socket = null;
				}
				
				reject(err);
			});
		});
	}
	
	/**
	 *
	 */
	static reconnect() {
		
		const connection = Connection.getConnection;
		if (connection) {
			connection.reconnect();
		}
	}
	
	/**
	 * Discover connection.
	 *
	 * @param	{String}	url
	 * @param	{Number}	retry
	 * @return	{Connection}
	 */
	static discover(urls, retry = 0) {
		urls = [...new Set(urls)];
		console.log('Connecting to ' + urls[retry] + ' ..');
		
		return Connection.connect(urls[retry])
				.then(res => {
					return res;
				})
				.catch(err => {
					console.error('Failed connecting to ' + urls[retry] + '!');
					
					retry++;
					if (urls[retry]) {
						return Connection.discover(urls, retry);
					}
					
					return false;
				});
	}
	
	/**
	 * Sets the current Connection.
	 *
	 * @param	void
	 * @return	{Connection}	Current Connection
	 */
	static set setConnection(connection) {
		Connection.connection = connection;
		return Connection;
	}
	
	/**
	 * Gets the current Connection.
	 *
	 * @param	void
	 * @return	{Connection}	Current Connection
	 */
	static get getConnection() {
		return Connection.connection || null;
	}
}
