/*
 * Code by MATERIAL-UI
 * @see https://material-ui.com/components/tables/#sorting-amp-selecting
 *
 */
import React from 'react'

import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import Checkbox from '@material-ui/core/Checkbox'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	noWrap: {
		whiteSpace: 'nowrap'
	},
	paddingNone: {
		padding: theme.spacing(0.5)
	}
}));


/*
 *
 */
export default function EnhancedTableHead(props) {
	const { headCells, onSelectAllClick, selectable, numSelected, rowCount, onRequestSort } = props;
	
	const createSortHandler = property => event => onRequestSort(event, property);

	return (
		<TableHead>
			<TableRow>
				{selectable !== false &&
				<TableCell padding="checkbox">
					<Checkbox
						indeterminate={numSelected > 0 && numSelected < rowCount}
						checked={rowCount > 0 && numSelected === rowCount}
						onChange={onSelectAllClick}
						inputProps={{ 'aria-label': 'select all' }}
						/>
					
				</TableCell>
				}

				{headCells.map((headCell, index) => (
					<EnhancedTableHeadColumn
						key={'EnhancedTableHeadColumn-' + index}
						{...props}
						sort={createSortHandler}
						index={index}
						headCell={headCell}
						/>
				))}
			</TableRow>
		</TableHead>
	);
}

/*
 *
 */
function EnhancedTableHeadColumn(props) {
	const { index, sortable, sort, order, orderBy } = props;
	const classes = useStyles();
	
	const headCell = typeof props.headCell === 'string' ? { 'label': props.headCell } : props.headCell;
	headCell.id = headCell.id || headCell.label;
	headCell.disablePadding = headCell.disablePadding !== undefined ? headCell.disablePadding : true;
	
	return (
		<TableCell
			key={headCell.id || (headCell.label + '_' + index).toLowerCase()}
			align={headCell.align || 'left'}
			padding={headCell.disablePadding ? 'none' : 'default'}
			sortDirection={sortable && (orderBy === headCell.id ? order : false)}
			style={{ width: headCell.width || 'auto' }}
			classes={{ paddingNone: classes.paddingNone }}
			>
			
			{sortable ?
			<TableSortLabel
				active={orderBy === headCell.id}
				direction={orderBy === headCell.id ? order : 'asc'}
				onClick={sort(headCell.orderBy || headCell.id)}
				>
				
				<strong>{headCell.label}</strong>
			</TableSortLabel>
			:
			<strong>{headCell.label}</strong>
			}
		</TableCell>
	)
}
