import i18n from '../controller/i18n'
import funcConfigurations from '../functions'
import { sortObjectByValue } from '../helpers/utils'


export default {
	"alias": require("./adapters/alias"),
	"ble": require("./adapters/ble"),
	"daswetter": require("./adapters/daswetter"),
	"deconz": require("./adapters/deconz"),
	"hm-rpc": require("./adapters/hm-rpc"),
	"hmip": require("./adapters/hmip"),
	"hue": require("./adapters/hue"),
	"hue-extended": require("./adapters/hue-extended"),
	"innogy-smarthome": require("./adapters/innogy-smarthome"),
	"knx": require("./adapters/knx"),
	"lifx": require("./adapters/lifx"),
	"linkeddevices": require("./adapters/linkeddevices"),
	"mihome": require("./adapters/mihome"),
	"mihome-vacuum": require("./adapters/mihome-vacuum"),
	"mqtt": require("./adapters/mqtt"),
	"nuki-extended": require("./adapters/nuki-extended"),
	"rpi2": require("./adapters/rpi2"),
	"shelly": require("./adapters/shelly"),
	"sonoff": require("./adapters/sonoff"),
	"tr-064": require("./adapters/tr-064"),
	"unifi": require("./adapters/unifi"),
	"wifilight": require("./adapters/wifilight"),
	"yeelight-2": require("./adapters/yeelight-2"),
	"zigbee": require("./adapters/zigbee"),
	"zwave2": require("./adapters/zwave2")
}


/**
 *
 */
export function detectFunction(device) {
	
	// detect function by name and states
	device.function = _detectFunction([{ 'label': device.name, 'points': 5 }].concat(Object.keys(device.states || {})), device);
	
	return device;
}

/**
 *
 */
function _detectFunction(source, device) {
	
	let hits = {}
	for (let func of Object.keys(funcConfigurations)) {
		const keywords = (i18n.t('keywords#' + func) + ',' + i18n.t('keywords#' + func, {}, {}, 'en')).replace(/, /g, ',').split(',');
		hits[func] = 0;
		
		source.forEach(s => {
			s = {
				'label': s.label || s,
				'points': s.points || 1
			}
			
			// detect nested language
			s.label = s.label.de || s.label.en || s.label;
			
			//
			if (s.label && typeof s.label === 'string') {
				for (let keyword of keywords) {
					if (keyword && s.label.toLowerCase().indexOf(keyword.toLowerCase()) > -1) {
						hits[func] += s.points;
					}
				}
			}
		});
		
		// remove func if 0
		if (hits[func] === 0) {
			delete hits[func];
		}
	}
	
	return Object.keys(hits).length > 0 ? Object.keys(sortObjectByValue(hits)).pop() : 'other';
}

/**
 *
 */
export function detectStateElements(state, deviceStructure) {
	let s = typeof state !== 'string' ? state : { "state": state }
	
	// writeable, thus action
	const obj = deviceStructure.objects[s.action || s.state];
	if (obj && obj.common && obj.common.write !== false) {
		const role = obj.common.role || '';
		s.actionElement = 'InputAction';
		s.action = s.state;
		
		/*
		 * ACTION
		 */
		// switch
		if (role.indexOf('switch') > -1) {
			s.actionElement = 'SwitchAction';
		}
		// button
		else if (role.indexOf('button') > -1) {
			s.actionElement = 'IconButtonAction';
		}
		// dropdown
		else if (obj.common.states !== undefined) {
			s.actionElement = 'DropdownAction';
			s.display = obj.common.states;
		}
		else if (obj.common.type === 'boolean' || obj.common.min === true || obj.common.min === false) {
			s.actionElement = 'SwitchAction';
		}
		
		/*
		 * BODY
		 */
		if (s.state.indexOf('.rgb') > -1 || s.state.indexOf('.hex') > -1 || s.state.indexOf('.hsv') > -1) {
			s.bodyElement = 'LightColorBody';
			s.actionElement = '';
		}
		else if (s.state.indexOf('.hue') > -1) {
			s.bodyElement = 'LightHueBody';
			s.actionElement = '';
		}
		else if (s.state.indexOf('.level') > -1 || s.state.indexOf('.bri') > -1 || s.state.indexOf('.dimmer') > -1) {
			s.bodyElement = 'LevelBody';
			s.actionElement = '';
		}
		else if (s.state.indexOf('.ct') > -1 || s.state.indexOf('.colorTemp') > -1) {
			s.bodyElement = 'LightTemperatureBody';
			s.actionElement = '';
		}
	}
	
	return s;
}

/**
 *
 */
export function isValidFunction(func) {
	
	if (Object.keys(funcConfigurations).indexOf(func) > -1) {
		return func;
	}
	
	return 'other';
}

/**
 *
 */
const BLACKLIST = [ 'r', 'g', 'b', 'w' ];
export function parseDefault(deviceStructure, options, device = {}) {
	const obj = deviceStructure.objects[deviceStructure.root].common;
	
	device = {
		'name': obj.name,
		'function': obj.role || 'other',
		'states': {},
		...device
	}
	
	return new Promise(resolve => {
		
		// add states
		deviceStructure.list.forEach(state => {
			const stateKey = state.substr(state.lastIndexOf('.')+1);
			
			if (BLACKLIST.indexOf(stateKey) === -1) {
				device.states[stateKey] = detectStateElements(state, deviceStructure)
			}
		});
		
		// detect function and action element
		if (device.states !== null) {
			device = detectFunction(device);
		}
		
		// resolve
		resolve(device);
	});
}

/**
 *
 *
 */
export function validateStates(states, deviceStructure) {
	
	Object.keys(states).forEach(stateKey => {
		const nodes = Array.isArray(states[stateKey]) ? states[stateKey] : [states[stateKey]];
		
		for (let node of nodes) {
			node = typeof node === 'string' ? { 'state': node } : node;
			node.state = (node.state && node.state.replace(deviceStructure.root, '')) || '';
			node.action = (node.action && node.action.replace(deviceStructure.root, '')) || '';
			
			if (deviceStructure.list.indexOf(deviceStructure.root + node.state) > -1 || deviceStructure.list.indexOf(deviceStructure.root + node.action) > -1) {
				states[stateKey] = detectStateElements({
					...node || {},
					'state': (deviceStructure.list.indexOf(deviceStructure.root + node.state) > -1 ? deviceStructure.root + node.state : ''),
					'action': (deviceStructure.list.indexOf(deviceStructure.root + node.action) > -1 ? deviceStructure.root + node.action : '')
				}, deviceStructure);
				break;
			}
			else {
				delete states[stateKey];
			}
		}
	});
	
	// no states left
	if (Object.keys(states).length === 0) {
		return {};
	}
	
	//
	return states;
}