import React, { useState, useEffect } from 'react'
import clsx from 'clsx'
import { v4 as uuid } from 'uuid'
import i18n from '../../controller/i18n'

import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete'
import Icon from '../../components/Icon'
import IconButton from '@material-ui/core/IconButton'
import InputAdornment from '@material-ui/core/InputAdornment'
import TextField from '@material-ui/core/TextField'

import ModifyIcon from '@material-ui/icons/Build'
import Field from './Field'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	noIconPadding: {
		paddingRight: '0 !important'
	},
	shrink: {
		width: '130%' // 'calc(140px / 0.75)', // 140px is incorrect (needs to be dynamic)
	},
	Input: {
		width: '100%'
	},
	ModifyIcon: {
		height: 20,
		width: 20
	}
}));


/*
 *
 */
export default React.forwardRef((props, ref) => {
	return props.options ? <TextAutoComplete {...props} ref={ref} /> : <TextInput {...props} ref={ref} />
});

/*
 *
 */
const setter = (val, json) => val !== '' && val !== null && val !== undefined ? (json && typeof val === 'object' ? JSON.stringify(val) : (typeof val === 'function' ? val(100) : val)) : '';
export const TextInput = React.forwardRef((props, ref) => {
	const { id, inputId, onChange, onValidate, onBlur, required, label, type, children, select, shrink, disabled, size, placeholder, errorMessage, autoFocus, multiline, val, onEnter, clearOnEnter, clearOnBlur, disableUnderline, icon, InputProps, json, action, ...otherProps } = props;
	const classes = useStyles();
	
	const [value,setValue] = useState(setter(val, json));
	useEffect(() => {
		setValue(setter(val, json));
	}, [val, json]);
	
	const [error,setError] = useState((props.error ? props.error : (required && !val)) || null);
	useEffect(() => { props.error !== undefined && setError(props.error) }, [props.error]);
	
	
	/**
	 *
	 */
	const validate = val => {
		
		// validate callback
		try {
			val = onValidate ? onValidate(val) : val;
		}
		catch(err) {
			setError(i18n.t(err.message));
			return [ true, '' ];
		}
		
		// validate required fields
		if (required && !val) {
			setError(i18n.t('No input given'));
			return [ true, '' ];
		}
		
		// validate JSON
		if (json && (val.indexOf(" ") > -1 || val.indexOf('"') > -1 || val.indexOf('{') > -1 || val.indexOf('}') > -1)) {
			
			try {
				val = val ? JSON.parse(val) : '';
			}
			catch(err) {
				setError(i18n.t('Wrong JSON format'));
				return [ true, {} ];
			}
		}
		
		setError(null);
		return [ false, val ];
	}
	
	const _inputId = inputId || (id ? (id + '-' + uuid()) : ('id-' + uuid()));
	const inputLabel = (Array.isArray(label) ? label.map(lbl => i18n.t(lbl)).join('') : i18n.t(label));
	
	return (
		<Field id={_inputId} {...props}>
			<TextField
				inputRef={ref}
				classes={{ root: clsx(classes.Input, classes.TextField, id && id.toLowerCase().replace(/ /g, '')) }}
				
				id={_inputId}
				type={type || 'text'}
				required={required}
				select={select}
				
				multiline={multiline}
				rows={4}
				size={size}
				disabled={disabled}
				
				fullWidth={true}
				placeholder={placeholder ? setter(placeholder, json).toString() : ''}
				autoFocus={autoFocus ? autoFocus : false}
				
				onKeyPress={e => {
					if (e.key === 'Enter' && !multiline) {
						e.preventDefault();
						clearOnEnter && setValue('');
						
						let [ err, val ] = validate(e.target.value);
						onEnter && onEnter({ ...props, error: err }, val);
					}
				}}
				
				error={typeof error === 'boolean' ? error : (error !== null)}
				helperText={errorMessage ? i18n.t(errorMessage) : (typeof error !== 'boolean' ? error : '')}
				
				label={inputLabel + (json ? ' (' + i18n.t('JSON format') + ')' : '')}
				value={value}
				onChange={e => {
					setValue(e.target.value);
					
					let [ err, val ] = validate(e.target.value);
					onChange && onChange({ ...props, error: err }, val);
				}}
				
				InputProps={{
					...InputProps || {},
					disableUnderline: disableUnderline,
					startAdornment: icon && (
						<InputAdornment position="start">
							<Icon icon={icon} />
						</InputAdornment>
					),
					onBlur: e => {
						onBlur && onBlur(props, value);
						clearOnBlur && setValue('');
					}
				}}
				
				InputLabelProps={{
					classes: { shrink: classes.shrink },
					shrink: shrink ? shrink : true
				}}
				
				{...otherProps}
				>
				
				{children ? children : null}
			</TextField>
			
			{action && <IconButton
				classes={{ root: classes.ModifyIcon }}
				color='primary'
				component='span'
				onClick={action}
				>
				
				<ModifyIcon fontSize='small' />
			</IconButton>}
		</Field>
	)
});


/*
 *
 */
export function TextAutoComplete(props) {
	const classes = useStyles();
	let { id, inputId, errorMessage, required, autoFocus, placeholder, label, shrink,
				showMaxResults, onChange, onInputChange, clearOnBlur, clearOnSelect, val, inputVal, json, popupIcon, closeIcon, action, ...otherProps } = props;
	
	
	const filterOptions = showMaxResults && showMaxResults > 0 ? createFilterOptions({ limit: showMaxResults }) : undefined;
	const objectify = props.options && props.options[0] && props.options[0].id ? true : false; // discover whether plain array of array of objects is used
	
	const [value,setValue] = useState(val !== undefined && val !== '' ? val : (props.multiple ? [] : null)); // can not be undefined, because this will change the element from uncontrolled to be controlled
	const [inputValue,setInputValue] = useState(inputVal || '');
	
	const [error,setError] = useState((props.error ? props.error : (required && !val)) || null);
	useEffect(() => { props.error !== undefined && setError(props.error) }, [props.error]);
	delete otherProps.error;
	
	/**
	 *
	 */
	const validate = options => {
		
		// validate required fields
		if (required && (!options || (Array.isArray(options) && options.length === 0))) {
			setError(i18n.t('No option selected'));
			return [ true, null ];
		}
		
		setError(null);
		return [ false, options ];
	}
	
	const _inputId = inputId || (id ? (id + '-' + uuid()) : ('id-' + uuid()));
	return (
		<Field id={_inputId} {...props}>
			<Autocomplete
				classes={{
					root: clsx(classes.Input, classes.Autocomplete),
					inputRoot: clsx(closeIcon === null && popupIcon === null && classes.noIconPadding)
				}}
				
				//defaultValue={val}
				value={value}
				inputValue={inputValue}
				
				onInputChange={(e, text) => {
					setInputValue(text);
					onInputChange && onInputChange({ ...props, error: error }, text);
				}}
				onChange={(e, selected, reason) => {
					
					if (props.multiple) {
						selected = selected.map(val => typeof val === 'string' && objectify ? { id: val.toLowerCase().replace(/ /g, ''), name: val } : val);
					}
					
					setValue(clearOnSelect ? null : selected);
					if (clearOnSelect) {
						setInputValue(inputValue || inputVal || '');
					}
					
					let [ err, options ] = validate(selected);
					onChange && onChange({ ...props, error: err }, options);
				}}
				
				getOptionSelected={(option, value) => option && value ? (objectify ? option.id === value.id : option === value) : false}
				filterOptions={filterOptions}
				
				renderInput={params => <TextField
										{...params}
										id={_inputId}
										
										required={required}
										autoFocus={autoFocus ? autoFocus : false}
										label={i18n.t(label) || (placeholder ? i18n.t(placeholder.toString()) : '')}
										
										error={typeof error === 'boolean' ? error : (error !== null)}
										helperText={errorMessage ? i18n.t(errorMessage) : (typeof error !== 'boolean' ? error : '')}						
										
										InputLabelProps={{
											classes: { shrink: classes.shrink },
											shrink: shrink ? shrink : true
										}}
										/>}
				
				onClose={(e, reason) => reason === 'blur' && clearOnBlur && setValue(null)}
				
				{...otherProps}
				/>
				
			{action && <IconButton
				classes={{ root: classes.ModifyIcon }}
				color='primary'
				component='span'
				onClick={action}
				>
				
				<ModifyIcon fontSize='small' />
			</IconButton>}
		</Field>
	
	);
}
