import Connection from '../../../controller/Connection'
import { allSettled } from '../../../helpers/utils'


/**
 *
 */
export function setPlayer(getPlayerNode) {
	const node = getPlayerNode(getPlayerNode('.devices.{PLAYER}.useForPlayback'));
	if (node) {
		const socket = Connection.connection;
		socket.setState(node, true)
			.catch(console.error);
	}
}

/**
 *
 */
export function getPlayers(adapter) {
	const socket = Connection.connection;
	let root = adapter + '.devices';
	
	// trigger player update
	socket.setState(adapter + '.getDevices', true);
	
	// retrieve players
	return new Promise(resolve => {
		socket.getObjectView('system', 'device', { startkey: root, endkey: root + '.\u9999' }).then(devices => {
			let promises = [];
			for (let device of devices) {
				
				// get device states
				promises.push(new Promise((resolve, reject) => {
					socket.getStates(device.id + '.*').then(states => {
						
						if (states && states[device.id + '.id'] !== null && states[device.id + '.id'] !== undefined && states[device.id + '.isAvailable'] !== null && states[device.id + '.isAvailable'] !== undefined && states[device.id + '.isAvailable'].val === true) {
							resolve({
								'id': device.id.replace(root + '.', ''),
								'name': states[device.id + '.name'].val
							});
						}
						else {
							reject();
						}
					});
				}));
			}
			
			allSettled(promises).then(devices => {
				let players = [];
				devices.forEach(device => device.status === 'fulfilled' && players.push(device.value));
				resolve(players);
			});
		});
	});
}

/**
 *
 */
function convertToSeconds(val) {
	const [ hours, seconds ] = val.split(':');
	return parseInt(hours) * 60 + parseInt(seconds);
}

/*
 *
 */
export default {
	"_namespace": "",
	
	// player
	"player": ".player.device.id",
	
	// information
	"artist": ".player.artistName",
	"album": ".player.album",
	"track": ".player.trackName",
	"year": null,
	"cover": ".player.albumImageUrl",
	
	// actions
	"play": {
		"state": ".player.isPlaying", // isPlaying
		"action": ".player.play",
		"setter": (val, getPlayerNode) => {
			/*
			const node = getPlayerNode(getPlayerNode('.devices.{PLAYER}.useForPlayback'));
			if (node) {
				const socket = Connection.connection;
				socket.setState(node, true)
					.catch(console.error);
			}
			*/
			
			return val;
		}
	},
	"pause": {
		"action": ".player.pause"
	},
	"resume": {
		"action": ".player.play"
	},
	"stop": {
		"action": null
	},
	"previous": {
		"action": ".player.skipMinus"
	},
	"next": {
		"action": ".player.skipPlus"
	},
	
	// attributes
	"progress": {
		"state": ".player.progress",
		"convert": convertToSeconds,
		"action": ".player.progressMs",
		"setter": seconds => {
			return seconds * 1000
		}
	},
	"duration": {
		"state": ".player.duration",
		"convert": convertToSeconds
	},
	
	// options
	"repeat": {
		"state": ".player.repeat",
		"action": ".player.repeat",
		"display": {
			"off": "off",
			"item": "track",
			"all": "context"
		}
	},
	"shuffle": {
		"state": ".player.shuffle",
		"action": ".player.shuffle",
		"display": {
			"off": "off",
			"on": "on"
		}
	},
	"volume": {
		"state": ".player.device.volume",
		"action": ".player.device.volume",
		"display": {
			"off": 0
		}
	}
}
