import React, { useState, useEffect, useRef } from 'react'

import { format, getWeek } from 'date-fns'
import { getSunrise, getSunset } from 'sunrise-sunset-js'

import CircularProgress from '@material-ui/core/CircularProgress'

import Icon from '../../components/Icon'

import { joinConfig } from '../../helpers/utils'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import Settings from '../../controller/Settings'


export default function DateTime(props) {
	let config = useRef(joinConfig(props.config, require('./DateTime.config.js').default));
	config.current.refresh = config.current.format.indexOf('s') > -1 ? 1000 : (config.current.format.indexOf('m') > -1 ? 10*1000 : 60*1000);
	config.current.date = format(new Date(), config.current._defaults.format, { locale: i18n.getLocale() });
	
	try {
		config.current.date = format(new Date(), config.current.format, { locale: i18n.getLocale() });
	}
	catch(err) {
		Log._('DateTime', i18n.t('Incorret DateTime parameter provided') + '!', 'error', true, err.stack);
		config.current.format = config.current._defaults.format;
	}
	
	const refTimer = useRef();
	const [DateTime, setDateTime] = useState(config.current.date);
	const [city, setCity] = useState(null);
	const [sunrise, setSunrise] = useState(null);
	const [sunset, setSunset] = useState(null);
	
	useEffect(() => {
		
		function refresh() {
			
			// set current time
			setDateTime(format(new Date(), config.current.format, { locale: i18n.getLocale() }));
		
			// set sunrise / sunset
			if (Settings.getSetting('iobroker').latitude && Settings.getSetting('iobroker').longitude) {
				setSunrise(getSunrise(Settings.getSetting('iobroker').latitude, Settings.getSetting('iobroker').longitude));
				setSunset(getSunset(Settings.getSetting('iobroker').latitude, Settings.getSetting('iobroker').longitude));
			}
			
			// constantly refresh
			refTimer.current = setTimeout(refresh, config.current.refresh);
		}
		
		// refresh current time
		clearTimeout(refTimer.current);
		refTimer.current = setTimeout(refresh, 1*1000); // first refresh some seconds after render
		
		setCity(Settings.getSetting('iobroker').city ? Settings.getSetting('iobroker').city : null);
		
		// unmount
		return () => clearTimeout(refTimer.current);
		
	}, [config.current.format, config.current.refresh]);
	
	const week = getWeek(new Date(), { locale: i18n.getLocale(), weekStartsOn: 1 });
	return (

<div style={config.current.style}>
	{DateTime}
	
	<div style={{ margin: '5px 0', fontSize: '80%' }}>
		{config.current.city && <>
			<Icon small icon="mdi:city-variant-outline" /> {city ? city : <CircularProgress size="1.2rem" />}
		</>}
		
		{config.current.sun !== false && sunrise && sunset && <>
			<span style={{ margin: '0 12px' }}></span>
			
			<Icon small icon="mdi:weather-sunset-up" /> {sunrise ? format(sunrise, 'HH:mm') : <CircularProgress size="1.2rem" />}
			<span style={{ margin: '0 4px' }}></span>
			
			<Icon small icon="mdi:weather-sunset-down" /> {sunset ? format(sunset, 'HH:mm') : <CircularProgress size="1.2rem" />}
		</>}
		
		{config.current.calendarweek && <>
			<span style={{ margin: '0 12px' }}></span>
			<Icon small icon="mdi:calendar-week" /> {week}.
		</>}
	</div>
</div>

	);
}
