import React, { useState, useRef, useCallback, useEffect } from 'react'
import clsx from 'clsx'

import Log from '../controller/Log'
import Events from '../controller/Events'
import Settings from '../controller/Settings'
import { joinConfig, getValueAndUnit } from '../helpers/utils'

import Icon from './Icon'

import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import Avatar from '@material-ui/core/Avatar'
import Typography from '@material-ui/core/Typography'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	card: {
		margin: theme.spacing(1, 1, 0, 0),
		[theme.breakpoints.down('md')]: {
			margin: theme.spacing(0.8, 0.8, 0, 0)
		}
	},
	cardHeader: {
		padding: theme.spacing(2, 3)
	},
	cardHeaderFullscreen: {
		padding: '0 !important',
		margin: '0 !important',
	},
	cardAvatar: style => ({
		backgroundColor: theme.palette.primary.main,
		color: '#fff',
		fontSize: '24px',
		...style.iconStyle
	}),
	cardContentJustification: style => ({
		display: 'flex',
		flexWrap: 'wrap',
		justifyContent: style.justification,
		textAlign: 'center'
	}),
	cardContent: {
		padding: theme.spacing(0, 2),
		
		[theme.breakpoints.down('md')]: {
			padding: theme.spacing(0)
		}
	},
	minHeight: style => ({
		minHeight: getValueAndUnit(style.minHeight).slice(0,2).join('')
	}),
	maxHeight: style => ({
		maxHeight: getValueAndUnit(style.maxHeight).slice(0,2).join(''),
		overflow: style.scaleContents === true ? 'visible' : 'auto'
	}),
	angled: {
		borderRadius: 0,
		margin: '1px'
	}
}));


export default React.memo((props) => {
	Log._('Jarvis', [ 'RENDER Widget', props ], 'development');
	const { id, fullscreen, title, minHeight, maxHeight, scaleContents, icon, tabConfig } = props;
	
	const config = joinConfig(props, require('../pages/ConfigurationPage/LayoutsPage/Widgets.config').default);
	const classes = useStyles(config);
	
	const CardRef = useRef();
	const [scale,setScale] = useState(1);
	const [height,setHeight] = useState();
	
	/**
	 *
	 *
	 */
	const resize = useCallback((CardContentElementHeight, CardHeaderElementHeight = 0) => {
		if (scaleContents && maxHeight && CardContentElementHeight) {
			const [,, px] = getValueAndUnit(maxHeight);
			const scaled = (px - CardHeaderElementHeight) / CardContentElementHeight;
			scaled < 1 && setScale(scaled);
		}
		
	}, [maxHeight, scaleContents]);
	
	// listen
	useEffect(() => {
		
		// listen for changes
		const handleEvent = w => {
			
			if (w && id === w.id && CardRef.current) {
				setTimeout(() => {
					const AppBarHeight = (document.getElementById('AppBar') && document.getElementById('AppBar').offsetHeight) || 0;
					window.scrollTo({ top: CardRef.current.offsetTop - AppBarHeight, behavior: 'smooth' });
				}, 500);
			}
		}
		
		// tab
		const events = new Events();
		events.setMaxListeners(0).on('widget.navigate', handleEvent);
		return () => events.off('widget.navigate', handleEvent);
		
	}, [id]);
	
	//
	const observe = useCallback(el => {
		let observer = null;
		
		if (el) {
			CardRef.current = el.parentNode;
			const CardContentElement = el;
			
			// set height
			const CardHeaderElement = CardRef.current.getElementsByClassName('CardHeader');
			const CardHeaderElementHeight = CardHeaderElement[0] ? CardHeaderElement[0].offsetHeight : 0;
			setHeight('calc(' + getValueAndUnit(minHeight).slice(0,2).join('') + ' - ' + (CardHeaderElementHeight ? CardHeaderElementHeight + 'px' : CardHeaderElementHeight) + ' - 24px)');
			
			// 
			if (maxHeight) {
				observer = new MutationObserver(() => resize(CardContentElement.offsetHeight, CardHeaderElementHeight));
				observer.observe(CardContentElement, { characterData: true, childList: true, attributes: true, subtree: true });
			}
		}
		
		return () => observer && observer.disconnect();
	}, [minHeight, maxHeight, resize]);
	
	
	return (
		<Card
			id={id}
			className={clsx('jarvis-Widget', 'Module-' + props.module, classes.card, tabConfig.widgetsEdges === true && classes.angled, minHeight && classes.minHeight, maxHeight && classes.maxHeight)}
			>
			
			{fullscreen !== true && <CardHeader
				classes={{ root: clsx('WidgetHeader CardHeader', classes.cardHeader) }}
				avatar={icon !== null &&
					<Avatar variant="rounded" classes={{ root: classes.cardAvatar }}>
						<Icon
							icon={(icon || Settings.getSetting('defaultBoxIcon') || 'home')}
							noDefault
							/>
						
					</Avatar>
				}
				title={title &&
					<Typography variant="h5" component="h2">
						{title}
					</Typography>
				}
			/>}
			<CardContent
				ref={observe}
				className={clsx('WidgetContent CardContent', classes.cardContentJustification, fullscreen ? classes.cardHeaderFullscreen : classes.cardContent, config.scaleContents === true && classes.scaleContents)}
				style={{
					'height': height,
					'transform': 'scale(' + scale + ')',
					'transformOrigin': 'top'
				}}
				>
				
				{props.children}
			</CardContent>
		</Card>
	);
});
