import i18n from '../controller/i18n'


export const FunctionList = [
	'defaults',
	
	'blind',
	'door',
	'fan',
	'heating',
	'household',
	'light',
	'location',
	'motion',
	'mower',
	'scenes',
	'sensor',
	'server',
	'smoke',
	'socket',
	'speaker',
	'switch',
	'tv',
	'user',
	'vacuum',
	'weather-station',
	'window'
];


let funcConfigurations = {}
let funcIcons = {}
let funcComponents = {}
let options = {}

FunctionList.forEach(func => {
	func = func === 'defaults' ? '_' + func : func;
	
	// load function language file
	try {
		i18n.setTranslations(language => require('./' + func + '/i18n/' + language));
	}
	catch(err) {
		//console.error(err);
	}
	
	// load function itself
	try {
		const module = require('./' + func);
		funcConfigurations[func] = module.config || {};
		funcIcons[func] = module.icon || null;
		
		// init function
		funcComponents[func] = funcComponents[func] || {
			_options: {},
			body: {},
			action: {}
		}
		
		// add components
		let elements = require('./' + func + '/components').default
		options[func] = { ...options[func] || {}, ...elements._options }
		funcComponents[func].body = { ...funcComponents[func].body, ...elements.body }
		funcComponents[func].action = { ...funcComponents[func].action, ...elements.action }
	}
	catch(err) {
		//console.error(err);
	}
});

export default funcConfigurations;
export const components = funcComponents;
export const icons = funcIcons;
export const componentsOptions = options;
