import React from 'react'
import clsx from 'clsx'

import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import Icon from '../../components/Icon'

import { components } from '../../functions'
import Events from '../../controller/Events'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	ListItem: {
		paddingTop: theme.spacing(0.5),
		paddingBottom: theme.spacing(0.5)
	},
	ListItemIcon: {
		padding: '4px 0',
	},
	ListItemText: {
		margin: '2px 0'
	},
	horizontalListItem: {
		display: 'inline-block',
		textAlign: 'center',
		paddingRight: '16px',
		width: '140px',
		marginTop: '10px'
	},
	horizontalListItemIcon: {
		minWidth: 'auto'
	},
	horizontalListItemAction: {
		textAlign: 'center',
		top: 0,
		right: 0,
		position: 'static',
		transform: 'none',
	},
	verticalListItemAction: {
		textAlign: 'right'
	},
	defaultListDivider: {
		padding: '2px 0 0 0 !important',
		marginBottom: '2px !important'
	},
	oversizedItem: {
		position: 'inherit'
	}
});


const _copy = require('rfdc')();
class StateListItem extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			ListItemTextWidth: 'auto',
			...(this.props.device._isDevice && this.props.device.getDeviceStates()) || {}
		}
		
		this.Events = new Events();
	}
	
	/*
	 *
	 */
	componentDidMount() {
		Log._('StateListItem', 'componentDidMount', 'development');
		
		if (this.props.device._isDevice) {
			
			// subscribe to state changes
			this.props.device.listen(this._stateChange); //, this.props.device.deviceStateDetails === true);
			
			// get initial values of states
			this.setState(this.props.device.getDeviceStates());
		}
	}
	
	/*
	 *
	 */
	componentWillUnmount() {
		Log._('StateListItem', 'componentWillUnmount', 'development');
		
		// unsubscribe from state changes
		this.props.device._isDevice && this.props.device.unlisten(this._stateChange);
	}
	
	/**
	 *
	 */
	_stateChange = (stateKey, state) => {
		this.setState({ [stateKey]: state });
	}
	
	/**
	 *
	 */
	_openDialog = event => {
		if ((this.props.device.options && this.props.device.options.suppressPopup !== true) && this.props.openDialog !== null && event.target && ((event.target.className && event.target.className.toString().indexOf('popup') > -1) || (event.target.parentElement && event.target.parentElement.className && event.target.parentElement.className.toString().indexOf('popup') > -1))) {
			this.Events.emit('device.details', this.props.device);
		}
	}
	
	render() {
		//Log._('StateListItem', [ 'RENDER render()', this.props.device.name, this.props ], 'development');
		const { classes, config, device } = this.props;
		
		// verify device
		if (!this.props.device._isDevice) {
			Log._('StateListItem', i18n.t('Incorrect device assigned to widget using StateList module') + '!', 'warn');
			return null;
		}
		
		//
		let options = _copy(this.props.options);
		options.primaryStateKey = options.primaryStateKey || device.primaryStateKey;
		const primaryState = this.state[options.primaryStateKey] || {};
		
		// Icon
		const icon = device.getIcon(options.primaryStateKey, primaryState.val) || device.getIcon(options.primaryStateKey, primaryState.value);
		
		// set defaults for DeviceStateDetails (popup)
		if (config.isDeviceStateDetails) {
			options.label = device.states[options.primaryStateKey].label || options.primaryStateKey;
			
			// Body
			options.bodyStateKey = options.primaryStateKey;
			options.bodyElement = options.bodyElement !== undefined ? options.bodyElement : ((device.configurations[options.bodyStateKey.toLowerCase()] && device.configurations[options.bodyStateKey.toLowerCase()].bodyElement) || 'LastChangeBody');
			
			// Action / State
			options.actionElement = options.actionElement !== undefined ? options.actionElement : (device.configurations[options.primaryStateKey.toLowerCase()] && device.configurations[options.primaryStateKey.toLowerCase()].actionElement);
			options.showState = options.actionElement === undefined || options.actionElement === null;
		}
		// set defaults for Widget
		else {
			options.bodyStateKey = options.bodyStateKey !== undefined ? options.bodyStateKey : options.primaryStateKey;
			options.bodyElement = config.horizontal !== true && options.bodyElement !== undefined ? options.bodyElement : (config.horizontal === true && options.lastUpdate === false ? null : 'LastChangeBody');
		}
		
		// Body
		let primaryBody = options.label || (device.states[options.bodyStateKey] && device.states[options.bodyStateKey].label) || device.label || device.name;
		if (options.bodyStateKey && options.bodyElement) {
			const bodyState = this.state[options.bodyStateKey] || {};
			const BodyElement = (components[device.function] && components[device.function].body[options.bodyElement]) || (components['_defaults'] && components['_defaults'].body[options.bodyElement]) || null;
			primaryBody = BodyElement && (
				<BodyElement
					key={'BodyElement-' + device.id}
					horizontal={config.horizontal}
					title={primaryBody}
					device={device}
					config={{ ...(device.states[options.bodyStateKey] && device.states[options.bodyStateKey][options.bodyElement + 'Config']) || {}, ...options[options.bodyElement + 'Config'] || {} }}
					state={bodyState}
					stateVal={bodyState.val}
					stateKey={options.bodyStateKey}
					/>
			);
		}
		
		// State
		const StateElement = components['_defaults'].action['State'];
		let actionConfig = { ...(device.states[options.primaryStateKey] && device.states[options.primaryStateKey][options.actionElement + 'Config']) || {}, ...options[options.actionElement + 'Config'] || {} }
		
		let stateAction = null;
		options.showState = options.showState !== undefined ? options.showState : (options.actionType !== undefined ? (options.actionType === 'state') : true);
		if (options.showState === true) {
			stateAction = (
				<StateElement
					key={'StateListItem-PrimaryState-' + device.id + '-' + options.primaryStateKey}
					horizontal={config.horizontal}
					device={device}
					config={actionConfig}
					state={primaryState}
					stateVal={primaryState.val}
					stateKey={options.primaryStateKey}
					primary
					/>
			);
		}
		
		// Action
		let primaryAction = null;
		if (options.actionElement) {
			const ActionElement = (components[device.function] && components[device.function].action[options.actionElement]) || (components['_defaults'] && components['_defaults'].action[options.actionElement]) || null;
			primaryAction = ActionElement && (
				<ActionElement
					key={'StateListItem-ActionState-' + device.id + '-' + options.primaryStateKey}
					device={device}
					config={actionConfig}
					state={primaryState}
					stateVal={primaryState.val}
					stateKey={options.primaryStateKey}
					/>
			);
		}
		
		// SecondaryState
		options.secondaryStatesKeys = options.secondaryStatesKeys && options.secondaryStatesKeys.filter(secondaryStateKey => {
			const state = this.state[secondaryStateKey] || {}
			return state && state.value !== undefined && state.value !== null && state.value !== '' && state.value !== 0;
		});
		
		const SubtitleElement = components['_defaults'].action['Subtitle'];
		const secondaryActions = options.secondaryStatesKeys ? (
			<div className="popup StateListItemActionSecondary" style={{ textAlign: config.horizontal ? 'center' : 'right'}}>
				{options.secondaryStatesKeys.map((secondaryStateKey, i) => {
					const state = this.state[secondaryStateKey] || {}
					const lastItem = (i === (options.secondaryStatesKeys.length-1))
					
					return (
						<SubtitleElement
							key={'StateListItem-SecondaryState' + i + '-' + device.id + '-' + secondaryStateKey}
							separator={(!lastItem ? ',' : '')}
							horizontal={config.horizontal}
							device={device}
							state={state}
							stateVal={this.state[secondaryStateKey] && this.state[secondaryStateKey].val}
							stateKey={secondaryStateKey}
							secondary
							/>
							
					)
				})}
			</div>) : null;
		
		return (
			<React.Fragment>
				{config.divider && config.divider !== 'after' && <ListItem divider className={classes.defaultListDivider} />}
				
				<ListItem
					className={clsx('popup', classes.ListItem, this.props.openDialog && classes.finger, config.horizontal && classes.horizontalListItem)}
					button={device.options.suppressPopup !== true && this.props.openDialog !== null}
					onClick={this._openDialog}
					>
					
					<ListItemIcon
						className={clsx('popup', classes.ListItemIcon, config.horizontal && classes.horizontalListItemIcon)}
						>
						
						{icon === null || typeof icon !== 'string' ? <React.Fragment /> :
						<Icon
							icon={icon}
							styles={device.getStyle('icon', options.primaryStateKey, primaryState.val)}
							/>
						}
					</ListItemIcon>
					
					<ListItemText
						classes={{ root: clsx('popup', classes.ListItemText) }}
						disableTypography
						style={{ display: config.horizontal ? 'initial' : 'flex' }}
						>
						
						<div className="StateListItemBody" style={{ flex: 'auto', margin: 'auto 0' }}>
							{primaryBody}
						</div>
						
						<div className="StateListItemAction" style={!config.horizontal ? { margin: 'auto 0 auto 24px', textAlign: 'right' } : {}}>
							<div className="StateListItemActionPrimary" style={{ display: 'flex' }}>
								{stateAction}
								{primaryAction && <div style={{ margin: (config.horizontal ? '0 auto' : (stateAction === null ? '0 0 0 auto' : '0 0 0 8px')) }}>{primaryAction}</div>}
							</div>
							{secondaryActions}
						</div>
						
					</ListItemText>
				</ListItem>
				
				{config.divider && config.divider === 'after' && <ListItem divider style={{margin: '5px 0'}} />}
			</React.Fragment>
		);
	}
}

export default withStyles(styles)(StateListItem);
