import React, { lazy, Suspense } from 'react'
//import clsx from 'clsx'

import config from './config/config'

import i18n from './controller/i18n'
import Log from './controller/Log'
import Settings from './controller/Settings'

import Divider from '@material-ui/core/Divider'
import Hidden from '@material-ui/core/Hidden'

//import { SwipeableViews } from '@tarragon/swipeable-tabs'

import Columns from './components/Columns'
import Widget from './components/Widget'

import Tabs, { TabPanel } from './pages/TopBar/Tabs'
import TopBar from './pages/TopBar'

import Modules, { ModuleSettings } from './modules'
import { INTRODUCTION_STEPS, DASHBOARD_STEPS } from './config/joyride'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	'@global': {
		':root': {
			'--primary': theme.palette.primary.main,
			'--secondary': theme.palette.secondary.main,
			'--text': theme.palette.text.primary,
		}
	},
});


/**
 *
 *
 */
const Module = React.memo(props => {
	Log._('Jarvis', [ 'RENDER Module', props ], 'development');
	const { widget, fullscreen } = props;
	
	// widget has no devices assigned
	/*
	if (ModuleSettings[widget.module] && ModuleSettings[widget.module].noDevicesAllowed !== true) {
		Log._('Jarvis', 'Widget ' + (widget.title || widget.module) + ' has no devices assigned to it!', 'error');
		return null;
	}
	*/
	
	// get module
	const Tag = Modules[widget.module || config.get('DEFAULT_MODULE')];
	
	// get tag
	if (Tag === undefined) {
		Log._('Jarvis', 'Undefined module "' + widget.module + '" used!', 'error');
		return null;
	}
	
	widget.id = widget.id || '';
	return (
		<Tag
			key={'tag-' + widget.id}
			id={widget.id}
			devices={widget.devices || []}
			config={widget.moduleConfig || {}}
			widgetConfig={widget}
			fullscreen={fullscreen}
			/>
	);
	
}, (prevProps, nextProps) => {
	return !prevProps.devices || !nextProps.devices || prevProps.devices.length === nextProps.devices.length;
});

/**
 *
 *
 */
const Widgets = React.memo(props => {
	Log._('Jarvis', [ 'RENDER Widgets', props ], 'development');
	const { id, tab, widgets } = props;
	
	return widgets.map((widget, i) => {
		
		if (!widget) {
			return null;
		}
		
		widget.module = widget.module || config.get('DEFAULT_MODULE');
		const moduleSettings = ModuleSettings[widget.module] || {};
		
		return (
			<Widget
				{...widget}
				key={id + '-widget-' + i}
				title={widget.title === null ? null : (widget.title || i18n.t(moduleSettings.title))}
				icon={widget.icon === null ? null : (widget.icon || moduleSettings.icon)}
				tabConfig={tab}
				>
				
				<Module {...props} widget={widget} />
			</Widget>
		);
	}).concat([
        <Hidden key={id + '-divider'} smUp>
			<Divider style={{ margin: '8.2px 20px 4px 20px', height: 0 }}/>
        </Hidden>
	]);
	
}, (prevProps, nextProps) => {
	return prevProps.widgets.length === nextProps.widgets.length;
});

/**
 *
 *
 */
const TabPanels = React.memo(props => {
	Log._('Jarvis', [ 'RENDER TabPanels', props ], 'development');
	
	const tabs = (Settings.getLayout()[0] && Settings.getLayout()[0].tabs) || [];
	return tabs.map((tab, tabIndex) => {
		tab.index = tabIndex;
		
		// detect fullscreen
		const fullscreen = tab._fullscreen !== undefined;
		
		// get widgets for each column
		let tabGridColumnsContent = null;
		if (fullscreen) {
			tabGridColumnsContent = tab._fullscreen && <Module {...props} widget={tab._fullscreen} fullscreen />
		}
		else if (tab.columns && Array.isArray(tab.columns)) {
			let tabGridColumnsContents = {};
			
			tab.columns.forEach((column, columnIndex) => {
				tabGridColumnsContents[columnIndex+1] = <Widgets {...props} id={'tab-' + tabIndex + '-column-' + columnIndex} tab={tab} widgets={column} />
			});
			
			tabGridColumnsContent = <Columns contents={tabGridColumnsContents} />
		}
		
		// render tab
		return (
			<TabPanel
				{...tab}
				key={'tabPanel-' + tabIndex}
				fullscreen={fullscreen}
				>
				
				{tabGridColumnsContent}
			</TabPanel>
		);
	});
	
}, (prevProps, nextProps) => {
	//console.log(prevProps, nextProps);
	return prevProps.selectedTab === nextProps.selectedTab;
});


/**
 *
 *
 */
class Jarvis extends React.Component {
	
    render() {
		Log._('Jarvis', 'RENDER render()', 'development');
		
        return (
			<>
				{/* JOYRIDE */}
				<LazyLoadPage
					page="./components/Joyride"
					run={Settings.get('_isTablet') === false && (Settings.get('joyrideIntroduction') !== true || Settings.get('joyrideDashboard') !== true)}
					steps={Settings.get('joyrideIntroduction') === true ? DASHBOARD_STEPS : INTRODUCTION_STEPS}
					/>
				
				
				{/* TOP BAR */}
				<TopBar>
					<Tabs />
				</TopBar>
				
				
				{/* QUICK JUMP */}
				<Hidden smUp>
					<LazyLoadPage page="./pages/QuickJump/QuickJump" />
				</Hidden>
				
				
				{/* NOTIFICATION EVENT */}
				<LazyLoadPage page="./pages/Notifications/Event" />
				
				
				{/* PAGE */}
				<LazyLoadPage page="./pages/Pages" />
				
				
				{/* POPUP */}
				<LazyLoadPage page="./components/DeviceStateDetails" />
				
				
				{/* TABS */}
			{/*
				<Hidden smUp>
				<SwipeableViews
					views={tabPanels}
					selectedView={this.state.selectedTab}
					onSwipe={this.onSelectTab}
					/>
				</Hidden>
				<Hidden smDown>
					{tabPanels}
				</Hidden>
			*/}
				<TabPanels />
			</>
		)
	}
}


function LazyLoadPage(props) {
	const Page = lazy(() => import(props.page + '.js'));
	
	return (
		<Suspense fallback={<></>}>
			<Page {...props} />
		</Suspense>
	);
}

export default withStyles(styles)(Jarvis);