import Cache from '../controller/Cache'


const _CONFIGURATION = {
	'UUID': 'bfceea54-998e-4f30-adf0-d0ff894e4a54',
	'DEFAULT_MODULE': 'StateList',
	
	'URL_PARAM_DELIMITER': '#',
	
	'ADAPTER_INSTANCE': () => {
		const params = Cache.get('params');
		const instance = params && params.instance !== undefined ? params.instance : '0';
		return 'jarvis.{instance}.'.replace('{instance}', instance);
	},
	'NODE_DATA': () => get('ADAPTER_INSTANCE') + 'info.data',
	'NODE_LOG': () => get('ADAPTER_INSTANCE') + 'info.log',
	'NODE_STYLES': () => get('ADAPTER_INSTANCE') + 'css',
	'NODE_LAYOUT': () => get('ADAPTER_INSTANCE') + 'layout',
	'NODE_SETTINGS': () => get('ADAPTER_INSTANCE') + 'settings',
	'NODE_DEVICES': () => get('ADAPTER_INSTANCE') + 'devices',
	'NODE_NOTIFICATIONS': () => get('ADAPTER_INSTANCE') + 'notifications',
	'NODE_NOTIFICATION_NEW': () => get('ADAPTER_INSTANCE') + 'addNotification',
	
	'DEFAULT_ADAPTERIMPORT': [
		'deconz',
		'hm-rpc',
		'hmip',
		'hue',
		'hue-extended',
		'shelly',
		'zigbee',
		'zwave2'
	],
	
	'SUPPORTED_LANGUAGES': [
		'en',
		'de'
	]
}

function get(param) {
	
	return typeof _CONFIGURATION[param] === 'function' ? _CONFIGURATION[param]() : _CONFIGURATION[param];
}

export default {
	_CONFIGURATION,
	get
}