import EventEmitter from 'events'
import io from 'socket.io-client'
import { v4 as uuid } from 'uuid'
import config from '../config/config'
import { allSettled } from '../helpers/utils'


/**
 * Class for Socket.io conncetion handling.
 *
 * @description This is based on an implementation @GermanBluefox (https://github.com/GermanBluefox), see https://github.com/ioBroker/adapter-react/blob/master/src/Connection.js
 *				Only required functions are taken from source class. Major change is that all events are emitted, so proper error handling is possible in the App.
 *
 *
 * @param	{String}		url
 * @param	{Object}		options
 * @return	{Socket}
 * @see https://github.com/ioBroker/ioBroker.socketio/blob/master/lib/socket.js https://github.com/ioBroker/ioBroker.js-controller/blob/master/lib/adapter.js
 *
 */
export default class Socket extends EventEmitter {
	
	constructor(url = null, options = null) {
		super();
		
		this._url = url;
		this._options = options;
		
		this._init();
	}
	
	/**
	 *
	 * @see https://socket.io/docs/v3/client-api/index.html#new-Manager-url-options
	 */
	_init(reconnect = false) {
		this.socket = this.connect();
		this.socket.open();
		this.registerEvents();
		
		/*
		 * DATA TRANSFER
		 *
		 */
		if (!reconnect) {
			this.stateSubscriptions = {} // subscribed states
			this.dataTransmitter = {} // transmitted data
		}
		
		this.subscribeState(config.get('NODE_DATA'), this.dataChange);
	}
	
	/**
	 * Subscribes to multiple states by setting a callback.
	 *
	 */
	_getPatternFromId(id) {
		let pattern = id
			.replace(/\./g, '\\.')
			.replace(/\*/g, '.*')
			.replace(/\(/g, '\\(')
			.replace(/\)/g, '\\)')
			.replace(/\+/g, '\\+')
			.replace(/\[/g, '\\[');

		if (pattern.indexOf('*') === -1) {
			pattern += '$';
		}
		
		return pattern;
	}
	
	/**
	 *
	 * API events
	 * @see https://github.com/socketio/socket.io-client/blob/HEAD/docs/API.md#socket
	 */
	registerEvents() {
		const pong = () => this.socket.emit('pong');
		
		// connect
		this.socket.on('connect', () => {
			this.emit('connect', this, this._url);
			this.socket.emit('name', 'jarvis');
			this.socket.on('ping', pong);
		});
		
		// discconect
		this.socket.on('disconnect', reason => {
			this.emit('disconnect', reason);
			this.socket.off('ping', pong);
		});
		
		// error
		this.socket.on('error', error => {
			this.emit('error', { 'type': 'error', 'event': 'error', 'message': (error && error.message) || 'Unknown error' });
			this.socket.off('ping', pong);
		});
		
		// connect_error
		this.socket.on('connect_error', error => this.emit('error', { 'type': 'connect', 'event': 'connect_error', 'message': (error && error.message) || 'Connection error' }));
		
		// connect_timeout
		this.socket.on('connect_timeout', () => this.emit('error', { 'type': 'connect', 'event': 'connect_timeout', 'message': 'Connect ran into timeout' }));
		
		// reconnect: Fired upon a successful reconnection.
		this.socket.on('reconnect', attempts => {
			this.emit('reconnect', attempts);
			this.socket.on('ping', pong);
		});
		
		// reconnect_error: Fired upon a reconnection attempt error.
		this.socket.on('reconnect_error', error => this.emit('error', { 'type': 'reconnect', 'event': 'reconnect_error', 'message': (error && error.message) || 'Reconnection error' }));
		
		// reconnect_failed: Fired upon a reconnection attempt error.
		this.socket.on('reconnect_failed', () => this.emit('error', { 'type': 'reconnect', 'event': 'reconnect_failed', 'message': 'Reconnection failed' }));
		
		/*
		 * IOBROKER EVENTS
		 *
		 */
		this.socket.on('stateChange', (id, state) => {
			setTimeout(() => this.stateChange(id, state), 0);
		});
	}
	
	/**
	 *
	 *
	 */
	connect() {
		return io(this._url, {
			'timeout': 5000, // default is 20000
			'reconnection': false,
			'autoConnect': false,
			...this._options || {}
		});
	}
	
	/**
	 *
	 *
	 */
	reconnect() {
		this._init(true);
		
		// re-register state subscriptions
		for (let state in this.stateSubscriptions) {
			this.socket.emit('subscribe', state);
		}
	}
	
	/**
	 *
	 *
	 */
	disconnect() {
		//this.removeAllListeners();
		return this.socket.close();
	}
	
	/**
	 * Subscribes to multiple states by setting a callback.
	 *
	 */
	subscribeStates(states, cb) {
		states = Array.isArray(states) ? states : [states];
		states.forEach(state => this.subscribeState(state, cb));
	}
	
	/**
	 * Subscribes to a state by setting a callback.
	 *
	 */
	subscribeState(state, cb) {
		
		if (!this.stateSubscriptions[state]) {
			this.stateSubscriptions[state] = [cb];
			this.socket.emit('subscribe', state);
		}
		else if (this.stateSubscriptions[state].indexOf(cb) === -1) {
			this.stateSubscriptions[state].push(cb); 
		}
	}
	
	/**
	 * Subscribes to a state by setting a callback.
	 *
	 */
	/*subscribeLog(cb) {
		
		if (this.socket.connected) {
			this.socket.emit('requireLog', true);
			this.socket.on('log', cb);
		}
	}*/
	
	/**
	 * Fires pre-registered callbacks (via subscribeState) upon state change.
	 *
	 */
	stateChange(state, value) {
		
		if (this.stateSubscriptions[state] !== undefined) {
			this.stateSubscriptions[state].forEach(cb => cb(state, value));
		}
	}
	
	/**
	 * Gets an object view.
	 *
	 */
	getAdapterInstances(adapter, cb) {
		
		if (typeof adapter === 'function') {
			cb = adapter;
			adapter = null;
		}
		
		return this.getObjectView('system', 'instance', { 'startkey': 'system.adapter.' + (adapter ? adapter + '.' : ''), 'endkey': 'system.adapter.' + (adapter ? adapter + '.999' : 'zzz') }, cb);
	}
	
	/**
	 * Gets all objects.
	 *
	 */
	getObjects(cb) {
		
		return allSettled([
			new Promise((resolve, reject) => this.socket.emit('getObjectView', 'system', 'device', null, (err, states) => err ? reject(err) : resolve(states))),
			new Promise((resolve, reject) => this.socket.emit('getObjectView', 'system', 'channel', null, (err, states) => err ? reject(err) : resolve(states))),
			new Promise((resolve, reject) => this.socket.emit('getObjectView', 'system', 'state', null, (err, states) => err ? reject(err) : resolve(states)))
		]);
	}
	
	/**
	 * Gets an object.
	 *
	 */
	getObject(id, cb) {
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getObject(id, (err, obj) => err ? reject(err) : resolve(obj)));
		}
		else {
			this.socket.emit('getObject', id, cb);
		}
	}
	
	/**
	 * Gets an object view.
	 *
	 */
	getObjectView(design, search, params = null, cb = null) {
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getObjectView(design, search, params, (err, res) => err || !res || !res.rows ? reject(err || new Error('Failed to retrieve enums')) : resolve(res.rows || [])));
		}
		else {
			this.socket.emit('getObjectView', design, search, params, cb);
		}
	}
	
	/**
	 * Gets a remote ressource.
	 *
	 */
	getRemoteResource(options, cb) {
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getRemoteResource(options, (err, state) => err ? reject(err, state) : resolve(state)));
		}
		else {
			this.sendTo(options.action || '_request', typeof options !== 'object' ? { 'url': options } : options, cb);
		}
	}
	
	/**
	 * Send message to an adapter.
	 *
	 */
	sendTo(action, data, adapter, cb) {
		
		if (!adapter || typeof adapter === 'function') {
			cb = adapter;
			adapter = config.get('ADAPTER_INSTANCE').slice(0, -1);
		}
		
		if (!cb) {
			return new Promise((resolve, reject) => this.sendTo(action, data, adapter, (err, state) => err ? reject(err, state) : resolve(state)));
		}
		else {
			data = data || {}
			data.token = uuid();
			
			this.dataTransmitter[data.token] = cb;
			this.socket.emit('sendTo', adapter, action, JSON.stringify(data), state => this.dataChange(null, { 'token': data.token, 'val': state }));
			
			// sendTo will not timeout by default, so it is necessary for foreign adapters
			if (adapter.indexOf('jarvis') === -1) {
				data.timeout = data.timeout || 3;
				
				setTimeout(() => {
					// callback still exists, so request has not been fulfilled
					if (this.dataTransmitter[data.token] !== undefined) {
						cb(new Error('Request failed'));
					}
					
				}, data.timeout * 1000);
			}
		}
	}
	
	/**
	 *
	 */
	dataChange = (err, state) => {
		let res = null;
		
		// check for event
		if (state && typeof state.val === 'string' && state.val.indexOf('{') > -1 && state.val.indexOf('event') > -1) {
			try {
				res = JSON.parse(state.val);
				this.emit(res.event);
			}
			catch(err) {
				console.error(err);
			}
		}
		
		// check dataTransmitter
		for (let token in this.dataTransmitter) {
			let cb = this.dataTransmitter[token];
			
			if (!cb) {
				continue;
			}
			
			try {
				// parse string as object
				if (state && typeof state.val === 'string' && state.val.indexOf('{') > -1) {
					res = JSON.parse(state.val);
					
					if (res.token === token) {
						cb(res.error || null, res.data);
						delete this.dataTransmitter[token];
					}
				}
				
				// result is object
				else if (state && typeof state.val === 'object') {
					
					if (state.token === token) {
						cb(null, state.val);
						delete this.dataTransmitter[token];
					}
				}
				
				// none, so error
				else {
					cb(new Error('Could not parse result: ' + JSON.stringify(state)));
				}
			}
			catch(err) {
				cb(err);
			}
		}
	}
	
	/**
	 * Gets a state.
	 *
	 */
	getState(id, cb) {
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getState(id, (err, state) => err ? reject(err) : resolve(state)));
		}
		else {
			this.socket.emit('getState', id, cb);
		}
	}
	
	/**
	 * Gets multiple states at once.
	 *
	 */
	getStates(pattern, cb) {
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getStates(pattern, (err, state) => err ? reject(err) : resolve(state)));
		}
		else {
			this.socket.emit('getStates', pattern, cb);
		}
	}
	
	/**
	 * Gets the state history.
	 *
	 */
	getStateHistory(id, options, cb) {
		
		if (typeof options === 'function') {
			cb = options;
			options = {};
		}
		
		if (!cb) {
			return new Promise((resolve, reject) => this.getStateHistory(id, options, (err, state) => err ? reject(err) : resolve(state)));
		}
		else {
			this.socket.emit('getHistory', id, options, cb);
		}
	}
	
	/**
	 * Sets a state.
	 *
	 */
	setState(id, val, ack, cb) {
		
		if (typeof ack === 'function') {
			cb = ack;
			ack = undefined;
		}
		
		if (!cb) {
			return new Promise((resolve, reject) => this.setState(id, val, ack, err => err ? reject(err) : resolve()));
		}
		else {
			this.socket.emit('setState', id, val, ack, cb);
		}
	}
	
	/**
	 * Unsubscribe from multiple states.
	 *
	 */
	unsubscribeStates(ids) {
		ids = Array.isArray(ids) ? ids : [ids];
		ids.forEach(id => this.unsubscribeState(id));
	}
	
	/**
	 * Unsubscribe a state.
	 *
	 */
	unsubscribeState(id) {
		
		if (this.stateSubscriptions[id] && this.socket.connected) {
			this.socket.emit('unsubscribe', id);
			delete this.stateSubscriptions[id];
		}
	}
}
