import React, { useState, useEffect, useRef } from 'react'
import clsx from 'clsx'
import { formatDistanceToNow, format } from 'date-fns'

import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import FormControl from '@material-ui/core/FormControl'
import Icon from '../../components/Icon'
import IconButton from '@material-ui/core/IconButton'
import Input from '@material-ui/core/Input'
import InputAdornment from '@material-ui/core/InputAdornment'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import Slider from '@material-ui/core/Slider'
import Switch from '@material-ui/core/Switch'

import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import Pro from '../../controller/Pro'
import Settings from '../../controller/Settings'
import componentOptions from '../componentOptions'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	error: {
		color: theme.palette.error.main
	},
	component: {
		padding: theme.spacing(1, 0, 0, 0),
		position: 'relative',
		zIndex: 99
	},
	sliderMarkLabel: {
		[theme.breakpoints.down('md')]: {
			display: 'none',
			'&:nth-child(5)': { display: 'block' },
			'&:nth-child(15)': { display: 'block' },
			'&:nth-child(25)': { display: 'block' }
		}
	},
	State: {
		width: '100%',
		margin: 'auto 0'
	},
	primaryAction: props => ({
		...props
	}),
	secondaryAction: props => ({
		color: '#999',
		fontSize: '14px',
		...props
	}),
	IconButtonAction: {
		width: 32,
		height: 32,
		fontSize: '1.5rem',
		padding: 1
	},
	positionEnd: {
		marginLeft: theme.spacing(0.5)
	},
	lineWrap: {
		display: 'block'
	}
}));


/*
 * ACTION
 *
 */
// State
function State(props) {
	const { separator, device, state, stateKey, stateVal, className } = props;
	const classes = useStyles(device ? device.getStyle('state', stateKey, stateVal) : {});
	
	// state
	const [stateValue,setStateValue] = useState(state && state.value !== undefined ? state.value : null);
	
	// reflect changes
	useEffect(() => {setStateValue(state && state.value !== undefined ? state.value : null)}, [state]);
	
	// detect HTML contents
	if (typeof stateValue === 'string' && stateValue.indexOf('<') > -1 && stateValue.indexOf('>') > -1) {
		if (Pro.isPro()) {
			return (
				<div className={clsx('popup', (props.primary && classes.primaryAction) || (props.secondary && classes.secondaryAction))}>
					<div dangerouslySetInnerHTML={{ __html: stateValue }} />
				</div>
			);
		}
		else {
			return (
				<Subtitle error state={{ 'value': i18n.t('HTML in StateList Module is only available to Pro users') + '.' }} className={classes.lineWrap} />
			);
		}
	}
	
	// text contents
	else if (stateValue !== null && stateValue !== '') {
		return (
			<>
				<span className={clsx('popup', className, classes.State, (props.primary && classes.primaryAction) || (props.secondary && classes.secondaryAction))}>
					{(stateValue.toString().indexOf('#') > -1 ? i18n.t(stateValue) : stateValue.toString()) + (state && state.unit ? (' ' + state.unit) : '')}
				</span>
				{(separator !== undefined ? (separator + ' ') : '')}
			</>
		);
	}
	
	return null;
}

// SwitchAction
function SwitchAction(props) {
	const { device, stateKey, stateVal, variant, onChange: parentOnChange } = props;
	
	let { on, off, level } = device.getStateProperties(stateKey);
	on = on !== undefined ? on : 'true';
	off = off !== undefined ? off : 'false';
	
	// onChange
	const onChange = e => {
		setChecked(e.target.checked === true);
		
		// dimmer
		let val = e.target.checked === true ? on : off;
		if (stateKey === 'level' || stateKey === 'dimmer') {
			val = e.target.checked ? (parseInt(level) || 100) : 0;
		}
		
		// cast to boolean
		val = typeof val === 'string' && (val === 'true' || val === 'false') ? (val === 'true') : val;
		device.setDeviceState(stateKey, val)
			.catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	// initial state
	const initialState = () => {
		
		for (let sKey of ['power', 'on']) {
			const powerOn = device.getStateProperties(sKey).on !== undefined ? device.getStateProperties(sKey).on : true;
			
			if (device.states[sKey] && device.states[sKey].state && device.states[sKey].state.value && device.states[sKey].state.value.val !== undefined) {
				return device.states[sKey].state.value.val === powerOn;
			}
		}
		
		return null;
	}
	
	// detect power state
	let power = stateKey === 'dimmer' || stateKey === 'level' ? initialState() : null;
	const [checked, setChecked] = useState(stateVal === undefined || stateVal === null ? off : (power !== null ? power : (stateVal.toString() === on || (typeof stateVal === 'number' && stateVal > 0))));
	useEffect(() => {
		setChecked(stateVal === undefined || stateVal === null ? off : (power !== null ? power : (stateVal.toString() === on || (typeof stateVal === 'number' && stateVal > 0))));
		
	}, [stateVal, power, on, off]);
	
	// CheckboxAction
	if (variant === 'checkbox') {
		return (
			<Checkbox
				checked={checked}
				onChange={parentOnChange || onChange}
				value={checked}
				color="primary"
				/>

		);
	}
	
	// SwitchAction
	return (
		<Switch
			checked={checked}
			onChange={parentOnChange || onChange}
			value={checked}
			color="primary"
			/>

	);
}

// CheckboxAction
function CheckboxAction(props) {
	return SwitchAction({ ...props, variant: 'checkbox' });
}

// IconButtonAction
function IconButtonAction(props) {
	const { config, device, stateKey, stateVal } = props;
	const classes = useStyles();
	
	let { on } = device.getStateProperties(stateKey);
	on = on !== undefined ? on : true;
	
	const [state,setState] = useState(stateVal == on || stateVal > 0); // eslint-disable-line eqeqeq
	const onClick = () => {
		setState(true);
		device.setDeviceState(stateKey, on).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	//
	useEffect(() => {
		setState(stateVal == on || stateVal > 0); // eslint-disable-line eqeqeq
	}, [stateVal, on]);
	
	// show state
	const showState = config[stateKey + '-showState'] !== undefined ? config[stateKey + '-showState'] : (config.showState !== undefined ? config.showState : componentOptions.IconButtonAction.showState.val);
	if (state == on && showState) { // eslint-disable-line eqeqeq
		return <State {...props} onChange={null} primary />
	}
	
	// show action
	const buttonSize = config[stateKey + '-buttonSize'] !== undefined ? config[stateKey + '-buttonSize'] : (config.buttonSize !== undefined ? config.buttonSize : componentOptions.IconButtonAction.buttonSize.val);
	const icon = config[stateKey + '-icon'] || config.icon || componentOptions.IconButtonAction.icon.val;
	return (
		<IconButton
			classes={{ root: classes.IconButtonAction }}
			color="primary"
			component="span"
			onClick={onClick}
			size={buttonSize === true ? 'small' : 'medium'}
			>
			
			<Icon icon={icon} />
		</IconButton>
	);
}

// InputAction
function InputAction(props) {
	const { device, state, stateKey } = props;
	const classes = useStyles();
	
	// parse value
	const parseValue = val => typeof val === 'number' ? (parseInt(val) || 0) : (val || '');
	
	// states
	//const dataType = useRef(typeof state.value);
	const [value,setValue] = useState(parseValue(state.value));
	const [sendValue,setSendValue] = useState(parseValue(state.value));
	
	// change from outside
	useEffect(() => {
		setValue(parseValue(state.value));
	}, [state.value]);
	
	// change from inside
	const onChange = (event, set = false) => setValue(parseValue(event.target.value));
	
	// set value
	const onSet = event => {
		let val = parseValue(event.target.value);
		
		// if the value has been send already (e.g. via Enter), do not send it again onBlur
		if (sendValue === val) {
			return false;
		}
		
		setSendValue(val);
		device.setDeviceState(stateKey, val).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	return (
		<Input
			onChange={onChange}
            endAdornment={state.unit && <InputAdornment classes={{ positionEnd: classes.positionEnd }} position="end">{state.unit}</InputAdornment>}
			size="small"
			value={value}
			inputProps={{
				style: { textAlign: 'right' },
				onKeyPress: e => e.key === 'Enter' && onSet(e),
				onBlur: onSet,
				size: Math.min(Math.ceil(value.toString().length*1,3, 15))
			}}
			/>
	);
}

// ButtonAction
function ButtonAction(props) {
	const { config, device, stateKey, stateVal } = props;
	
	let { on } = device.getStateProperties(stateKey);
	on = on !== undefined ? on : true;
	
	const [state,setState] = useState(stateVal == on || stateVal > 0); // eslint-disable-line eqeqeq
	const onClick = () => {
		setState(on);
		device.setDeviceState(stateKey, on).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	//
	useEffect(() => {
		setState(stateVal == on || stateVal > 0); // eslint-disable-line eqeqeq
	}, [stateVal, on]);
	
	// show state
	const showState = config[stateKey + '-showState'] !== undefined ? config[stateKey + '-showState'] : (config.showState !== undefined ? config.showState : componentOptions.IconButtonAction.showState.val);
	if (state == on && showState === true) { // eslint-disable-line eqeqeq
		return <State {...props} onChange={null} primary />
	}
	
	// show action
	const buttonSize = config[stateKey + '-buttonSize'] !== undefined ? config[stateKey + '-buttonSize'] : (config.buttonSize !== undefined ? config.buttonSize : componentOptions.IconButtonAction.buttonSize.val);
	const icon = config[stateKey + '-icon'] || config.icon || componentOptions.IconButtonAction.icon.val;
	return (
		<Button
			variant="outlined"
			color="primary"
			startIcon={<Icon icon={icon} />}
			onClick={onClick}
			size={buttonSize === true ? 'small' : 'medium'}
			>
			
			{config[stateKey + '-label'] || config.label || i18n.t(componentOptions.ButtonAction.label.val)}
		</Button>
	);
}

// DropdownAction
function DropdownAction(props) {
	const { device, stateKey, stateVal } = props;
	
	const onChange = (e, option) => {
		const number = parseInt(e.target.value);
		const value = Number.isNaN(number) || number.toString().length !== e.target.value.toString().length ? e.target.value : number;
		device.setDeviceState(stateKey, value).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	const options = (device.states[stateKey] && device.states[stateKey].display) || null;
	const MenuItems = options && Object.keys(options).sort().map(key => <MenuItem key={'MenuItem-' + device.id + '-' + stateKey + '-' + key} value={key}>{options[key]}</MenuItem>);
	
	return !options ? <span>{i18n.t('Display attribute of state %s must be defined').replace('%s', stateKey)}!</span> : (
		<FormControl variant="outlined" size="small">
			<Select
				value={stateVal !== undefined ? stateVal : ''}
				onChange={onChange}
				>
				
				{MenuItems}
			</Select>
		</FormControl>
	);
}


/*
 * BODY
 *
 */
// Label
function Label(props) {
	const { label, br } = props;
	const linebreak = br || false;
	
	return (
		<React.Fragment>
			{label}
			{linebreak && <br />}
		</React.Fragment>
	);
}

// Subtitle
function Subtitle(props) {
	const classes = useStyles();
	
	return (
		<span className={clsx('popup MuiTypography-root MuiListItemText-secondary MuiTypography-body2 MuiTypography-colorTextSecondary', props.error && classes.error, props.className)}>
			<State {...props} />
		</span>
	);
}

// Component
function Component(props) {
	const { title, children } = props;
	const classes = useStyles();
	
	return (
		<React.Fragment>
			<Label label={title} />
			<div className={classes.component}>
				{children}
			</div>
		</React.Fragment>
	);
}

// LastChangeBody
function LastChangeBody(props) {
	let { config, title, state, stateKey } = props;
	const classes = useStyles();
	
	const timeFormat = config[stateKey + '-timeFormat'] || config.timeFormat || Settings.getSetting('LastChangeBody') || componentOptions.LastChangeBody.timeFormat.val;
	const time = config[stateKey + '-time'] || config.time || componentOptions.LastChangeBody.time.val;
	state[time] = state[time] || 0;
	const [lastUpdate,setLastUpdate] = useState(formatDistanceToNow(new Date(state[time]-5*1000), { 'locale': i18n.getLocale(), 'includeSeconds': true, 'addSuffix': true }));
	
	// update on state change
	useEffect(() => {
		const updated = formatDistanceToNow(state[time]-(10*1000), { 'locale': i18n.getLocale(), 'includeSeconds': true, 'addSuffix': true });
		setLastUpdate(updated);
		
	}, [state, time]);
	
	// update regularly
	const refresher = useRef();
	useEffect(() => {
		
		if (state[time] > 0) {
			
			function refresh(rate = 0) {
				let updated = i18n.t('Wrong time format');
				try {
					updated = timeFormat.toLowerCase() === 'distancetonow' ? formatDistanceToNow(state[time]-(10*1000), { 'locale': i18n.getLocale(), 'includeSeconds': true, 'addSuffix': true }) : format(state[time]-(10*1000), timeFormat, { 'locale': i18n.getLocale(), 'includeSeconds': true, 'addSuffix': true });
				}
				catch(error) {
					Log._('Function (defaults)', error.message, 'error', true, error.stack);
				}
				
				
				refresher.current && clearTimeout(refresher.current);
				refresher.current = setTimeout(() => {
					setLastUpdate(updated);
					refresh((Date.now()-state[time]-5*1000)/1000/60/60);
					
				}, (rate*10)*60*1000);
			}
			
			refresh();
			return () => refresher.current && clearTimeout(refresher.current);
		}
		
	}, [state, time, timeFormat]);
	
	return (
		<>
			{title}
			{state[time] > 0 && <Subtitle {...props} state={{ 'value': lastUpdate }} className={classes.lineWrap} />}
		</>
	);
}

// LevelBody
function LevelBody(props) {
	const { device, config, stateKey, title } = props;
	const classes = useStyles();
	
	const stateVal = parseFloat(!props.stateVal ? 0 : props.stateVal);
	const [level, setLevel] = useState(stateVal);
	useEffect(() => setLevel(stateVal), [stateVal]);
	
	// validate state value
	if (typeof stateVal !== 'number' || Number.isNaN(stateVal)) {
		Log._('Function (defaults)', i18n.t('Value provided for LevelBody not a number') + '!', 'warn');
		return (
			<>
				{title}
				<Subtitle error state={{ 'value': i18n.t('Value provided for LevelBody not a number') + '!' }} className={classes.lineWrap} />
			</>
		);
	}
	
	const onChange = (event, val) => setLevel(val);
	const onChangeCommitted = (event, val) => {
		setLevel(val);
		device.setDeviceState(stateKey, val).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	// default settings
	let { min, max } = device.getStateProperties(stateKey);
	min = min === undefined ? 0 : min;
	max = max === undefined ? 100 : max;
	let tmpMax = max;
	
	max = Math.max(min, max);
	min = Math.min(min, tmpMax);
	
	const step = parseFloat(config[stateKey + '-step'] || config.step || componentOptions.LevelBody.step.val);
	let markStep = 10;
	let marks = 10;
	
	try {
		markStep = ((max-min)/parseInt(config[stateKey + '-markStep'] || config.markStep || componentOptions.LevelBody.markStep.val));
		marks = (Math.round((max-min)/markStep) || 10);
	}
	catch(err) {
		console.error(err);
	}
	
	return (
		<Component title={title || stateKey }>
			<Slider
				classes={{ markLabel: classes.sliderMarkLabel }}
				onChange={onChange}
				onChangeCommitted={onChangeCommitted}
				value={level}
				valueLabelDisplay="auto"
				marks={[...Array(marks).keys()].map(number => {
					let val = min+number*markStep;
					val = val.toString().indexOf('.') > -1 ? val.toFixed(1) : val;
					return { 'value': val, 'label': val }
					
				}).concat([{ 'value': max, 'label': max }])}
				min={parseInt(min)}
				max={parseInt(max)}
				step={step}
			/>

		</Component>
	);
}

// CustomTextBody
function CustomTextBody(props) {
	const { config, title, stateKey } = props;
	const classes = useStyles();
	
	const txt = config[stateKey + '-text'] || config.text || componentOptions.CustomTextBody.text.val;
	
	return (
		<>
			{title}
			<Subtitle state={{ 'value': txt }} className={classes.lineWrap} />
		</>
	);
}


export default {
	_options: componentOptions,
	components: {
		Label,
		Component
	},
	body: {
		LastChangeBody,
		LevelBody,
		CustomTextBody
	},
	action: {
		State,
		Subtitle,
		SwitchAction,
		CheckboxAction,
		InputAction,
		ButtonAction,
		IconButtonAction,
		DropdownAction
	}
}