const _ls = require('local-storage');


/**
 * Cache
 *
 *
 */
export default class Cache {

	/*
	 * holds the cache
	 */
	static cache = {}
	
	/*
	 * cache namespace
	 */
	static ns = null;
	
	/*
	 * get namespace
	 */
    static setNamespace(ns) {
		Cache.ns = ns;
		return Cache;
	}
	
	/**
	 *
	 */
	static get(key) {
		
		// get from cache
		let got = Cache.cache[Cache.ns + key];
		
		// get from local storage
		if (got === null || got === undefined) {
			got = _ls.get(Cache.ns + key);
		}
		
		return got !== null && got !== undefined ? got : null;
	}
	
	/**
	 * 
	 */
	static set(key, value, persist = true) {
		
		if (key === undefined || value === undefined) {
			return false;
		}
		else if (typeof key === 'object') {
			persist = value !== undefined ? value : persist; // remap arg
			Object.keys(key).forEach(k => Cache.set(k, key[k], persist));
		}
		
		Cache.cache[Cache.ns + key] = value;
		
		if (persist) {
			_ls.set(Cache.ns + key, value);
		}
		
		return Cache;
	}
}
