import { useTheme } from '@material-ui/core/styles'
import useMediaQuery from '@material-ui/core/useMediaQuery'

/**
 * Strip HTML.
 *
 */
export function stripHtml(html) {
	/*
	let tmp = document.createElement('DIV');
	tmp.innerHTML = html;
	return tmp.textContent || tmp.innerText || '';
	*/
	
	//return html.replace(/<[^>]*>?/gm, '');
	return html.replace(/(<([^>]+)>)/gi, '');
}

/**
 * Capitalize first letter of a word.
 *
 */
export default function ucFirst(str) {
	if (!str) {
		return '';
	}
	
	return str.charAt(0).toUpperCase() + str.slice(1);
}

/**
 * Lowercases first letter of a word.
 *
 */
export function lcFirst(str) {
	if (!str) {
		return '';
	}
	
	return str.charAt(0).toLowerCase() + str.slice(1);
}

/**
 * Get a random number
 *
 */
export function random(min, max) {
	return Math.floor(Math.random() * (max - min + 1) + min);
}

/**
 *
 */
export function leadingZero(val) {
	return ('0' + val).substr(-2);
}

/**
 *
 */
export function getValueAndUnit(val) {
	
	const value = parseInt(val);
	if (value && value > 0) {
		
		// get unix
		let unit = 'px'; // default
		['px', 'vw', 'vh', '%'].forEach(u => {
			if (val.indexOf(u) > -1) {
				unit = u;
			}
		});
		
		// convert to px
		let px = val;
		if (unit === 'vw') {
			px = value/100 * window.innerWidth;
		}
		else if (unit === 'vh') {
			px = value/100 * window.innerHeight;
		}
		
		//
		return [ value, unit, px ];
	}
	
	return [ null, null, null ];
}

/**
 * Move a key in an object up or down ("sort")
 *
 */
export function moveKeyInObject(obj, key, action) {
	
	// sort array
	let sorted = moveKeyInArray(Object.keys(obj), key, action);
	
	// build new object
	let next = {}
	sorted.map(key => next[key] = obj[key]);
	return next;
}

/**
 * Move a key in an object up or down ("sort")
 *
 */
export function moveKeyInArray(arr, keyOrIndex, action) {
	
	let index = typeof keyOrIndex === 'string' ? arr.indexOf(keyOrIndex) : keyOrIndex;
	let element = arr[index];
	
	// move up
	if (action === 'up' && index-1 >= 0) {
	  arr.splice(index-1, 0, element);
	  arr.splice(index+1, 1);
	}
	
	// move down
	else if (action === 'down' && index+1 < arr.length) {
	  arr.splice(index+2, 0, element);
	  arr.splice(index, 1);
	}
	
	return arr;
}

/**
 * Sort array or object by specific key
 *
 */
export function sort(arrayOrObject, key) {
	return Array.isArray(arrayOrObject) ? sortArray(arrayOrObject, key) : sortArrayWithObjects(arrayOrObject, key);
}

/**
 * Sort array by specific key
 *
 */
export function sortArray(arr, key) {
	
	arr = arr.slice(0);
	arr.sort((a, b) => {
		let x = (a[key] && a[key].toLowerCase()) || '';
		let y = (b[key] && b[key].toLowerCase()) || '';
		return x < y ? -1 : x > y ? 1 : 0;
	});
	
	return arr;
}

/**
 * Sort array with objects by specific key
 *
 */
export function sortArrayWithObjects(arrayOfObjects, key = '_sort', lowerCaseComparision = true) {
	
	return arrayOfObjects.slice(0).sort((a, b) => {
		a[key] = lowerCaseComparision === true ? a[key].toLowerCase() : a[key];
		b[key] = lowerCaseComparision === true ? b[key].toLowerCase() : b[key];
		
		return a[key] > b[key] ? 1 : -1;
	});
}

/**
 * Sort object
 *
 */
export function sortObjectByValue(obj) {
	
	return Object.entries(obj)
		.sort(([,a],[,b]) => a > b ? 1 : -1)
		.reduce((r, [k, v]) => ({ ...r, [k]: v }), {});
}

/**
 * Sort object
 *
 */
export function sortObject(unordered, caseSensitiv = true) {
	
	const ordered = {}
	Object.keys(unordered).sort(caseSensitiv === false && ((a, b) => a.toLowerCase().localeCompare(b.toLowerCase()))).forEach(key => {
		ordered[key] = unordered[key];
	});
	
	return ordered;
	
	/*
	return Object
		.entries(obj)
		.sort(([,a], [,b]) => console.log(a,b))
		.reduce((_sortedObj, [k, v]) => ({
			..._sortedObj, 
			[k]: v
		}), {});
	*/
}

/**
 * 
 *
 */
export function joinConfig(UserConfig, AdapterConfig) {
	
	let config = {}
	config._defaults = {}
	
	if (typeof AdapterConfig === 'object') {
		AdapterConfig = Object.keys(AdapterConfig).map(key => ({ 'parameter': key, ...AdapterConfig[key] }));
	}
	
	AdapterConfig.forEach(param => {
		config._defaults[param.parameter] = param.val !== undefined ? param.val : param.placeholder;
		config[param.parameter] = UserConfig[param.parameter] !== undefined ? UserConfig[param.parameter] : config._defaults[param.parameter];
		
		if (param.json === true && config[param.parameter] !== undefined && config[param.parameter] && typeof config[param.parameter] === 'string') {
			
			try {
				config[param.parameter] = JSON.parse(config[param.parameter]);
			}
			catch(err) {
				console.error(err);
			}
		}
	});
	
	return config;
}

/**
 * 
 * @see https://stackoverflow.com/a/39031032
 */
export function allSettled(promises) {
	const wrappedPromises = promises.map(p => Promise.resolve(p)
		.then(
			val => ({ status: 'fulfilled', value: val }),
			err => ({ status: 'rejected', reason: err })
		));
	
	return Promise.all(wrappedPromises);
}

/**
 *
 *
 * @see https://material-ui.com/components/use-media-query/#migrating-from-withwidth
 */
export function useWidth() {
	const theme = useTheme();
	const keys = [...theme.breakpoints.keys].reverse();
	
	return (
		keys.reduce((output, key) => {
			// eslint-disable-next-line react-hooks/rules-of-hooks
			const matches = useMediaQuery(theme.breakpoints.up(key));
			return !output && matches ? key : output;
		}, null) || 'xs'
	);
}
