
const LastChangeBody = {
	"time": {
		"label": "config#componentOptions#LastChangeBody#time#label",
		"info": "config#componentOptions#LastChangeBody#time#info",
		"type": "Select",
		"selection": [
			{
				"lc": "Last Change"
			},
			{
				"ts": "Last Update"
			}
		],
		"val": "lc"
	},
	"timeFormat": {
		"label": "config#componentOptions#LastChangeBody#timeFormat#label",
		"info": "config#componentOptions#LastChangeBody#timeFormat#info",
		"link": "https://date-fns.org/v2.16.1/docs/format",
		"val": "distancetonow"
	}
}

const LevelBody = {
	"step": {
		"label": "config#componentOptions#LevelBody#step#label",
		"info": "config#componentOptions#LevelBody#step#info",
		"val": 1
	},
	"markStep": {
		"label": "config#componentOptions#LevelBody#markStep#label",
		"info": "config#componentOptions#LevelBody#markStep#info",
		"val": 10
	}
}

const CustomTextBody = {
	"text": {
		"label": "config#componentOptions#CustomTextBody#text#label",
		"val": ""
	}
}

const IconButtonAction = {
	"icon": {
		"label": "config#componentOptions#IconButtonAction#icon#label",
		"info": "config#componentOptions#IconButtonAction#icon#info",
		"val": "power"
	},
	"showState": {
		"label": "config#componentOptions#IconButtonAction#showState#label",
		"info": "config#componentOptions#IconButtonAction#showState#info",
		"type": "Switch",
		"val": false
	},
	"buttonSize": {
		"label": "config#componentOptions#ButtonAction#buttonSize#label",
		"info": "config#componentOptions#ButtonAction#buttonSize#info",
		"type": "Switch",
		"val": true
	}
}

const ButtonAction = {
	...IconButtonAction,
	"label": {
		"label": "config#componentOptions#ButtonAction#label#label",
		"info": "config#componentOptions#ButtonAction#label#info",
		"_translate": true,
		"val": "turn on",
	},
	"buttonSize": {
		"label": "config#componentOptions#ButtonAction#buttonSize#label",
		"info": "config#componentOptions#ButtonAction#buttonSize#info",
		"type": "Switch",
		"val": true
	}
}

const BlindLevelAction = {
	"activityState": {
		"label": "config#componentOptions#BlindLevelAction#activityState#label",
		"info": "config#componentOptions#BlindLevelAction#activityState#info",
		"type": "Select",
		"selection": props => [''].concat(Object.keys((props.device && props.device.states) || {}).map(stateKey => ({ [stateKey]: stateKey }))),
		"vla": "activity"
	},
	"stopAction": {
		"label": "config#componentOptions#BlindLevelAction#stopAction#label",
		"info": "config#componentOptions#BlindLevelAction#stopAction#info",
		"type": "Select",
		"selection": props => [''].concat(Object.keys((props.device && props.device.states) || {}).map(stateKey => ({ [stateKey]: stateKey }))),
		"val": "stop"
	}
}


export default {
	LastChangeBody,
	LevelBody,
	CustomTextBody,
	IconButtonAction,
	ButtonAction,
	BlindLevelAction
}