import React, { useRef, useState, useEffect } from 'react'
import clsx from 'clsx'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'
import StateListItem from './StateListItem'
import Switch from '@material-ui/core/Switch'

import Icon from '../../components/Icon'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	defaultListSectionDivider: {
		padding: '2px 0 0 0 !important',
		marginBottom: '2px !important'
	},
	defaultListSectionDividerItem: {
		margin: '8px 0 4px 0',
		padding: '0 16px !important'
	},
	defaultListSectionText: {
		margin: 0,
		[theme.breakpoints.down('md')]: {
			margin: '0 0 0 10px',
		}
	},
	fullWidth: {
		width: '100%',
	},
	horizontalList: {
		display: 'flex',
		flexDirection: 'row',
		flexWrap: 'wrap',
		justifyContent: 'space-around',
	}
});


const ListGroup = withStyles(styles)(props => {
	Log._('StateList', [ 'RENDER ListGroup', props ], 'development');
	const { classes, title, options } = props;
	
	
	/** 
	 *
	 */
	const getGroupDevicesState = g => {
		let devices = {}
		
		const group = props.getDevices();
		for (let deviceId in group) {
			const { device, options } = group[deviceId];
			
			const state = device.getDeviceState(options.primaryStateKey);
			const stateVal = state && state.val;
			
			if (options.useInGroup !== false) {
				let { on } = device.getStateProperties(options.primaryStateKey);
				on = on !== undefined ? on : true;
				
				devices[deviceId] = stateVal === on || (typeof stateVal === 'number' && stateVal > 0);
			}
		}
		
		return devices;
	}
	
	/*
	 *
	 */
	const groupDevices = useRef(getGroupDevicesState(props.getDevices() || {}));
	const [groupState,setGroupState] = useState(Object.values(groupDevices.current).indexOf(false) === -1);
	
	useEffect(() => {
		let listeners = {}
		
		// attach state listener
		const group = props.getDevices();
		for (let key in group) {
			const { device, options } = group[key];
			
			if (options.useInGroup !== false) {
				let { on } = device.getStateProperties(options.primaryStateKey);
				on = on !== undefined ? on : true;
				
				listeners[key] = (stateKey, state) => {
					if (stateKey === options.primaryStateKey) {
						groupDevices.current[key] = state.val === on;
						setGroupState(Object.values(groupDevices.current).indexOf(false) === -1);
					}
				}
				
				device.listen(listeners[key]);
			}
		}
		
		// remove state listener
		return () => {
			for (let key in group) {
				const { device } = group[key];
				device.unlisten(listeners[key]);
			}
		}
		
	}, [props]);
	
	const handleSwitchChange = (e, targetStatus) => handleChange(targetStatus);
	const handleChangeOn = () => handleChange(true);
	const handleChangeOff = () => handleChange(false);
	const handleChange = targetStatus => {
		
		const stateList = props.getDevices() || {}
		Object.keys(stateList).forEach(key => {
			const { device, options } = stateList[key];
			let status = targetStatus;
			
			// with defined on/off
			let primaryStateKey = options.primaryStateKey.toLowerCase();
			let { on, off, level } = device.getStateProperties(options.primaryStateKey);
			
			if (on !== undefined && off !== undefined) {
				status = status ? (level ? level : on) : off;
			}
			
			// no on/off propery defined, thus fallback to match the stateKey
			else if (primaryStateKey === 'level' || primaryStateKey === 'dimmer') {
				status = status ? (level ? level : 100) : 0;
			}
			
			if (options.useInGroup !== false) {
				device.setDeviceState(options.primaryStateKey, status)
					.catch(res => Log._('StateList', res.error.message, 'error', true, res.error.stack));
			}
		});
	}
	
	return (
		<React.Fragment>
			<ListItem divider className={classes.defaultListSectionDivider} />
			<ListItem className={classes.defaultListSectionDividerItem}>
				<ListItemText
					className={classes.defaultListSectionText}
					primary={title}
					/>
				
				<ListItemSecondaryAction>
				{options.actions === true && (!options.actionsElement || options.actionsElement === 'SwitchAction') &&
					<Switch
						checked={groupState}
						onChange={handleSwitchChange}
						/>
				}
				{options.actions === true && options.actionsElement === 'ButtonAction' &&
					<ButtonGroup color="primary" size="small">
						<Button onClick={handleChangeOff}>{options.actionLabelOff || i18n.t('off')}</Button>
						<Button onClick={handleChangeOn}>{options.actionLabelOn || i18n.t('on')}</Button>
					</ButtonGroup>
				}
				{options.actions === true && options.actionsElement === 'IconButtonAction' &&
					<ButtonGroup color="primary" size="small">
						<Button onClick={handleChangeOff}><Icon small icon={options.actionLabelOff || 'mdi-power-off'} /></Button>
						<Button onClick={handleChangeOn}><Icon small icon={options.actionLabelOn || 'mdi-power'} /></Button>
					</ButtonGroup>
				}
				</ListItemSecondaryAction>
			</ListItem>
		</React.Fragment>
	);
});


class StateList extends React.Component {
	
	constructor(props) {
		super(props);
		this.groups = {};
	}
	
	
	render() {
		Log._('StateList', [ 'RENDER render()', this.props ], 'development');
		const { classes, devices, ...otherProps } = this.props;
		
		let group = null;
		let items = [];
		let index = 0;
		
		Devices.forEachDevice(devices, (device, i, options) => {
			
			if (options.hide !== true) {
				index++;
				
				// group
				if (device.type === 'group') {
					group = device.id;
					
					items.push(
						<ListGroup
							key={'ListGroup-' + index + '-' + device.id}
							title={device.name}
							getDevices={() => this.groups[device.id]}
							options={options}
							/>
					);
				}
				
				// divider
				else if (device.type === 'divider') {
					group = null;
					
					items.push(
						<ListItem key={'ListDivider-' + index + '-' + device.id} divider className={classes.defaultListSectionDivider} />
					);
				}
				
				// device
				else if (device._isDevice) {
					
					if (group && options.actionElement) {
						this.groups[group] = this.groups[group] || {};
						this.groups[group][device.id + '->' + (options.primaryStateKey || 'default')] = this.groups[group][device.id + '->' + (options.primaryStateKey || 'default')] || { device, options };
					}
					
					items.push(
						<StateListItem
							{...otherProps}
							key={'StateListItem-' + index + '-' + device.id}
							
							config={{
								...otherProps.config,
								divider: this.props.config.divider && index > 1
							}}
							
							device={device}
							options={options}
							/>
					);
				}
			}
		});
		
		return (
			<List dense={false} className={clsx(this.props.config.horizontal ? classes.horizontalList : classes.fullWidth)}>
				{items}
			</List>

		);
	}
}

export default withStyles(styles)(StateList);
