import React from 'react'
import clsx from 'clsx'
import i18n from '../../controller/i18n'

import Info from '../Info'
import Grid from '@material-ui/core/Grid'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	Field: {
		display: 'inline-flex',
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		padding: '0 !important',
	},
	CustomField: {
	},
}));


/*
 *
 */
export default function Field(props) {
	const { children, id, info } = props;
	const classes = useStyles(); // useStyles(props);
	
	const width = props.width || 250;
	return (
		<Grid
			key={'grid-' + id}
			className={clsx(classes.Field, classes.CustomField)}
			style={{ width }}
			container
			item
			>
			
			{info && i18n.t(info) && <Grid item style={{ width: 30 }}>
				<Info {...props} />
			</Grid>}
			<Grid container item style={{ width: info && i18n.t(info) ? width-30 : (width || '100%'), flexWrap: 'initial' }}>
				{children}
			</Grid>
		</Grid>

	);
}