import EventEmitter from 'events'
import { v1 as uuid } from 'uuid'

import Connection from './Connection'
import i18n from './i18n'
import Pro from './Pro'

import config from '../config/config'


/**
 * Notifications
 *
 *
 */
export default class Singleton {

	constructor() {
		if (!Singleton.instance) {
			Singleton.instance = new Notifications();
		}
		
		return Singleton.instance;
	}
}

class Notifications extends EventEmitter {

	constructor() {
		super();
		
		window.Notification = window.Notification || {}
		this.socket = Connection.connection;
		this.notifications = [];
		this.unread = [];
	}
	
	/**
	 *
	 */
	retrieve(type) {
		
		if (!Pro.isPro()) {
			return Promise.reject('Notification feature may only be used with a Pro account');
		}
		
		return new Promise((resolve, reject) => {
			
			this.socket.getState(config.get('NODE_NOTIFICATIONS'))
				.then(state => {
					this._parse(state);
					
					return this.socket.getState(config.get('NODE_NOTIFICATION_NEW'));
				})
				.then(state => {
					this.process(config.get('NODE_NOTIFICATION_NEW'), state);
					resolve();
				})
				.catch(reject);
		});
	}
	
	/**
	 *
	 * @see https://developer.mozilla.org/de/docs/Web/API/notification
	 */
	emitBrowserNotification(title, options) {
		const notification = new Notification(title, options);
		notification.onclick = e => {
			console.log(e);
		}
	}
	
	/**
	 *
	 */
	getBrowserSubscription() {
		return Notification.permission === 'granted';
	}
	
	/**
	 *
	 */
	askBrowserSubscription() {
		this.emit('new', {
			'display': 'askBrowserSubscription'
		});
		
		return this;
	}
	
	/**
	 *
	 */
	reqBrowserSubscription() {
		
		if (!Pro.isPro()) {
			return Promise.reject('Notification feature may only be used with a Pro account');
		}
		
		return Notification && Notification.requestPermission();
	}
	
	/**
	 *
	 */
	process(id, state) {
		
		if (state && state.val) {
			this.socket.setState(id, '')
				.catch(console.error);
			
			try {
				let notification = JSON.parse(state.val);
				notification = typeof notification === 'string' ? { 'title': notification } : notification;
				notification.id = uuid();
				notification.ts = notification.ts || Date.now();
				notification.title = notification.title || notification.message;
				notification.state = !notification.state ? 'read' : notification.state;
				
				if (!notification.title) {
					throw new Error(i18n.t('Notification has no title and will thus be ignored') + '!');
				}
				
				if (this.getBrowserSubscription() === true) {
					this.emitBrowserNotification(notification.title, { 'body': notification.message, 'icon': notification.icon, 'tag': notification.id });
				}
				
				this.emit('new', notification);
				
				if (notification.display !== 'none') {
					this.save([ ...this.notifications, notification ]);
				}
			}
			catch(err) {
				console.error(err);
			}
		}
	}
	
	/**
	 *
	 */
	subscribe() {
		
		if (!Pro.isPro()) {
			return false;
		}
		
		/*
		 * new notification
		 */
		this.socket.subscribeState(config.get('NODE_NOTIFICATION_NEW'), (id, state) => this.process(id, state));
		
		/*
		 * all notifications
		 */
		this.socket.subscribeState(config.get('NODE_NOTIFICATIONS'), (id, state) => this._parse(state));
		
		return this;
	}
	
	/**
	 *
	 */
	_parse(state) {
		
		try {
			this.notifications = JSON.parse(state.val || '[]').map(notification => ({ ...notification, 'id': notification.id || uuid() }));
			this.emit('all', this.notifications);
			
			this.unread = this.notifications.filter(notification => !notification.state || notification.state === 'unread');
			this.emit('unread', this.unread);
		}
		catch(err) {
			console.error(err);
		}
	}
	
	/**
	 *
	 */
	save(notifications) {
		this.socket.setState(config.get('NODE_NOTIFICATIONS'), JSON.stringify(notifications))
			.catch(console.error);
	}
	
	/**
	 *
	 */
	get(type, cb) {
		
		if (type === 'all' && cb) {
			cb(this.notifications);
		}
		else if (type === 'unread' && cb) {
			cb(this.unread);
		}
	}
	
	/**
	 *
	 */
	set(id, props = {}) {
		const foundNotification = this.notifications.findIndex(notification => notification.id === id);
		
		if (foundNotification > -1) {
			this.notifications[foundNotification] = { ...this.notifications[foundNotification], ...props }
			this.save(this.notifications);
		}
	}
	
	/**
	 *
	 */
	del(id) {
		const foundNotification = this.notifications.findIndex(notification => notification.id === id);
		
		if (foundNotification > -1) {
			this.notifications.splice(foundNotification, 1);
			this.save(this.notifications);
		}
	}
}
