import i18n from '../../../controller/i18n'

export default {
	"pageSettings": {
		"label": "config#Settings#pageSettings#label",
		"settings": {
			"language": {
				"label": "config#Settings#pageSettings#settings#language#label",
				"type": "Select",
				"selection": [
					{
						"de-DE": "Deutsch",
						"_translated": true
					},
					{
						"en-GB": "English",
						"_translated": true
					}
				],
				"val": () => i18n.getLanguage(true)
			},
			"pageFavicon": {
				"label": "config#Settings#pageSettings#settings#pageFavicon#label",
				"info": "config#Settings#pageSettings#settings#pageFavicon#info"
			},
			"pageTitle": {
				"label": "config#Settings#pageSettings#settings#pageTitle#label",
				"placeholder": "jarvis - just another remarkable vis"
			},
			"LastChangeBody": {
				"label": "config#Settings#pageSettings#settings#LastChangeBody#label",
				"info": "config#Settings#pageSettings#settings#LastChangeBody#info",
				"placeholder": "DistanceToNow"
			}
		}
	},
	"themeSettings": {
		"label": "config#Settings#themeSettings#label",
		"settings": {
			"themeDarkMode": {
				"label": "config#Settings#themeSettings#settings#themeDarkMode#label",
				"type": "Switch"
			},
			"themeColorPrimary": {
				"label": "config#Settings#themeSettings#settings#themeColorPrimary#label",
				"info": "config#Settings#themeSettings#settings#themeColorPrimary#info",
				"link": "https://material-ui.com/customization/color/#color-palette",
				"type": "Text",
				"placeholder": "blue|700"
			},
			"themeColorSecondary": {
				"label": "config#Settings#themeSettings#settings#themeColorSecondary#label",
				"info": "config#Settings#themeSettings#settings#themeColorSecondary#info",
				"link": "https://material-ui.com/customization/color/#color-palette",
				"type": "Text",
				"placeholder": "pink|500"
			},
			"defaultBoxIcon": {
				"label": "config#Settings#themeSettings#settings#defaultBoxIcon#label",
				"type": "Text",
				"placeholder": "home"
			},
			"hideTopBar": {
				"label": "config#Settings#themeSettings#settings#hideTopBar#label",
				"type": "Switch",
				"val": false
			},
			"columnsOnSmallScreenSizes": {
				"label": "config#Settings#themeSettings#settings#columnsOnSmallScreenSizes#label",
				"info": "config#Settings#themeSettings#settings#columnsOnSmallScreenSizes#info",
				"type": "Select",
				"selection": [
					{ "12": "alle", "_translated": true },
					{ "1": "1", "_translated": true },
					{ "2": "2", "_translated": true },
					{ "3": "3", "_translated": true },
					{ "4": "4", "_translated": true },
					//{ "5": "5", "_translated": true },
					{ "6": "6", "_translated": true }
				],
				"val": 2
			},
			"columnsOnMediumScreenSizes": {
				"label": "config#Settings#themeSettings#settings#columnsOnMediumScreenSizes#label",
				"info": "config#Settings#themeSettings#settings#columnsOnMediumScreenSizes#info",
				"type": "Select",
				"selection": [
					{ "12": "alle", "_translated": true },
					{ "1": "1", "_translated": true },
					{ "2": "2", "_translated": true },
					{ "3": "3", "_translated": true },
					{ "4": "4", "_translated": true },
					//{ "5": "5", "_translated": true },
					{ "6": "6", "_translated": true }
				],
				"val": 2
			},
			"columnsOnLargeScreenSizes": {
				"label": "config#Settings#themeSettings#settings#columnsOnLargeScreenSizes#label",
				"info": "config#Settings#themeSettings#settings#columnsOnLargeScreenSizes#info",
				"type": "Select",
				"selection": [
					{ "12": "alle", "_translated": true },
					{ "1": "1", "_translated": true },
					{ "2": "2", "_translated": true },
					{ "3": "3", "_translated": true },
					{ "4": "4", "_translated": true },
					//{ "5": "5", "_translated": true },
					{ "6": "6", "_translated": true }
				],
				"val": 12
			},
			"columnsOnExtraLargeScreenSizes": {
				"label": "config#Settings#themeSettings#settings#columnsOnExtraLargeScreenSizes#label",
				"info": "config#Settings#themeSettings#settings#columnsOnExtraLargeScreenSizes#info",
				"type": "Select",
				"selection": [
					{ "12": "alle", "_translated": true },
					{ "1": "1", "_translated": true },
					{ "2": "2", "_translated": true },
					{ "3": "3", "_translated": true },
					{ "4": "4", "_translated": true },
					//{ "5": "5", "_translated": true },
					{ "6": "6", "_translated": true }
				],
				"val": 12
			}
		}
	},
	"mobileSettings": {
		"label": "config#Settings#mobileSettings#label",
		"settings": {
			"hideQuickJumper": {
				"label": "config#Settings#mobileSettings#settings#hideQuickJumper#label",
				"type": "Switch"
			}
		}
	}
}