import React, { useState, useEffect, useRef } from 'react'
import { v4 as uuid } from 'uuid'

import Connection from '../../controller/Connection'
import Inputs from '../../components/Inputs'

import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import DeleteIcon from '@material-ui/icons/Delete'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%'
	}
}));


export default {
	icon: 'mdi-calendar-month-outline',
	devices: false,
	ModuleConfiguration
}

/*
 *
 */
function ModuleConfiguration(props) {
	const { options, _onChange } = props;
	const classes = useStyles();
	
	
	const [calendars,setCalendars] = useState(options.calendars || []);
	const EDITOR = useRef({ 'calendars': options.calendars || [] });
	
	/*
	 * update calendars from props when widget reopens
	 */
	useEffect(() => {
		
		if (Array.isArray(props.options.calendars) && props.options.calendars.length > 0) {
			setCalendars(props.options.calendars);
			EDITOR.current.calendars = props.options.calendars;
		}
		
	}, [props.options]);
	
	/*
	 * send changes back to widget
	 */
	useEffect(() => {
		_onChange({ 'id': 'moduleConfig.calendars' }, calendars);
		
	}, [_onChange, calendars]);
	
	/*
	 * Initially get calendars from ical adapter
	 */
	useEffect(() => {
		
		if (EDITOR.current.calendars.length === 0) {
			Connection.connection.getObject('system.adapter.ical.0', (err, obj) => {
				
				if (!err && obj && obj.native && obj.native.calendars && Array.isArray(obj.native.calendars)) {
					const newCalendars = obj.native.calendars.map(calendar => ({ ...calendar, 'id': uuid() }));
					setCalendars(newCalendars);
					EDITOR.current.calendars = newCalendars;
				}
			});
		}
		
	}, []);
	
	/**
	 *
	 */
	const onChange = (props, val) => {
		const [, index, id] = props.id.split('.');
		EDITOR.current.calendars[index][id] = val;
		_onChange({ 'id': 'moduleConfig.calendars' }, EDITOR.current.calendars);
	}
	
	/**
	 *
	 */
	const _addCalendarEntry = () => {
		const newCalendar = { 'id': uuid(), sslignore: true } 
		setCalendars(prevCalendars => ([ ...prevCalendars, newCalendar ]));
		EDITOR.current.calendars.push(newCalendar);
	}
	
	/**
	 *
	 */
	const _deleteCalendarEntry = index => {
		
		//EDITOR.current.calendars.splice(index, 1);
		setCalendars(prevCalendars => {
			let nextCalendars = [ ...prevCalendars ];
			nextCalendars.splice(index, 1);
			return nextCalendars;
		});
	}
	
	/*
	 *
	 */
	const calendarList = calendars.map((calendar, i) => {
		
		return (
			<ListItem
				key={'Calendar-' + calendar.id}
				divider
				>
				
				<ListItemIcon>
					<IconButton onClick={() => _deleteCalendarEntry(i)}>
						<DeleteIcon />
					</IconButton>
				</ListItemIcon>
				<ListItemText
					disableTypography
					primary={<Inputs.Fieldset>
						<Inputs.Text
							id={'calendar.' + i + '.name'}
							label='Label'
							width={120}
							
							val={calendar.name || ''}
							onChange={onChange}
							/>
						
						<Inputs.Text
							id={'calendar.' + i + '.icon'}
							label='Icon'
							width={120}
							
							val={calendar.icon || 'mdi-calendar'}
							onChange={onChange}
							/>
						
						<Inputs.Text
							id={'calendar.' + i + '.url'}
							label='Calendar URL'
							width={430}
							
							val={calendar.url || ''}
							onChange={onChange}
							/>
						
						<Inputs.Text
							id={'calendar.' + i + '.user'}
							label='User'
							width={120}
							
							val={calendar.user || ''}
							onChange={onChange}
							/>
						
						<Inputs.Text
							id={'calendar.' + i + '.pass'}
							label='Password'
							type='password'
							width={130}
							
							val={calendar.pass || ''}
							onChange={onChange}
							/>
						
						<Inputs.Switch
							id={'calendar.' + i + '.sslignore'}
							label='Ignore SSL errors'
							width={100}
							
							val={calendar.sslignore !== undefined ? calendar.sslignore : false}
							onChange={onChange}
							/>
					</Inputs.Fieldset>}
					/>
				
			</ListItem>
		);
	});
	
	return (
		<>
			<Inputs.Button
				label="Add Calendar"
				onClick={_addCalendarEntry}
				/>
			
			<List classes={{ root: classes.fullWidth }}>
				{calendarList}
			</List>
		</>
	)
}
