import React from 'react'
//import clsx from 'clsx'

import Icon from '../../components/Icon'

import Typography from '@material-ui/core/Typography'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	ErrorPage: style => ({
		width: style.wdith,
		height: style.height,
		backgroundColor: theme.palette.error.dark,
		display: 'flex',
		justifyContent: 'center',
		alignContent: 'center',
		flexDirection: 'column',
		textAlign: 'center',
		color: '#fff'
	}),
	errorTitle: {
		width: '80%',
		margin: '24px auto 8px auto',
	},
	errorStack: {
		width: '80%',
		margin: '0 auto',
		whiteSpace: 'pre',
		backgroundColor: 'transparent'
	}
}));


export default function ErrorPage(props) {
	const { icon, title, message, error } = props;
	const classes = useStyles({
		width: window.innerWidth,
		height: window.innerHeight
	});
	
	return (
		<div className={classes.ErrorPage}>
			<Icon style={{ fontSize: '10rem' }} icon={icon} />
			<Typography display="block" variant="h2">{title}</Typography>
			<Typography display="block" variant="body1">{message}</Typography>
			
			{error && <>
			<strong className={classes.errorTitle}>{error.name + ': ' + error.message}</strong>
			<code className={classes.errorStack}>{error.stack}</code>
			</>}
		</div>
	);
}
