import React from 'react'
import clsx from 'clsx'
import i18n from '../../controller/i18n'

import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import { MuiPickersUtilsProvider } from '@material-ui/pickers'
import Typography from '@material-ui/core/Typography'

/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	noPadding: {
		padding: '0 !important'
	},
	Fieldset: customStyles => ({
		padding: theme.spacing(0.5, 3, 2, 3),
		...customStyles
	}),
	FieldsetItem: {
		padding: theme.spacing(2, 0, 1, 0)
	},
	Legend: customStyles => ({
		fontSize: '1.2rem',
		color: theme.palette.primary.dark,
		padding: theme.spacing(1, 3),
		...customStyles
	}),
	Legend_h4: {
		fontSize: '1.0rem !important',
		padding: theme.spacing(3, 0, 1, 0) + ' !important',
	}
}));

/*
 *
 */
export default function Fieldset(props) {
	const { children, noPadding, label, ...otherProps } = props;
	const classes = useStyles();
	
	return (
		<React.Fragment key={label && 'Fieldset-' + label.toLowerCase().replace(/ /g, '')}>
			<FieldsetLegend {...otherProps} label={label} />
			<Grid container className={clsx(classes.Fieldset, noPadding && classes.noPadding, label)} {...otherProps}>
				{children}
			</Grid>
		</React.Fragment>
	);
}

/*
 *
 */
export function FieldsetItem(props) {
	const { container, children, columns, noPadding, centered, className, ...otherProps } = props;
	const classes = useStyles(props.styles);
	
	return (
		<Grid
			container={container !== undefined ? container : false}
			item
			className={clsx(classes.FieldsetItem, noPadding && classes.noPadding, className)}
			xs={columns || 12}
			style={centered && { textAlign: 'center' }}
			{...otherProps}
			>
			
			{children}
		</Grid>
	);
}

/*
 *
 */
export function FieldsetLegend(props) {
	const classes = useStyles();
	return !props.label ? null : (
		<>
			<Typography className={clsx(classes.Legend, classes['Legend_' + (props.variant || 'h3')])} variant={props.variant || 'h3'}>{i18n.t(props.label)}</Typography>
			{props.divider !== false && <Divider />}
		</>
	);
}

/*
 *
 */
export function Form(props) {
	const { children } = props;
	
	return (
		<MuiPickersUtilsProvider utils={i18n.getLocaleDateAdapter()} locale={i18n.getLocale()}>
			{children}
		</MuiPickersUtilsProvider>
	);
}