
export default [

	// chart config
	{
		"parameter": "chartConfigyLeftAxisUnitBefore",
		"label": "config#Chart#chartConfigyLeftAxisUnitBefore#label"
	},
	{
		"parameter": "chartConfigyLeftAxisUnitAfter",
		"label": "config#Chart#chartConfigyLeftAxisUnitAfter#label"
	},
	
	// chart filter
	{
		"parameter": "chartFilterTimeRangeValue",
		"label": "config#Chart#chartFilterTimeRangeValue#label",
		"type": "Number",
		"val": 1
	},
	{
		"parameter": "chartFilterTimeRangeUnit",
		"label": "config#Chart#chartFilterTimeRangeUnit#label",
		"type": "Select",
		"selection": [
			{ "seconds": "Seconds" },
			{ "minutes": "Minutes" },
			{ "hours": "Hours" },
			{ "days": "Days" },
			{ "weeks": "Weeks" },
			{ "months": "Months" },
			{ "years": "Years" }
		],
		"val": "days"
	},
	/*{
		"parameter": "chartFilterDateTime",
		"label": "config#Chart#chartFilterDateTime#label",
		"type": "DatePicker",
		"val": new Date()
	},*/
	{
		"parameter": "liveUpdates",
		"label": "config#Chart#liveUpdates#label",
		"type": "Switch",
		"val": true
	},
	{
		"parameter": "multiplicator",
		"label": "config#Chart#multiplicator#label",
		"info": "config#Chart#multiplicator#info",
		"type": "Number",
		"step": "any",
		"val": 1,
		
		//<IGNORE>
		"onValidate": val => {
			val = Number.parseFloat(val);
			if (Number.isNaN(val)) {
				throw new Error('Incorrect number');
			}
			
			return val;
		}//</IGNORE>
	}
]