import React, { useState, useEffect } from 'react'

import i18n from '../../controller/i18n'
import Inputs from '../../components/Inputs'
import { components, componentsOptions } from '../../functions'

import Checkbox from '@material-ui/core/Checkbox'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'

import ModifyIcon from '@material-ui/icons/Build'

import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank'
import CheckBoxIcon from '@material-ui/icons/CheckBox'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	wrap: { 
		display: 'inherit'
	},
	Divider: {
		margin: '0 -16px 0 -16px'
	},
	ModifyIcon: {
		width: 24,
		height: 24,
		verticalAlign: 'bottom'
	},
	Screenshot: {
		display: 'block',
		width: '100%'
	}
}));


export default {
	devices: ['device', 'divider', 'group'],
	DeviceBodyConfiguration,
	DeviceActionsConfiguration
}

/**
 *
 */
export function getComponents(func, type = 'action', inclState = false) {
	const componentsFunction = Object.keys((components[func] && components[func][type]) || {});
	const componentsDefaults = Object.keys((components['_defaults'] && components['_defaults'][type]) || {}).filter(item => (inclState === true || (inclState === false && item !== 'State')) && item !== 'Subtitle' && componentsFunction.indexOf(item) < 0);
	return [''].concat(componentsDefaults, componentsFunction);
}

/*
 *
 */
function DeviceBodyConfiguration(props) {
	const { device, options, onChange, elements = {}, onConfig } = props;
	const classes = useStyles();
	
	const deviceStates = Object.keys(device.states || {});
	const [bodyStateKey,setBodyStateKey] = useState(options.bodyStateKey !== undefined ? options.bodyStateKey : (options.primaryStateKey || (deviceStates && deviceStates[0]) || null));
	const [bodyElement,setBodyElement] = useState(options.bodyElement !== null ? (options.bodyElement || 'LastChangeBody') : '');
	
	// save initial settings
	const mount = () => {
		if (options.type === 'device') {
			onChange({ 'id': 'bodyStateKey' }, bodyStateKey);
			onChange({ 'id': 'bodyElement' }, bodyElement);
		}
	}
	useEffect(mount, []);
	
	/*
	 * DIVIDER / GROUP CONFIGURATION
	 */
	if (options.type !== 'device') {
		return (
			<Inputs.Fieldset>
				<Inputs.FieldsetItem>
					{elements.Icon} {elements.Device}
				</Inputs.FieldsetItem>
			</Inputs.Fieldset>
		);
	}
	
	/*
	 * DEVICE CONFIGURATION
	 */
	const bodyStateKeys = deviceStates; // [''].concat(deviceStates.filter(stateKey => device.states[stateKey].action && device.states[stateKey].action !== ''));
	const bodyComponents = getComponents(device.function, 'body');
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Body'}>
			<Inputs.FieldsetItem container>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} container noPadding>
				<Inputs.Text
					key={'label-' + (device.id || options.deviceId)}
					id='label'
					width={180}
					
					placeholder={(device.states && device.states[bodyStateKey] && device.states[bodyStateKey].label) || device.label || device.name}
					val={options.label || ''}
					label='Label'
					onChange={onChange}
					/>
				
				<Inputs.Text
					key={'bodyStateKey-' + (device.id || options.deviceId)}
					id='bodyStateKey'
					width={160}
					
					autoComplete
					autoHighlight
					
					val={bodyStateKey}
					label='Body State Key'
					options={bodyStateKeys}
					onChange={(props, val) => {
						setBodyStateKey(val);
						onChange(props, val);
					}}
					/>
				
				{bodyStateKey !== null && <>
					<Inputs.Text
						//classes={{ option: classes.wrap }}
						key={'bodyElement-' + (device.id || options.deviceId)}
						id='bodyElement'
						width={200}
						
						autoComplete
						autoHighlight
						
						val={bodyElement}
						label='Body Element'
						onChange={(props, val) => {
							setBodyElement(val);
							onChange(props, val);
						}}
						options={bodyComponents}
						renderOption={(option, state) => <Screenshot func={device.function} option={option} selected={state.selected} />}
						/>
						
						{((componentsOptions['_defaults'] && componentsOptions['_defaults'][bodyElement]) || (componentsOptions[device.function] && componentsOptions[device.function][bodyElement])) && <IconButton
							id='bodyElementConfig'
							classes={{ root: classes.ModifyIcon }}
							color='primary'
							component='span'
							onClick={() => onConfig('bodyElementConfig', bodyStateKey, bodyElement)}
							>
							
							<ModifyIcon fontSize='small' />
						</IconButton>}
				</>}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}

/*
 *
 */
export function DeviceActionsConfiguration(props) {
	const { device, options, onChange, onConfig } = props;
	const classes = useStyles();
	
	// device
	const deviceStates = Object.keys(device.states || {});
	const [showState,setShowState] = useState(options.showState !== undefined ? options.showState : (options.actionType !== undefined ? (options.actionType === 'state') : true));
	const [actionStateKey,setActionStateKey] = useState(options.primaryStateKey || (deviceStates && deviceStates[0]) || null);
	const [actionElement,setActionElement] = useState(options.actionElement || null);
	
	// group
	const [groupActions,setGroupActions] = useState(options.actions !== undefined ? options.actions : false);
	const [actionsElement,setActionsElement] = useState(options.actionsElement || 'SwitchAction');
	
	// save initial settings
	const mount = () => {
		if (options.type === 'device') {
			onChange({ 'id': 'primaryStateKey' }, actionStateKey);
			onChange({ 'id': 'showState' }, showState);
			onChange({ 'id': 'actionElement' }, actionElement);
		}
	}
	useEffect(mount, []);
	
	/*
	 * DIVIDER CONFIGURATION
	 */
	if (options.type === 'divider') {
		return null;
	}
	
	/*
	 * GROUP CONFIGURATION
	 */
	else if (options.type === 'group') {
		const actionsComponents = ['SwitchAction', 'ButtonAction', 'IconButtonAction'];
		
		return (
			<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Action'}>
				<Inputs.FieldsetItem container noPadding>
					<Inputs.Switch
						id='actions'
						label='Group Actions'
						width={100}
						
						val={options.actions !== undefined ? options.actions : false}
						onChange={(props, val) => {
							onChange(props, val);
							setGroupActions(val);
						}}
						/>
					
					{groupActions && <>
						<Inputs.Text
							key={'actionsElement-' + (device.id || options.deviceId)}
							id='actionsElement'
							width={200}
							
							autoComplete
							autoHighlight
							
							val={actionsElement}
							label='Actions Element'
							onChange={(props, val) => {
								setActionsElement(val);
								onChange(props, val);
							}}
							options={actionsComponents}
							renderOption={(option, state) => <Screenshot func='_defaults' option={option} selected={state.selected} />}
							/>
					</>}
					
					{groupActions && (actionsElement === 'ButtonAction' || actionsElement === 'IconButtonAction') && <>
						<Inputs.Text
							key='actionLabelOff'
							id='actionLabelOff'
							width={70}
							
							val={options.actionLabelOff || ''}
							placeholder={i18n.t('off')}
							label='Button Off'
							onChange={onChange}
							/>
						
						<Inputs.Text
							key='actionLabelOn'
							id='actionLabelOn'
							width={70}
							
							val={options.actionLabelOn || ''}
							placeholder={i18n.t('on')}
							label='Button On'
							onChange={onChange}
							/>
					</>}
					
				</Inputs.FieldsetItem>
			</Inputs.Fieldset>
		);
	}
	
	/*
	 * DEVICE CONFIGURATION
	 */
	// Checkbox Icon
	const icon = <CheckBoxOutlineBlankIcon fontSize='small' />
	const checkedIcon = <CheckBoxIcon fontSize='small' />
	
	// Action
	//const actionStateKeys = [''].concat(deviceStates.filter(stateKey => device.states[stateKey].action && device.states[stateKey].action !== ''));
	const actionComponents = getComponents(device.function);
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Action'}>
			<Inputs.FieldsetItem container>
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} container noPadding>
				
				<Inputs.Text
					key={'primaryStateKey-' + (device.id || options.deviceId)}
					id='primaryStateKey'
					required
					width={160}
					
					autoComplete
					autoHighlight
					
					val={actionStateKey}
					label='Primary State Key'
					onChange={(props, val) => {
						setActionStateKey(val);
						onChange(props, val);
					}}
					options={[''].concat(deviceStates)}
					/>
				
				{actionStateKey !== null && <>
				<Inputs.Checkbox
					key={'showState-' + (device.id || options.deviceId)}
					id='showState'
					
					label="Show State"
					val={showState}
					onChange={(props, val) => {
						setShowState(val);
						onChange(props, val);
					}}
					/>
				
				<Inputs.Text
					//classes={{ option: classes.wrap }}
					key={'actionElement-' + (device.id || options.deviceId)}
					id='actionElement'
					width={180}
					
					autoComplete
					autoHighlight
					
					val={actionElement}
					label='Action Element'
					onChange={(props, val) => {
						setActionElement(val);
						onChange(props, val);
					}}
					options={actionComponents}
					renderOption={(option, state) => <Screenshot func={device.function} option={option} selected={state.selected} />}
					/>
					
				{((componentsOptions['_defaults'] && componentsOptions['_defaults'][actionElement]) || (componentsOptions[device.function] && componentsOptions[device.function][actionElement])) && <IconButton
					id='actionElementConfig'
					classes={{ root: classes.ModifyIcon }}
					color='primary'
					component='span'
					onClick={() => onConfig('actionElementConfig', actionStateKey, actionElement)}
					>
					
					<ModifyIcon fontSize='small' />
				</IconButton>}
				
				{options.withinGroup === true &&
				<Inputs.Checkbox
					key={'useInGroup-' + (device.id || options.deviceId)}
					id='useInGroup'
					label='Use in group trigger'
					checked={options.useInGroup !== undefined ? options.useInGroup : true}
					onChange={onChange}
					/>}
				</>}
				
				{props.secondaryStatesKeys !== false && (
				<Inputs.Text
					key={'secondaryStatesKeys-' + (device.id || options.deviceId)}
					id='secondaryStatesKeys'
					width={320}
					
					autoComplete
					autoHighlight
					multiple
					limitTags={2}
					disableCloseOnSelect
					size='small'
					
					val={options.secondaryStatesKeys || []}
					label='Secondary States Keys'
					onChange={onChange}
					options={deviceStates}
					
					renderOption={(option, { selected }) => (
						<React.Fragment>
							<Checkbox
								icon={icon}
								checkedIcon={checkedIcon}
								style={{ marginRight: 8 }}
								checked={selected}
								/>
							
							{option}
						</React.Fragment>
					)}
					/>)}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}

/**
 *
 */
export function Screenshot(props) {
	const { func, selected } = props;
	const classes = useStyles();
	
	
	// try to get screenshot of option
	let option = props.option;
	if (option) {
		
		let screenshot = null;
		try {
			screenshot = require('../../functions/' + func + '/screenshots/' + option + '.png');
		}
		catch(err) {
			try {
				screenshot = require('../../functions/_defaults/screenshots/' + option + '.png');
			}
			catch(err) {}
		}
		
		if (screenshot) {
			option = <>
				<div>{option}</div>
				<img src={screenshot} alt='' />
			</>
		}
	}
	
	// empty option
	else {
		return option;
	}
	
	option = selected === true ? <strong>{option}</strong> : option;
	return <div className={classes.Screenshot}>
		{option}
		<Divider classes={{ root: classes.Divider }} />
	</div>
}