import React, { useState, useRef, useCallback, useEffect } from 'react'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'

import { getValueAndUnit } from '../../helpers/utils'


/*
 *
 */
export default function CustomHTML(props) {
	const { id, devices } = props;
	//let config = joinConfig(props.config, require('./CustomHTML.config.js').default);
	return Devices.forEachDevice(devices, (device, index, options) => <CustomHTMLElement {...props} key={'CustomHTMLElement-' + id + '-Device-' + device.id} device={device} options={options} />);
}

/*
 *
 */
function CustomHTMLElement(props) {
	const { device, options } = props;
	
	const [html,setHTML] = useState('');
	useEffect(() => {
		
		// receive state update
		device.listen('stateChange', (stateKey, state, device) => options.primaryStateKey === stateKey && setHTML(state.val), false);
		
		// request device state
		device.requestDeviceState(options.primaryStateKey, true).catch(res => {
			Log._('Modules (CustomHTML)', res.error.message, 'error', true, res.error.stack);
		});
		
	}, [device, options]);
	
	if (html === '') {
		return null;
	}
	else if (!html) {
		return i18n.t('Incorrect HTML provided to CustomHTML module') + '!';
	}
	
	return html.toString().indexOf('<script') > -1 ? <IFrameHTMLElement {...props} html={html} /> : <DivHTMLElement {...props} html={html} />
}

/*
 *
 */
function DivHTMLElement(props) {
	const [height, setHeight] = useState('auto');
	
	// adjust height
	const htmlComponent = useCallback(el => {
		if (el !== null && props.widgetConfig.maxHeight) {
			const box = el.parentNode;
			const widget = box.parentNode;
			const widgetHeader = widget.getElementsByClassName('MuiCardHeader-root');
			
			const [ , , px ] = getValueAndUnit(props.widgetConfig.maxHeight);
			setHeight(px - (widgetHeader && widgetHeader[0] ? widgetHeader[0].offsetHeight : 0));
		}
	}, [props.widgetConfig.maxHeight]);
	
	return (
		<div
			ref={htmlComponent}
			style={{
				'width': '100%',
				'height': height,
				'overflow': 'auto', // https://forum.iobroker.net/post/585915
				'textAlign': 'left'
			}}
			dangerouslySetInnerHTML={{ __html: props.html }}
			/>
	
	);
}

/*
 *
 */
function IFrameHTMLElement(props) {
	const [height, setHeight] = useState('auto');
	const html = '<html><body style="padding: 0; margin: 0">' + props.html + '</body></html>';
	
	
	// adjust height
	const ref = useRef(null);
	const htmlComponent = useCallback(iFrame => {
		
		if (iFrame !== null) {
			const srcdoc = iFrame.contentDocument || iFrame.contentWindow.document;
			ref.current = iFrame;
			
			if (srcdoc && srcdoc.body) {
				srcdoc.body.innerHTML = html;
				setHeight(srcdoc.body.offsetHeight + 'px');
			}
			else {
				Log._('Modules (CustomHTML)', 'Could not set Custom HTML because iFrame is corrupt', 'error');
			}
		}
		
		
	}, [html]);
	
	// detect height changes
	const polling = useRef();
	useEffect(() => {
		
		const resize = () => {
			ref.current.contentDocument && ref.current.contentDocument.body && setHeight(ref.current.contentDocument.body.offsetHeight);
			polling.current = setTimeout(resize, 5000);
		}
		
		polling.current = setTimeout(resize, 700);
		return () => clearTimeout(polling.current);
		
	}, []);
	
	return (
		<iframe
			ref={htmlComponent}
			srcDoc={html}
			title="CustomHTML"
			style={{
				'width': '100%',
				'height': height,
				'overflow': 'hidden',
				'textAlign': 'left',
				'border': 0
			}}
			//sandbox="allow-modals allow-forms allow-pointer-lock allow-popups allow-same-origin allow-scripts" // An iframe which has both allow-scripts and allow-same-origin for its sandbox attribute can remove its sandboxing.
			>
		</iframe>
	);
}
