import React from 'react'
import clsx from 'clsx'

import { Line } from 'react-chartjs-2'
// eslint-disable-next-line
import chartjs_colorschemes from 'chartjs-plugin-colorschemes'
// eslint-disable-next-line
import chartjs_zoom from 'chartjs-plugin-zoom'
import DateFnsUtils from '@date-io/date-fns'

import Alert from '@material-ui/lab/Alert'
import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import CircularProgress from '@material-ui/core/CircularProgress'
import Tooltip from '@material-ui/core/Tooltip'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider'
import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers'
import InputBase from '@material-ui/core/InputBase'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import IconButton from '@material-ui/core/IconButton'
import CalendarIcon from '@material-ui/icons/Event'
import CalendarTodayIcon from '@material-ui/icons/EventAvailable'
import HistoryIcon from '@material-ui/icons/History'
import FastRewindIcon from '@material-ui/icons/FastRewind'
//import DisablePan from '@material-ui/icons/SelectAll'
//import DisableZoom from '@material-ui/icons/LocationDisabled'
import ResetZoom from '@material-ui/icons/ZoomOutMap'
import LiveUpdates from '@material-ui/icons/Update'

import helpers from '../../helpers/timeConverter'
import { joinConfig, allSettled } from '../../helpers/utils'

import Cache from '../../controller/Cache'
import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'



/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	loading: {
		position: 'absolute',
		backgroundColor: 'rgba(0, 0, 0, 0.3)',
		width: '100%',
		height: '100%',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
	},
	errorMessage: {
		marginBottom: 5,
		marginRight: 15,
		width: '100%',
		textAlign: 'left'
	},
	toolbar: {
		display: 'flex'
	},
	toolbarButtons: {
		display: 'initial',
		whiteSpace: 'nowrap'
	},
	options: {
		display: 'flex',
		justifyContent: 'center',
	},
	datePicker: {
		padding: '2px 4px',
		display: 'flex',
		alignItems: 'center',
		margin: 2
	},
	input: {
		marginLeft: theme.spacing(1),
		flex: 1,
	},
	select: {
		width: 100
	},
	icon: {
		margin: 10,
	},
	iconButton: {
		padding: 10,
	},
	divider: {
		margin: '15px 0',
	},
	dividerVertical: {
		height: 28,
		margin: 4,
	},
	button: {
		backgroundColor: theme.palette.primary.main,
		color: '#fff !important',
		margin: '4px'
	},
	buttonDisabled: {
		backgroundColor: theme.palette.background.default,
		margin: '4px'
	}
});


class Chart extends React.Component {
	
	constructor(props) {
		super(props);
		this.config = joinConfig(this.props.config, require('./Chart.config.js').default);
		
		// chart configuration
		this.chartConfig = this.config.chartConfig || {};
		
		this.chartConfig.yAxes = this.chartConfig.yAxes || [];
		this.chartConfig.yAxes.unshift({
			id: 'left',
			type: 'linear',
			position: 'left',
			ticks: {
				callback: value => (this.config.chartConfigyLeftAxisUnitBefore || '') + Number.parseFloat(value).toFixed(2) + (this.config.chartConfigyLeftAxisUnitAfter || '')
			}
		});
		
		// states
		this.state = {
			loading: true,
			errors: [],
			
			datePicker: false,
			selectedTimeRangeValue: (this.config.chartFilterTimeRangeValue) || 1,
			selectedTimeRangeUnit: (this.config.chartFilterTimeRangeUnit) || 'days',
			selectedDateTime: (this.config.chartFilterDateTime && new Date(this.config.chartFilterDateTime)) || new Date(),
			dateNow: this.config.chartFilterDateTime ? false : true,
			
			chartLabels: [],
			chartData: {
				labels: [],
				datasets: []
			},
			
			chartPan: true,
			chartZoom: true
		}
		
		//
		this.historyEnabledStates = []
		this.historyAdapterByState = {}
		this.historyChangeCallbacks = {}
		this.timer = null;
		this.timeRangeRef = React.createRef();
		
		// chart instance options
		this.chartRef = React.createRef();
		this.chartAxisOptions = {};
		
		this.chartPluginOptions = {
			maintainAspectRatio: false,
			spanGaps: true,
			legend: {
				onClick: this.legendClickHandler.bind(this)
			},
			tooltips: {
				callbacks: {
					label: (item, data) => data.datasets[item.datasetIndex].label + ': ' + item.value + data.datasets[item.datasetIndex].unit
				},
			},
			scales: {
				yAxes: this.chartConfig.yAxes
			},

			plugins: {
				colorschemes: {
					scheme: 'brewer.SetOne9'
				},
				zoom: {
					// Container for pan options
					pan: {
						// Boolean to enable panning
						enabled: true,

						// Panning directions
						mode: 'xy'
					},
					
					// Container for zoom options
					zoom: {
						// Boolean to enable zooming
						enabled: true,

						// Zooming directions.
						mode: 'xy'
					}
				}
			}
		};
	}
	
	componentDidMount() {
		
		// get history adapters
		const historyAdapters = Cache.get('adapterList').filter(adapter => {
			return ['history', 'influxdb', 'sql'].indexOf(adapter.substr(0, adapter.indexOf('.'))) > -1;
		});
		
		// get history enabled states
		const socket = Connection.connection;
		allSettled(
			historyAdapters.map(adapter => new Promise((resolve, reject) => socket.sendTo('getEnabledDPs', null, adapter, (err, states) => err ? reject(err) : resolve({ 'states': states, 'adapter': adapter }))))
		)
		.then(adapters => {
			Log._('Modules (Chart)', [ i18n.t('Received list of historicized states'), adapters, this.historyEnabledStates ], 'development', true);
			adapters.forEach(res => {
				
				if (res.value) {
					const states = Object.keys(res.value.states);
					this.historyEnabledStates = [ ...this.historyEnabledStates, ...states ];
					states.forEach(state => {
						this.historyAdapterByState[state] = res.value.adapter;
					});
				}
			});
			
			// request device history
			this._updateDeviceHistory(true);
		});
	}
	
	/*
	 *
	 */
	componentWillUnmount() {
		
		if (Object.keys(this.historyChangeCallbacks).length > 0) {
			Devices.forEachDevice(this.props.devices, (device, index, options) => {
				device.unlisten('historyChange', this.historyChangeCallbacks[index]);
			});
		}
		
		clearTimeout(this.timer);
	}
	
	/*
	 *
	 */
	componentDidUpdate(prevProps, prevState) {
		
		if (this.state.selectedTimeRangeValue &&
				(prevState.selectedDateTime !== this.state.selectedDateTime || prevState.selectedTimeRangeValue !== this.state.selectedTimeRangeValue || prevState.selectedTimeRangeUnit !== this.state.selectedTimeRangeUnit)
			) {
			
			// request updated history data
			this.setState({ loading: true, errors: [] });
			
			clearTimeout(this.timer);
			this.timer = setTimeout(() => this._updateDeviceHistory(), 500);
		}
	}
	
	/**
	 *
	 */
	_historyChange = (device, stateKey, history, options) => {
		
		if (this.props.devices.findIndex(d => d.deviceId === device.id && d.primaryStateKey === stateKey) === options.index) {
			this._addChartGraph(options.label ? options.label : (device.name + ' (' + i18n.t(stateKey) + ')'), device, options, history, this._getAxis(this.chartAxisOptions.start, this.chartAxisOptions.end));
			this.setState({ loading: false });
		}
	}
	
	/**
	 *
	 */
	_updateDeviceHistory(listenForUpdates = false) {
		
		const chartAxisOptions = this._chartAxisOptions();
		Devices.forEachDevice(this.props.devices, (device, index, options) => {
			const deviceState = device.states[options.primaryStateKey || device.primaryStateKey];
			const state = deviceState && deviceState.state && typeof deviceState.state === 'object' ? deviceState.state.node : deviceState.state;
			
			// attach history listener
			new Promise((resolve, reject) => {
				
				if (device._isDevice === true && state) {
					
					// request history
					if (this.historyEnabledStates.indexOf(state) > -1) {
						if (!this.historyChangeCallbacks[index]) {
							this.historyChangeCallbacks[index] = (stateKey, history, device) => this._historyChange(device, stateKey, history, { ...options, 'index': index });
							device.listen('historyChange', this.historyChangeCallbacks[index], false);
						}
						
						
						chartAxisOptions.instance = this.historyAdapterByState[state];
						device.requestDeviceStateHistory(options.primaryStateKey || device.primaryStateKey, chartAxisOptions)
							.catch(({ error }) => {
								Log._('Modules (Chart)', error.message, 'error', true, error.stack);
								reject(i18n.t(error.message));
							});
					}
					else {
						reject(i18n.t('State %s not enabled in history adapter').replace('%s', state || options.primaryStateKey || device.primaryStateKey) + '!');
					}
				}
				else {
					reject(i18n.t('State %s not part of a valid device').replace('%s', state || options.primaryStateKey || device.primaryStateKey) + '!');
				}
			})
			
				// error
				.catch(errMessage => {
					if (errMessage && this.state.errors.indexOf(errMessage) === -1) {
						this.setState(prevStates => ({
							loading: false,
							errors: [ ...prevStates.errors, errMessage ]
						}));
					}
				});
		});
		
		if (listenForUpdates === true) {
			clearTimeout(this.timer);
			this.timer = setTimeout(() => this._updateDeviceHistory(true), chartAxisOptions.axisStep);
		}
	}
	
	/**
	 *
	 */
	_chartAxisOptions() {
		
		const end = this.state.selectedDateTime.getTime();
		const unit = this.state.selectedTimeRangeUnit;
		
		let timeRange = this.state.selectedTimeRangeValue;
		let step = 1;
		let axisTimeFormat = 'dd. MMM yyyy, HH:mm';
		
		switch(unit) {
			default:
				break;
			
			case 'seconds':
				axisTimeFormat = 'dd. MMM yyyy, HH:mm:ss';
				break;
				
			case 'minutes':
				axisTimeFormat = 'dd. MMM yyyy, HH:mm:ss';
				timeRange = timeRange*60;
				//step = step;
				break;
				
			case 'hours':
				axisTimeFormat = 'dd. MMM yyyy, HH:mm';
				timeRange = timeRange*60*60;
				step = step*60; // minute-by-minute step
				break;
				
			case 'days':
				axisTimeFormat = 'dd. MMM yyyy, HH:mm';
				timeRange = timeRange*60*60*24;
				step = step*60*60; // hourly step
				break;
				
			case 'weeks':
				axisTimeFormat = 'dd. MMM yyyy';
				timeRange = timeRange*60*60*24*7;
				step = step*60*60*24; // daily step
				break;
				
			case 'months':
				axisTimeFormat = 'dd. MMM yyyy';
				timeRange = timeRange*60*60*24*31;
				step = step*60*60*24; // daily step
				break;
				
			case 'years':
				axisTimeFormat = 'dd. MMM yyyy';
				timeRange = timeRange*60*60*24*365;
				step = step*60*60*24*31; // monthly step
				break;
		}
		
		this.chartAxisOptions = {
			'axisTimeFormat': axisTimeFormat,
			
			'start': end-timeRange*1000,
			'end': end,
			'axisStep': step*1000,
			'count': 999999
		};
		
		this.chartAxisOptions.cache = Object.values(this.chartAxisOptions).join('#');
		return this.chartAxisOptions;
	}
	
	/**
	 *
	 */
	_getAxis(start, end) {
		
		let axis = {};
		
		for (let time = start; time <= end; time += this.chartAxisOptions.axisStep) {
			axis[time] = helpers.secondsToDateTime(time/1000, this.chartAxisOptions.axisTimeFormat);
		}
		
		return {
			axisTimeFormat: this.chartAxisOptions.axisTimeFormat,
			axis
		}
	}
	
	/**
	 *
	 */
	_groupChartData(chartData, axis, deviceOptions = {}) {
		this.config.multiplicator = this.config.multiplicator ? parseFloat(this.config.multiplicator) : 1;
		
		let timestamps = Object.keys(axis);
		let prevTimestamp;
		let data = [];
		
		timestamps.forEach((ts, i) => {
			prevTimestamp = timestamps[(i-1 > 0 ? i-1 : 0)];
			ts = parseInt(ts);
			
			for (let entry of chartData) {
				entry.ts = parseInt(entry.ts);
				let val = (typeof entry.val === 'boolean' ? (entry.val === true ? 1 : 0) : parseFloat(entry.val)) * this.config.multiplicator;
				
				// no timestamp or no data
				if (!entry.ts || val === null) {
					continue;
				}
				
				// ack
				entry.ack = entry.ack !== undefined ? entry.ack : null;
				if (deviceOptions.ack !== undefined && deviceOptions.ack !== 'both' && entry.ack === null) {
					Log._('Modules (Chart)', [ i18n.t('Ack value is not given, thus comparision will be skipped') + '.', i18n.t('See debug log for more details') + '.' ], 'warn', true);
					console.debug(entry, deviceOptions);
				}
				else if (deviceOptions.ack !== undefined && deviceOptions.ack !== 'both' && (deviceOptions.ack !== entry.ack.toString())) {
					continue;
				}
				
				// before range, continue
				if (prevTimestamp > entry.ts) {
					continue;
				}
				
				// in range
				else if (prevTimestamp <= entry.ts && entry.ts <= ts) {
					data[i] = !data[i] ? [ val ] : [ ...data[i], val ]; // parseFloat((data[i] ? ((data[i]+entry.val)/2) : entry.val).toFixed(2));
				}
				
				// beyond range, so next marker
				else if (entry.ts > ts) {
					break;
				}
			}
			
			//
			if (!data[i]) {
				// 
			}
			else if (data[i].length === 1) {
				data[i] = data[i][0];
			}
			else {
				data[i] = (data[i].reduce((a,b) => a + b, 0) / data[i].length).toFixed(2);
			}
		});
		
		return data;
	}
	
	/**
	 *
	 */
	_addChartGraph(label, device, deviceOptions, history, chartOptions) {
		const chartIndex = this.state.chartLabels.findIndex(entry => entry === label);
		
		// only add chart if data is given (and not yet added)
		if (chartIndex === -1 && (!history || !history.length)) {
			const errorMessage = i18n.t('No history data given for %s').replace('%s', label) + '!';
			
			if (this.state.errors.indexOf(errorMessage) === -1) {
				this.setState(prevStates => ({ errors: [ ...prevStates.errors, errorMessage ] }));
			}
			
			return false;
		}
		
		// process data
		const data = this._groupChartData(history, chartOptions.axis, deviceOptions);
		data.splice(0, 1);
		
		// axis
		let axis = Object.values(chartOptions.axis);
		axis.splice(0, 1);
		
		// update chart
		if (chartIndex > -1) {
			this.chartRef.current.chartInstance.data.labels = axis;
			this.chartRef.current.chartInstance.data.datasets[chartIndex].data = data;
			this.chartRef.current.chartInstance.update();
		}
		
		// index chart
		else {
			this.setState(prevStates => ({ chartLabels: [ ...prevStates.chartLabels, label ] }));
			
			// chart dataset
			const color = !deviceOptions.color || deviceOptions.color === 'auto' ? undefined : deviceOptions.color;
			let dataSet = {
				label: label,
				unit: device.states[deviceOptions.primaryStateKey || device.primaryStateKey].state.value.unit || '',
				
				fill: false,
				borderColor: color,
				pointBorderColor: color,
				pointBackgroundColor: color,
				...deviceOptions.graphConfig || {},
				
				data: data
			};
			
			// add data to chart
			this.setState(prevStates => ({
				axisTimeFormat: chartOptions.axisTimeFormat,
				chartData: {
					labels: axis,
					datasets: [ ...prevStates.chartData.datasets, dataSet ],
				}
			}));
		}
	}
	
	/**
	 *
	 */
	legendClickHandler(e, legendItem) {
		
		let index = legendItem.datasetIndex;
		let ci = this.chartRef.current.chartInstance;
		let meta = ci.getDatasetMeta(index);

		meta.hidden = meta.hidden === null ? !ci.data.datasets[index].hidden : null;
		ci.update();
		ci.resetZoom();
	}
	
	/**
	 *
	 */
	disablePan() {
		let pan = this.chartRef.current.chartInstance.options.plugins.zoom.pan.enabled;
		this.chartRef.current.chartInstance.options.plugins.zoom.pan.enabled = (pan === false);
		this.setState({ chartPan: (pan === false) });
	}
	
	/**
	 *
	 */
	disableZoom() {
		let zoom = this.chartRef.current.chartInstance.options.plugins.zoom.zoom.enabled;
		this.chartRef.current.chartInstance.options.plugins.zoom.zoom.enabled = (zoom === false);
		this.setState({ chartZoom: (zoom === false) });
	}
	
	/*
	 *
	 */
	render() {
		const { classes, config } = this.props;
		
		return (

<div style={{ width: '100%' }}>
	<div className={classes.options}>
	
		<Paper className={classes.datePicker}>
			<Tooltip title={i18n.t('Period shown in Chart')}>
				<HistoryIcon className={classes.icon} />
			</Tooltip>
			
			<InputBase
				inputRef={this.timeRangeRef}
				className={classes.input}
				value={this.state.selectedTimeRangeValue}
				onChange={event => this.setState({ selectedTimeRangeValue: event.target.value })}
				/>
			
			<Select
				value={this.state.selectedTimeRangeUnit}
				onChange={event => this.setState({ selectedTimeRangeUnit: event.target.value })}
				displayEmpty
				disableUnderline
				className={classes.select}
				>
				
				<MenuItem value="seconds">{i18n.t('Seconds')}</MenuItem>
				<MenuItem value="minutes">{i18n.t('Minutes')}</MenuItem>
				<MenuItem value="hours">{i18n.t('Hours')}</MenuItem>
				<MenuItem value="days">{i18n.t('Days')}</MenuItem>
				<MenuItem value="weeks">{i18n.t('Weeks')}</MenuItem>
				<MenuItem value="months">{i18n.t('Months')}</MenuItem>
				<MenuItem value="years">{i18n.t('Years')}</MenuItem>
			</Select>
			
			{/*
			<IconButton className={classes.iconButton} onClick={() => this.timeRangeRef.current.focus()}>
				<CalendarIcon />
			</IconButton>
			*/}
		</Paper>

		<Paper className={classes.datePicker}>
			<Tooltip title={i18n.t('End date')}>
				<FastRewindIcon className={classes.icon} />
			</Tooltip>
			
			<MuiPickersUtilsProvider locale={i18n.getLocale()} utils={DateFnsUtils}>
				<DateTimePicker
					className={classes.input}
					value={this.state.selectedDateTime}
					onChange={selectedDateTime => this.setState({ selectedDateTime: selectedDateTime, dateNow: false })}
					
					open={this.state.datePicker}
					onOpen={() => this.setState({ datePicker: true })}
					onClose={() => this.setState({ datePicker: false })}
					
					variant="inline"
					ampm={false}
					format='dd.MM.yyyy HH:mm'
					InputProps={{
						disableUnderline: true,
					}}
					/>
					
			</MuiPickersUtilsProvider>
			
			<Tooltip title={i18n.t('Select end date')}>
				<IconButton color="primary" className={classes.iconButton} onClick={() => this.setState({ datePicker: true })}>
					<CalendarIcon />
				</IconButton>
			</Tooltip>
			<Divider className={classes.dividerVertical} orientation="vertical" />
			<Tooltip title={i18n.t('Select today as end date')}>
				<IconButton className={classes.iconButton} onClick={() => this.setState({ selectedDateTime: new Date(), dateNow: true })}>
					<CalendarTodayIcon />
				</IconButton>
			</Tooltip>
		</Paper>
		
		<Tooltip title={i18n.t('Live updates are %onOff').replace('%onOff', this.config.liveUpdates !== false && this.state.dateNow)} placement="top" style={{ display: 'flex' }}>
			<span>
				<Button disabled={true} className={clsx(classes.datePicker, this.config.liveUpdates !== false && this.state.dateNow ? classes.button : classes.buttonDisabled)}>
					<LiveUpdates />
				</Button>
			</span>
		</Tooltip>
		
	</div>
	<Divider className={classes.divider} />
	
	<div className={classes.toolbar}>
		<div className={classes.errorMessage}>
			{this.state.errors.length > 0 && <Alert severity="error">{this.state.errors.map((error, i) => [ error, React.createElement('br', { key: 'br-error-' + i }) ])}</Alert>}
		</div>
		
		<ButtonGroup className={classes.toolbarButtons} color="primary">
		{/*
			<Button disableElevation={true} variant={this.state.chartPan ? 'contained' : 'outlined'} onClick={this.disablePan.bind(this)}>
				<Tooltip title={i18n.t(this.state.chartPan ? 'Disable panning' : 'Enable panning')}>
					<DisablePan />
				</Tooltip>
			</Button>
			<Button disableElevation={true} variant={this.state.chartZoom ? 'contained' : 'outlined'} onClick={this.disableZoom.bind(this)}>
				<Tooltip title={i18n.t(this.state.chartZoom ? 'Disable zoom' : 'Enable zoom')}>
					<DisableZoom />
				</Tooltip>
			</Button>
		*/}
			<Button disableElevation={true} variant="contained" onClick={() => this.chartRef.current.chartInstance.resetZoom()}>
				<Tooltip title={i18n.t('Reset Zoom')}>
					<ResetZoom />
				</Tooltip>
			</Button>
		</ButtonGroup>
	</div>
	
	<div className={'chart-container'} style={{ position: 'relative', height: config.chartHeight || '500px' }}>
		{this.state.loading && <div className={classes.loading}>
			<CircularProgress />
		</div>}
		
		<Line
			ref={this.chartRef}
			data={this.state.chartData}
			options={this.chartPluginOptions}
			/>
			
	</div>
</div>

		);
	}
}

export default withStyles(styles)(Chart);
