import Cache from '../../controller/Cache'

export default [
	{
		"parameter": "adapter",
		"label": "config#MediaControl#adapter#label",
		"required": true,
		"columns": 2,
		"type": "Select",
		//<IGNORE>
		"selection": () => {
			const adapterList = Cache.get('adapterList') || [];
			return adapterList.filter(adapter => adapter.indexOf('spotify-premium') > -1 || adapter.indexOf('plex') > -1 || adapter.indexOf('alexa2') > -1 || adapter.indexOf('yamaha') > -1 || adapter.indexOf('musiccast') > -1).map(adapter => {
				const adapterId = adapter.toLowerCase().replace(/ /g, '');
				
				return {
					[adapterId]: adapter,
					"_translated": true
				}
			});
		},//</IGNORE>
		"val": null
	},
	{
		"parameter": "coverMode",
		"label": "config#MediaControl#coverMode#label",
		"description": "config#MediaControl#coverMode#info",
		"columns": 2,
		"type": "Switch",
		"val": false
	},
	{
		"parameter": "coverHeight",
		"label": "config#MediaControl#coverHeight#label",
		"columns": 2,
		"type": "Number",
		"val": 200
	},
	{
		"parameter": "coverUrl",
		"label": "config#MediaControl#coverUrl#label",
		"description": "config#MediaControl#coverUrl#info",
		"columns": 2,
		"val": ""
	}
]
