import React from 'react'

import CircularProgress from '@material-ui/core/CircularProgress'
import Grid from '@material-ui/core/Grid'
import Skeleton from '@material-ui/lab/Skeleton'
import Typography from '@material-ui/core/Typography'

import config from './config/config'
import { random, allSettled } from './helpers/utils'

import ErrorPage from './pages/ErrorPage'
import Columns from './components/Columns'
import Status from './components/Status'

import Cache from './controller/Cache'
import Connection from './controller/Connection'
import Devices from './controller/Devices'
import i18n from './controller/i18n'
import Log from './controller/Log'
import Notifications from './controller/Notifications'
import Settings from './controller/Settings'
import Jarvis from './Jarvis'


/*
 * STYLES
 */
import { ThemeProvider, withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	loading: {
		margin: '10px 0 0 0',
		color: '#999'
	},
	gridItem: {
		margin: theme.spacing(1, 1, 0, 0),
		[theme.breakpoints.down('md')]: {
			marginRight: theme.spacing(0.4, 0.4, 0, 0)
		}
	}
});


class App extends React.Component {
	
    constructor(props) {
		super(props);
		
		this.state = {
			loaded: [],
			status: Date.now(),
			
			connected: false,
			error: false,
			errorMessage: '',
			failed: null,
			
			layout: null,
			theme: null
		}
		
		this.url = null;
		this.socket = null;
		this.reloadTimer = null;
		this.configDataUpdater = null;
		this.usageDataCollection = [];
		this.unmounted = false;
		
		this.devices = null;
		this.layout = null;
	}

	static getDerivedStateFromError(error) {
		return {
			failed: {
				'icon': 'mdi-robot-dead',
				'title': i18n.t('jarvis crashed due to an error'),
				'message': i18n.t('Please screenshot the entire page and report to the developer (open a Github issue or post in the ioBroker forum)'),
				'error': error
			}
		}
	}
	
	componentDidCatch(error, errorInfo) { 
		console.error(error, errorInfo);
	}

	componentWillUnmount() {
		Log._('App', 'RENDER componentWillUnmount()', 'development');
		this.unmounted = true;
		
		if (this.socket) {
			Log._('Connection', i18n.t('Connection closed') + '!');
			
			this.socket.disconnect();
			this.socket = null;
			clearTimeout(this.reloadTimer);
		}
		
		if (this.configDataUpdater !== null) {
			clearTimeout(this.configDataUpdater);
			this.configDataUpdater = null;
		}
	}
	
	componentDidMount() {
		Log._('App', 'RENDER componentDidMount()', 'development');
		const params = Settings.setParamsFromUrl(window.location.href) || Cache.get('params') || {};
		
		/*
		 * load function language file
		 */
		try {
			i18n.setTranslations(language => require('./i18n/' + language));
		}
		catch(err) {
			//console.error(err);
		}
		
		
		/*
		 * show jarvis version
		 */
		const jarvisInfo = require('../package.json');
		Log._('App', i18n.t('Welcome to jarvis {jarvis.version}').replace('{jarvis.version}', jarvisInfo.version));
		Log._('App', i18n.t('You are on instance {jarvis.instance}').replace('{jarvis.instance}', config.get('ADAPTER_INSTANCE')));
		Cache.set('jarvis.version', jarvisInfo.version);
		
		
		/*
		 * connect and get connection
		 */
		let urls = [];
		
		// overwrite URL from parameters
		if (params.socketPort !== undefined) {
			urls.push((window.location.protocol.indexOf('https:') > -1 || params.socketSecure === 'true' ? 'https://' : 'http://') + (params.socketHost || window.location.hostname) + ':' + (params.socketPort || window.location.port));
			Log._('Connection', i18n.t('Connection URL (%url) found in cache').replace('%url', urls[1]) + '.');
		}
		
		// add more possibilities
		urls.push(window.location.origin);
		urls.push('http://' + (params.socketHost || window.location.hostname) + ':8084'); // default socket-io adapter port (http)
		urls.push('https://' + (params.socketHost || window.location.hostname) + ':8084'); // default socket-io adapter port (https)
		urls.push('http://' + (params.socketHost || window.location.hostname) + ':8082'); // default web adapter port (http)
		urls.push('https://' + (params.socketHost || window.location.hostname) + ':8082'); // default web adapter port (https)
		
		Connection.discover(urls)
			.then(connection => {
				
				// connection failed
				if (connection === false) {
					throw urls;
				}
				
				// connected
				this.socket = connection.socket;
				this.url = connection.url;
				this.setState({ connected: true });
				
				// reconnect
				const reconnect = message => {
					Log._('Connection', i18n.t(message), 'warn');
					this.setState({ connected: false });
					
					// reconnect socket on error or disconnection
					if (this.unmounted === false) {
						Log._('Connection', i18n.t('Reconnecting') + '..');
						Connection.reconnect();
					}
				}
				
				// listeners
				this.socket.on('connect', () => {
					this.setState({ connected: true, error: false, errorMessage: '' });
					clearTimeout(this.reloadTimer);
				});
				
				this.socket.on('reconnect', () => reconnect('Reconnecting'));
				this.socket.on('disconnect', reconnect);
				this.socket.on('error', error => reconnect(error.message));
				
				/*
				 * retrieve settings
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getState(config.get('NODE_SETTINGS'))
						.then(state => {
							let settings = null;
							
							try {
								settings = JSON.parse(state.val || '{}');
								Settings.setSettings(settings).applySettings();
								
								if (this.state.loaded.includes('settings') || this.state.loaded.includes('styles')) {
									this.getTheme();
								}
								
								this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'settings' ] }));
								resolve(true);
							}
							catch(err) {
								Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
								resolve(err);
								
								this.setState(prevState => ({
									loaded: [ ...prevState.loaded, 'settings' ],
									error: true,
									errorMessage: i18n.t('Error parsing settings') + '!'
								}));
							}
						})
						.catch(err => {
							Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
							resolve(err);
							
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'settings' ],
								error: true,
								errorMessage: typeof err === 'string' ? i18n.t(err) : i18n.t(err.message)
							}));
						});
				}));
				
				
				/*
				 * retrieve layout
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getState(config.get('NODE_LAYOUT'))
						.then(state => {
							this.layout = JSON.parse(state.val || '[]');
							Settings.setLayout(this.layout);
							resolve(true);
							
							if (this.layout.length === 0) {
								this.setState(prevState => ({
									loaded: [ ...prevState.loaded, 'layout' ],
									error: true,
									errorMessage: i18n.t('Please create a configuration. See documentation.')
								}));
							}
							else {
								this.setState(prevState => ({
									layout: this.layout,
									loaded: [ ...prevState.loaded, 'layout' ]
								}));
							}
						})
						.catch(err => {
							Log._('App', 'GET_LAYOUT: ' + err.message, 'error');
							resolve(err);
							
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'layout' ],
								error: true,
								errorMessage: typeof err === 'string' ? i18n.t(err) : i18n.t(err.message)
							}));
						});
				}));
				
				
				/*
				 * retrieve styles
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getState(config.get('NODE_STYLES'))
						.then(state => {
							Cache.set('styles', (state && state.val) || '');
							
							if (this.state.loaded.includes('settings') || this.state.loaded.includes('styles')) {
								this.getTheme();
							}
							
							this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'styles' ] }));
							resolve(true);
						})
						.catch(err => {
							Log._('App', 'GET_STYLES: ' + err.message, 'error');
							resolve(err);
							
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'styles' ]
							}));
						});
				}));
				
				
				/*
				 * retrieve devices
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getState(config.get('NODE_DEVICES'))
						.then(state => {
							
							try {
								this.devices = JSON.parse(state.val || '{}');
								resolve(true);
								
								Devices.setDevices(this.devices).then(res => {
									Log._('App', [ 'Processed ' + res.processedDevices + ' devices and discarded ' + (res.processedDevices - res.addedDevices) + ' devices due to incorrect configuration. See browser console log for more details: ', res ]);
									this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'devices' ] }));
								});
							}
							catch(err) {
								Log._('App', 'GET_DEVICES: ' + err.message, 'error');
								resolve(err);
								
								this.setState(prevState => ({
									loaded: [ ...prevState.loaded, 'devices' ],
									error: true,
									errorMessage: i18n.t('Error parsing devices') + '!'
								}));
							}
						})
						.catch(err => {
							Log._('App', 'GET_DEVICES: ' + err.message, 'error');
							resolve(err);
							
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'devices' ],
								error: true,
								errorMessage: i18n.t('Socket failed while getting devices') + '!'
							}));
						});
				}));
				
				
				/*
				 * retrieve notifications
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					const NotificationsClass = new Notifications();
					NotificationsClass.subscribe();
					NotificationsClass.retrieve().then(resolve).catch(err => {});
				}));
				
				
				/*
				 * retrieve adapter list config
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getAdapterInstances((err, data) => {
						const instances = (data && data.rows) || [];
						
						Cache.set('adapters', instances, false);
						Cache.set('adapterList', instances.map(row => row && row.id.replace('system.adapter.', '')), false);
						
						resolve(true);
						this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'adapters' ] }));
					});
				}));
				
				
				/*
				 * retrieve ioBroker config
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getObject('system.config', (err, data) => {
						
						if (!err && data && data.common) {
							Settings.setSettings({ iobroker: data.common });
							resolve(true);
						}
					});
				}));
				
				
				/*
				 * retrieve web config
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getObject('system.adapter.web.0', (err, data) => {
						
						if (!err && data && data.native) {
							Cache.set('web', data.native);
							resolve(true);
						}
					});
				}));
				
				
				/*
				 * retrieve socket-io config
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getObject('system.adapter.socketio.0', (err, data) => {
						
						if (!err && data && data.native) {
							Cache.set('socketio', data.native);
							resolve(true);
						}
					});
				}));
				
				
				/*
				 * retrieve iot config
				 */
				this.usageDataCollection.push(new Promise(resolve => {
					this.socket.getObject('system.adapter.iot.0', (err, data) => {
						
						if (!err && data && data.native && data.native.login) {
							const _shajs = require('sha.js');
							Cache.set('identifier', _shajs('sha256').update(data.native.login).digest('hex'));
							Cache.set('email', data.native.login);
							
							resolve(true);
						}
					});
				}));
				
				
				/*
				 * send usage data
				 */
				allSettled(this.usageDataCollection).then(() => {
					
					// subscribe to layout changes
					this.socket.subscribeStates([config.get('NODE_DEVICES'), config.get('NODE_LAYOUT'), config.get('NODE_SETTINGS'), config.get('NODE_STYLES')], (id, state) => {
						const val = state && state.val;
						
						try {
							// devices
							if (val && id === config.get('NODE_DEVICES')) {
								const devices = JSON.parse(state.val || '{}');
								Devices.setDevices(devices).then(res => {
									Log._('App', [ 'Processed ' + res.processedDevices + ' devices and discarded ' + (res.processedDevices - res.addedDevices) + ' devices due to incorrect configuration. See browser console log for more details: ', res ]);
								});
							}
							
							// layout
							else if (val && id === config.get('NODE_LAYOUT')) {
								const layout = JSON.parse(state.val || '[]');
								Settings.setLayout(layout, false);
							}
							
							// settings
							else if (val && id === config.get('NODE_SETTINGS')) {
								const settings = JSON.parse(state.val || '{}');
								Settings.setSettings(settings).applySettings();
								this.getTheme();
							}
							
							// styles
							else if (val && id === config.get('NODE_STYLES')) {
								Cache.set('styles', (state && state.val) || '');
								this.getTheme();
							}
							
							// update
							this.setState(prevStates => ({ status: Date.now() }));
						}
						catch(err) {
							Log._('App', 'UPDATE_' + id.substr(id.lastIndexOf('.')+1).toUpperCase() + ': ' + err.message, 'error');
							
							this.setState(prevState => ({
								error: true,
								errorMessage: i18n.t('Socket failed while getting ' + id.substr(id.lastIndexOf('.')+1)) + '!'
							}));
						}
					});
					
					// sent usage data
					this.configDataUpdate();
				});
			})
			.catch(urls => {
				this.url = urls.join(', ');
				
				this.setState({
					connected: false,
					failed: {
						'icon': 'mdi-lan-disconnect',
						'title': i18n.t('Connection failed'),
						'mesage': i18n.t('Could not connect to {urls}').replace('{urls}', this.url)
					}
				});
			});
    }
	
	/**
	 *
	 */
	getTheme() {
		import('./controller/theme').then(theme => {
			this.setState(prevState => ({
				loaded: [...new Set([ ...prevState.loaded, 'theme' ])],
				theme: theme.default()
			}));
		});
	}
	
	/**
	 *
	 */
	configDataUpdate() {
		const lastUpdate = Cache.get('lastUsageDataSent');
		const identifier = Cache.get('identifier');
		
		if (identifier && Settings.getSetting('sendUsageData') !== false && (!lastUpdate || lastUpdate < (Date.now()-(24*60*60*1000)))) { // update daily
			Cache.set('lastUsageDataSent', Date.now());
			
			// get browser information
			Settings.getBrowserDetails();
			
			// set data
			Settings.sendData({
				'identifier': identifier,
				'configData': {
					'version': Cache.get('jarvis.version'),
					'web': Cache.get('web'),
					'socketio': Cache.get('socketio'),
					'devices': Devices.getDevices({ raw: true, copy: true }),
					'settings': Settings.getSettings(),
					'layout': Settings.getLayout(),
					'browser': Cache.get('browser')
				}
			});
		}
		
		this.configDataUpdater = setTimeout(this.configDataUpdate, 24*60*60*1000);
	}
	
	/*
	 *
	 */
	renderError(error) {
		Log._('App', 'RENDER renderError()', 'development');
		
		error = error || this.state.errorMessage;
		Log._('App', 'Render Error: ' + error, 'debug');
		
		return (
			<Status
				key="StatusSnackbar"
				variant="error"
				message={error.toString()}
				/>
		);
	}
	
	/*
	 *
	 */
	renderLoadingProgress() {
		Log._('App', 'RENDER renderLoadingProgress()', 'development');
		
		const { classes } = this.props;
		return (
			<Grid
				key="loadingProcess"
				container
				spacing={0}
				direction="column"
				justify="center"
				alignItems="center"
				style={{ minHeight: "100vh" }}
				>

				<CircularProgress />
				<Typography className={classes.loading}>{i18n.t('Retrieving settings')}...</Typography>
			</Grid>
		);
	}
	
	/*
	 *
	 */
	renderLoadingSkeleton(props) {
		Log._('App', 'RENDER renderLoadingSkeleton()', 'development');
		
		const { classes } = this.props;
		const { topBar, tabBar, gridColumns } = props;
		
		const SkeletonLoader = (props) => <Skeleton animation="wave" variant="rect" className={classes.gridItem} height={props.height} />;
		
		let gridContents = {}
		for (let column = 1; column <= gridColumns; column++) {
			gridContents[column] = gridContents[column] || [];
			
			let loaders = random(2,5);
			for (let loader = 1; loader <= loaders; loader++) {
				gridContents[column].push(<SkeletonLoader key={column + '#' + loader} height={random(100,400)} />);
			}
		}
		
		return (
			<React.Fragment>
				{topBar && <Skeleton animation="wave" variant="rect" height={64} />}
				{tabBar && <Skeleton animation="wave" variant="rect" height={48} />}
				<Columns key="loadingSkeleton" contents={gridContents} />
			</React.Fragment>
		);
	}
	
	/*
	 *
	 */
    render() {
		Log._('App', 'RENDER render()', 'development');
		
		// error message
		const error = this.state.error ? this.renderError() : null;
		
		/*
		 * Connection failed, so only return error
		 */
		if (this.state.failed !== null) {
			return (
				<ErrorPage {...this.state.failed} />
			);
		}
		
		/*
		 * Show LoadingProgress as long as layout is unloaded
		 */
		else if (this.state.loaded.indexOf('layout') === -1 || this.state.loaded.indexOf('theme') === -1) {
			return this.renderLoadingProgress();
		}
		 
		/*
		 * Show LoadingSkeleton when layout is loaded but anything else is unloaded
		 */
		else if (this.state.loaded.indexOf('layout') !== -1 && (this.state.loaded.indexOf('styles') === -1 || this.state.loaded.indexOf('adapters') === -1 || this.state.loaded.indexOf('settings') === -1 || this.state.loaded.indexOf('theme') === -1 || this.state.loaded.indexOf('devices') === -1)) {
			return !this.state.layout || this.state.layout.length === 0 ? null : this.renderLoadingSkeleton({ topBar: true, tabBar: this.state.layout && this.state.layout[0] && this.state.layout[0].tabs.length > 1, gridColumns: this.state.layout && this.state.layout[0] && this.state.layout[0].tabs && this.state.layout[0].tabs[0] && this.state.layout[0].tabs[0].columns && this.state.layout[0].tabs[0].columns.length });
		}
		
		/*
		 * Show App
		 */
		Log._('App', [ 'Connected:', this.state.connected ], 'debug');
		
		// layout defined already
		if (this.devices && Object.keys(this.devices).length > 0 && this.layout && this.layout[0] && this.layout[0].tabs.length > 0) {
			Settings.set('joyrideIntroduction', true);
		}
		
		return (
			<ThemeProvider theme={this.state.theme}>
				{error}
				<Jarvis key="jarvis" connectionStatus={this.state.connected} />
			</ThemeProvider>
		)
	}
}

export default withStyles(styles)(App);
