import React, { useCallback, useState, useEffect } from 'react'
import clsx from 'clsx'
import color from 'color'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import ExpandLessIcon from '@material-ui/icons/ExpandLess'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import StopIcon from '@material-ui/icons/Stop'

import { allSettled } from '../../helpers/utils'
import Log from '../../controller/Log'
import componentOptions from '../componentOptions'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	value: {
		float: 'left',
		lineHeight: '32px',
		marginRight: '10px'
	},
	borderLeftTransparent: {
		borderLeftColor: 'transparent !important'
	},
	borderLeftColor: {
		borderLeftColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightTransparent: {
		borderRightColor: 'transparent !important'
	},
	borderRightColor: {
		borderRightColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightColorSecondary: {
		borderRightColor: color(theme.palette.secondary.main).alpha(0.5).rgb().toString() +  '!important'
	}
}));


/*
 * ACTIONS
 *
 */
// BlindLevelAction
function BlindLevelAction(props) {
	const { device, stateKey, config } = props;
	const classes = useStyles();
	
	//
	const { min, max } = { 'min': 0, 'max': 100, ...device.getStateProperties(stateKey) };
	const up = max;
	const down = min;
	
	//
	const activityState = config[stateKey + '-activityState'] !== undefined ? config[stateKey + '-activityState'] : (config.activityState !== undefined ? config.activityState : componentOptions.BlindLevelAction.activityState.val);
	const stopAction = config[stateKey + '-stopAction'] !== undefined ? config[stateKey + '-stopAction'] : (config.stopAction !== undefined ? config.stopAction : componentOptions.BlindLevelAction.stopAction.val);
	const [activity, setActivity] = useState(null);
	
	useEffect(() => {
		
		// state updates
		const stateChange = (sKey, s, d) => {
			activityState && stopAction && sKey === activityState && setActivity(s.val);
		}
		
		// listen for updates
		device.listen(stateChange);
		
		// request initial device states
		allSettled(device.requestDeviceStates([activityState], true))
			.catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack)); // 'level', 
		
		// componentWillUnmount
		return () => device.unlisten(stateChange);
		
	}, [device, down, activityState, stopAction]);
	
	const onGoUp = () => {
		//setLevel(up);
		setActivity(true);
		device.setDeviceState(stateKey, up)
			.catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	const onGoDown = () => {
		//setLevel(down);
		setActivity(true);
		device.setDeviceState(stateKey, down)
			.catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	const onStop = () => {
		setActivity(false);
		device.setDeviceState(stopAction, true)
			.catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	const ref = useCallback(el => {
		window.dispatchEvent(new Event('resize'));
	}, []);
	
	return (
		<ButtonGroup size="small">
			<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderRightColor : classes.borderRightTransparent))} onClick={onGoUp}><ExpandLessIcon /></Button>
			{activity !== null && <Button  ref={ref} color="secondary" className={clsx(!activity ? [ classes.borderLeftTransparent, classes.borderRightTransparent ] : classes.borderRightColorSecondary)} onClick={onStop} disabled={!activity}><StopIcon /></Button>}
			<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderLeftColor : classes.borderLeftTransparent))} onClick={onGoDown}><ExpandMoreIcon /></Button>
		</ButtonGroup>
	);
}


export default {
	body: {
	},
	action: {
		BlindLevelAction
	}
}
