import React, { useState, useEffect } from 'react'
import clsx from 'clsx'

import Events from '../../controller/Events'
import Notifications from '../../controller/Notifications'
import Pro from '../../controller/Pro'
import Settings from '../../controller/Settings'

import Icon from '../../components/Icon'
import { getInitialTab, getTabs } from './Tabs'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Badge from '@material-ui/core/Badge'
import NotificationsIcon from '@material-ui/icons/Notifications'
import DonateIcon from '@material-ui/icons/Favorite'
import SettingsIcon from '@material-ui/icons/Settings'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	appBar: props => ({
		flexGrow: 1,
		boxShadow: 'none',
		transform: 'none',
		flexDirection: props.hideTopBar !== true ? 'column' : 'initial'
	}),
	toolbar: {
		backgroundColor: theme.palette.background.main,
		color: '#fff',
	},
	menuButton: {
		marginRight: theme.spacing(2)
	},
	title: {
		flexGrow: 1
	},
	notConnected: {
		color: '#f30'
	},
	hide: {
		display: 'none'
	},
	vCentered: {
		margin: 'auto 0'
	},
	tabIcon: {
		color: '#fff',
		paddingRight: '6px',
		width: '1em',
		height: '1em',
		display: 'inline-block',
		fontSize: '1.5rem',
		transition: 'fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
		flexShrink: 0,
		userSelect: 'none'
	},
}));


export default function TopBar(props) {
	const classes = useStyles({ hideTopBar: Settings.get('hideTopBar') });
	
	// get tab icon / title
	const [header,setHeader] = useState('->' + (Settings.get('topBarTitle') || Settings.get('pageTitle') || 'jarvis - Smart Home Overview'));
	useEffect(() => {
		
		// get initial tab
		const initialTab = getInitialTab();
		if (initialTab) {
			const tabs = getTabs();
			const tab = tabs[tabs.findIndex(tab => tab.id === initialTab)] || tabs[0];
			if (tab) {
				setHeader(tab.icon + '->' + tab.title);
			}
		}
		
		// listen for changes
		const handleEvent = e => e && setHeader(e.icon + '->' + e.title);
		
		const events = new Events();
		events.on('tabs.navigate', handleEvent);
		return () => events.off('tabs.navigate', handleEvent);
		
	}, []);
	
	
	const [icon, title] = header.split('->');
	return (
		<AppBar id="AppBar" position="static" className={clsx('jarvis-AppBar', classes.appBar)} color="inherit">
			
			{Settings.get('hideTopBar') !== true && (
			<Toolbar className={clsx('jarvis-Toolbar', classes.toolbar)} variant="dense">
				<Typography variant="h6" className={classes.title}>
					{icon && (
					<Icon
						icon={icon}
						className={classes.tabIcon}
						/>
					)}
					
					<span> {title}</span>
				</Typography>
				<TopBarIcons />
				
			</Toolbar>)}
			
			{props.children}
		</AppBar>
	);
}

export function TopBarIcons(props) {
	const classes = useStyles();
	
	const _isMobile = Settings.get('_isMobile') || Settings.get('_isTablet');
	
	const [unread,setUnread] = useState([]);
	useEffect(() => {
		const NotificationsClass = new Notifications();
		NotificationsClass.get('unread', res => setUnread(res));
		NotificationsClass.on('unread', res => setUnread(res));
		
	}, []);
	
	const events = new Events();
	const navigate = page => events.emit('page.navigate', page);
	
	return (
		<>
			<IconButton classes={{ root: classes.vCentered }} color="inherit" onClick={() => navigate('donation')}>
				<DonateIcon />
			</IconButton>
			{Pro.isPro() && (
			<IconButton classes={{ root: classes.vCentered }} color="inherit" onClick={() => navigate('notifications')}>
				<Badge badgeContent={unread.length} color="secondary">
					<NotificationsIcon />
				</Badge>
			</IconButton>)}
			<IconButton classes={{ root: clsx(classes.vCentered, _isMobile && classes.hide) }} id="SettingsButton" color="inherit" onClick={() => navigate('configuration')}>
				<SettingsIcon />
			</IconButton>
		</>
	);
}
