import React from 'react'
import clsx from 'clsx'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	closeButton: {
		position: 'absolute',
		right: theme.spacing(1),
		top: theme.spacing(1),
		color: theme.palette.grey[500],
	},
	Dialog: {},
	DialogDense: {
		padding: theme.spacing(2, 0)
	},
	DialogNoPadding: {
		padding: 0
	},
	DialogTitle: {
		marginRight: theme.spacing(4)
	},
	CustomDialogTitle: {
	},
	DialogTitleTypography: {
		//padding: theme.spacing(2)
	},
	DialogContent: {
		backgroundColor: theme.palette.background.default
	},
	CustomDialogContent: {
	},
	DialogActions: {
		margin: 0,
		padding: theme.spacing(1),
	},
	CustomDialogActions: {
	}
}));


export default function CustomDialog(props) {
	let { id, header, onClose, children, actions, dense, noPadding, disableBackdropClick, disableEscapeKeyDown, classes: customClasses, ...otherProps } = props;
	
	customClasses = customClasses || {}
	const classes = useStyles(customClasses);
	
	return (
		<Dialog
			classes={{ paper: clsx(classes.Dialog, customClasses.Dialog, id) }}
			PaperProps={{
				'id': id
			}}
			
			disableBackdropClick={disableBackdropClick !== undefined ? disableBackdropClick : true}
			disableEscapeKeyDown={disableEscapeKeyDown !== undefined ? disableEscapeKeyDown : true}
			onClose={onClose}
			
			{...otherProps}
			>
			
			{header && <DialogTitle
				classes={{ root: clsx(classes.DialogTitle, customClasses.CustomDialogTitle) }}
				disableTypography
				>
				
				{typeof header !== 'string' ? header : <Typography className={classes.DialogTitleTypography} variant="h6">
					{header}
				</Typography>}
				{props.onClose &&
				<IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
					<CloseIcon />
				</IconButton>}
			</DialogTitle>}
			
			<DialogContent id='DialogContent' className={clsx(classes.DialogContent, customClasses.CustomDialogContent, dense && classes.DialogDense, noPadding && classes.DialogNoPadding)} dividers>
				{children}
			</DialogContent>
			
			<DialogActions classes={{ root: clsx(classes.DialogActions, customClasses.CustomDialogActions) }}>
				{actions}
			</DialogActions>
		</Dialog>
	);
}
