import React from 'react'

import Inputs from '../../components/Inputs'
import { DeviceActionsConfiguration } from '../../modules/StateList/StateList.settings'


export default {
	devices: ['device', 'divider'],
	devicesStates: {
		selectable: ['primaryStateKey'],
		lastUpdateOption: true
	},
	DeviceBodyConfiguration,
	DeviceActionsConfiguration
}

/*
 *
 */
function DeviceBodyConfiguration(props) {
	const { device, options, onChange, elements = {} } = props;
	
	if (options.type !== 'device') {
		return <Inputs.Fieldset>
			<Inputs.FieldsetItem>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	}
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Body'}>
			<Inputs.FieldsetItem container>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} noPadding>
				<Inputs.Text
					key='label'
					id='label'
					
					placeholder={device.name}
					val={options.label || ''}
					label='Device Label'
					onChange={onChange}
					/>
				
				<Inputs.Switch
					id="lastUpdate"
					label='Display Last Update'
					
					val={options.lastUpdate !== undefined ? options.lastUpdate : true}
					onChange={onChange}
					/>
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}
