import { sort, allSettled } from '../helpers/utils'
import Connection from './Connection'
import Adapters from '../config/adapters'
import Device from './Device'
import Log from './Log'
import i18n from './i18n'

import { v5 as uuid } from 'uuid'
const _copy = require('rfdc')();


/**
 * Devices
 *
 *
 */
export default class Devices {

	/*
	 * holds the raw data
	 */
	static rawDevices = {};
	
	/*
	 * holds the devices
	 */
	static Devices = {};
	
	/**
	 * Set devices
	 *
	 * @param	{Object}	devices
	 * @return	{Array<Promises>}
	 */
	static setDevices(deviceList) {
		
		let devices = {}
		let discardedDevices = {}
		let states = [];
		
		let processedDevices = 0, addedDevices = 0, ignoredDevices = 0;
		for (let deviceId in deviceList) {
			processedDevices++;
			
			// skip
			if (deviceList[deviceId].attributes && deviceList[deviceId].attributes.ignore === true) {
				ignoredDevices++;
				continue;
			}
			
			// correct options
			deviceList[deviceId].id = deviceId;
			deviceList[deviceId].function = deviceList[deviceId].function || 'other';
			deviceList[deviceId].options = { ...deviceList[deviceId].options, ...deviceList[deviceId].jarvis }
			delete deviceList[deviceId].jarvis;
			
			// get device and its current state values
			try {
				devices[deviceId] = new Device(deviceList[deviceId]);
				//Object.freeze(devices[deviceId]);
				
				states = [ ...states, ...devices[deviceId].requestDeviceStates(null, true) ];
				addedDevices++;
			}
			catch(err) {
				discardedDevices[deviceId] = deviceList[deviceId];
				Log._('Devices', err.message, 'warn');
			}
		}
		
		// remember raw device list
		Devices.rawDevices = _copy(deviceList);
		
		// remember devices
		Devices.Devices = devices;
		
		// 
		return new Promise(resolve => {
			allSettled(states).then(res => {
				resolve({
					devices: devices,
					states: res,
					processedDevices: processedDevices,
					addedDevices: addedDevices,
					ignoredDevices: ignoredDevices,
					discardedDevices: discardedDevices
				});
			}).catch(() => {});
		});
	}
	
	/**
	 * Get devices
	 *
	 * @param	void
	 * @return	Object
	 */
	static getDevices(options = {}) {
		
		if (typeof options === 'boolean') {
			options = { raw: options }
		}
		
		let devices = options.raw ? Devices.rawDevices : Devices.Devices;
		devices = options.copy ? _copy(devices) : devices;
		
		if (options.sort) {
			devices = sort(devices, 'name');
		}
		
		return devices;
	}
	
	/**
	 *
	 *
	 */
	static getDevice(deviceId) {
		return deviceId && Devices.Devices[deviceId] ? Devices.Devices[deviceId] : null;
	}
	
	/**
	 *
	 *
	 */
	static importDevices(requestedAdapters = Object.keys(Adapters)) {
		const socket = Connection.getConnection;
		
		let gotAdapters = [];
		requestedAdapters = Array.isArray(requestedAdapters) ? requestedAdapters : [requestedAdapters];
		requestedAdapters.forEach(requestedAdapter => {
			
			gotAdapters.push(new Promise((resolve, reject) => {
				try {
					const adapter = Adapters[requestedAdapter];
					
					// get objects (either device, channel or state; depends on adapter structure)
					socket.getObjectView('system', adapter.deviceObjectType || 'device', { startkey: adapter.namespace + '.0', endkey: adapter.namespace + '.99' }).then(result => {
						
						// parse root
						if (adapter.root) {
							let addDevicesToRoot = Promise.resolve([]);
							
							// add devices (if not given)
							if (adapter.deviceObjectType !== 'device') {
								addDevicesToRoot = new Promise(resolve => {
									socket.getObjectView('system', 'device', { startkey: adapter.namespace + '.0', endkey: adapter.namespace + '.99' }).then(resolve);
								});
							}
							
							addDevicesToRoot.then(additionalResult => {
								
								// convert structure
								const [, objects ] = Devices._convertStructure([{
									'status': 'fulfilled',
									'value': {
										'type': 'objects',
										'retrieved': [ ...result, ...additionalResult ]
									}
								}]);
								
								// get devices from root
								adapter.root(objects, adapter)
									.then(devices => {
										resolve(devices.map(device => {
											return {
												'status': 'fulfilled',
												'value': {
													...device,
													'attributes': {
														'imported': true,
														'manufacturer': {
															'name': adapter.default,
															'namespace': adapter.namespace
														}
													}
												}
											}
										}));
									})
									.catch(err => resolve([]));
							});
						}
						
						//
						else {
							Devices.parseDevices(result).then(resolve).catch(reject);
						}
					})
					.catch(err => {
						const errorMessage = i18n.t('Could not load any adapter devices, channels or states.');
						Log._('Devices', errorMessage + '!', 'error', true, err.stack);
						reject(new Error(errorMessage));
					});
				}
				catch(err) {
					const errorMessage = i18n.t('Adapter structure not defined for adapter %adapter').replace(/%adapter/g, requestedAdapter);
					Log._('Devices', errorMessage + '!', 'error', true, err.stack);
					reject(new Error(errorMessage));
				}
			}));
		});
		
		return new Promise(resolve => {
			allSettled(gotAdapters).then(result => {
				
				let devices = [];
				result.forEach(res => res.value && (devices = devices.concat(res.value)));
				resolve(devices);
			})
		});
	}
	
	/**
	 *
	 *
	 */
	static importEnums(type = 'functions', requestedAdapters = []) {
		const socket = Connection.getConnection;
		
		let gotEnums = [];
		return new Promise((resolve, reject) => {
			socket.getObjectView('system', 'enum')
				.then(enums => {
					enums.forEach(en => {
						if (en.id.indexOf('enum.functions.control_center') === -1 && en.id.indexOf('enum.' + type) > -1 && en.value && en.value.common && en.value.common.members && Array.isArray(en.value.common.members) && en.value.common.members.length > 0) {
							
							// convert structure
							let states = [];
							en.value.common.members.forEach(state => {
								const adapter = state.substr(0, state.indexOf('.'));
								if (requestedAdapters.indexOf(adapter) > -1) {
									states.push({ id: state });
								}
							});
							
							gotEnums.push(Devices.parseDevices(states, { 'function': en.id.indexOf('enum.functions.') > -1 ? en.id.replace('enum.functions.', '') : null }));
						}
					});
			
					allSettled(gotEnums).then(result => {
						let devices = [];
						result.forEach(res => {devices = devices.concat(res.value)});
						resolve(devices);
					})
				})
				.catch(err => {
					let errorMessage = i18n.t('Enums could not be retrieved');
					Log._('Devices', errorMessage + '!', 'error', true, err.stack);
					reject(new Error(errorMessage));
				});
		});
	}
	
	/**
	 *
	 */
	static _convertStructure(result) {
		
		// extract result
		let objects = {}
		let states = {}
		
		result.forEach(res => {
			
			if (res.status === 'fulfilled' && res.value && (res.value.type === 'devices' || res.value.type === 'channels' || res.value.type === 'objects')) {
				res.value.retrieved.forEach(obj => objects[obj.id] = obj.value);
			}
			else if (res.status === 'fulfilled' && res.value && res.value.type === 'states') {
				states = res.value.retrieved;
			}
		});
		
		return [ states, objects ];
	}
	
	/**
	 *
	 *
	 */
	static parseDevices(states, options = {}) {
		const socket = Connection.getConnection;
		
		//
		let devices = [];
		for (let parse of states) {
			let state = parse.id;
			let namespace = state.substr(0, state.indexOf('.'));
			let adapter = Adapters[namespace];
			
			// device template
			let tplDevice = {
				'id': uuid(state, '4eaf6392-6a70-4802-b343-5ff1a1673f39'),
				'name': 'unknown device (state ' + state + ')',
				'function': options.function || 'unknown',
				'states': {},
				'options': {},
				'attributes': {
					'imported': true,
					'manufacturer': {
						'name': adapter.default,
						'namespace': namespace
					}
				}
			}
			
			// verify device by pre-defined pattern
			if (adapter.devicePattern && RegExp('^' + adapter.namespace + '\\.\\d\\.' + adapter.devicePattern + '$').test(state) === false) {
				continue;
			}
			
			// get and verify device information
			devices.push(new Promise((resolve, reject) => {
				
				// get device structure
				allSettled([
					new Promise(resolve => socket.getObjectView('system', 'device', { startkey: state, endkey: state + '.\u9999' }).then(res => resolve({ 'retrieved': res, 'type': 'devices' }))),
					new Promise(resolve => socket.getObjectView('system', 'channel', { startkey: state, endkey: state + '.\u9999' }).then(res => resolve({ 'retrieved': res, 'type': 'channels' }))),
					new Promise(resolve => socket.getObjectView('system', 'state', { startkey: state, endkey: state + '.\u9999' }).then(res => resolve({ 'retrieved': res, 'type': 'objects' }))),
					new Promise(resolve => socket.getStates(state + '.*').then(res => resolve({ 'retrieved': res, 'type': 'states' })))
					
				]).then(result => {
					
					// convert result to object
					const [ states, objects ] = Devices._convertStructure(result);
					const deviceStructure = {
						'root': state,
						'list': Object.keys(states),
						'states': states,
						'objects': objects
					}
					
					// parse device structure and thus retrieve function and states
					adapter.parse(deviceStructure, options)
						.then(device => {
							
							resolve({
								...tplDevice,
								...device,
								'id': device.name ? device.name.toLowerCase().replace(/ /g, '') + '_' + uuid(state, '4eaf6392-6a70-4802-b343-5ff1a1673f39').substr(0, 5) : uuid(state, '4eaf6392-6a70-4802-b343-5ff1a1673f39')
							});
						})
						.catch(err => {
							const errorMessage = i18n.t(err.message).replace('%state', state) + '!';
							Log._('Devices', errorMessage, 'error', true, err.stack);
							
							reject({
								...tplDevice,
								'attributes': {
									...tplDevice.attributes,
									'note': errorMessage
								}
							});
						});
				})
				.catch(err => {
					let errorMessage = i18n.t('Could not retrieve state %state').replace('%state', state);
					Log._('Devices', errorMessage + '!', 'error', true, err.stack);
					reject(new Error(errorMessage));
				});
			}));
		}
		
		return allSettled(devices);
	}
	
	/**
	 *
	 *
	 */
	static forEachDevice(list, callback) {
		
		return list.map((item, index) => {
			
			// defaults
			item.secondaryStatesKeys = item.secondaryStatesKeys || [];
			
			//
			let device = (Devices.getDevice(item.deviceId) || item || {});
			device.id = device.id || (device.name && device.name.toLowerCase().replace(/ /g, ''));
			
			return callback && callback(device, index, item);
		});
	}
}
