import React from 'react'
import { v4 as uuid } from 'uuid'

import LinearProgress from '@material-ui/core/LinearProgress'

import config from './config'
import layoutTemplate from './template.layout.json'

import Cache from '../controller/Cache'
import Connection from '../controller/Connection'
import Devices from '../controller/Devices'
import i18n from '../controller/i18n'
import Log from '../controller/Log'
import Settings from '../controller/Settings'


const _clone = require('rfdc')();

/*
 * INTRODUCTION_STEPS
 */
export const INTRODUCTION_STEPS = [
	{
		style: { maxWidth: 'inherit' },
		target: 'body',
		placement: 'center',
		title: '#introduction.title',
		content: '#introduction.content',
		cb: () => {
			Settings.set('joyrideIntroduction', true);
			Connection.connection.setState(config.get('NODE_SETTINGS'), JSON.stringify({ ...Settings.getSettings(), 'joyrideIntroduction': true }))
		}
	},
	{
		style: { margin: '0 0 0 auto' },
		target: '#SettingsButton',
		title: '#settings.title',
		content: '#settings.content',
		secondaryButton: '#settings.buttons.manually',
		secondaryAction: props => {
			props.setRun(false);
			document.getElementById('SettingsButton').click();
		},
		primaryButton: '#settings.buttons.automatically',
		primaryAction: props => {
			let devices = {}
			let deviceList = {}
			
			Devices.importDevices(config.get('DEFAULT_ADAPTERIMPORT').concat(['daswetter']))
				.then(result => {
					result.forEach(res => {
						if (res.status === 'fulfilled') {
							let device = res.value;
							
							devices[device.function] = devices[device.function] || [];
							devices[device.function].push(device);
							deviceList[device.id] = device;
						}
					});
					
					// get layout from template
					let layout = _clone(layoutTemplate);
					
					// change config of Weather module
					const weather = Cache.get('daswetter');
					if (weather) {
						// add credentials
						layout[0].tabs[0].columns[0][1].moduleConfig = {
							"apikey": weather.affiliate_id,
							"localid": weather.localidad
						}
					}
					else {
						// remove widget if credentials could not be retrieved
						layout[0].tabs[0].columns[0].splice(1, 1);
					}
					
					// define widget template
					const tplWidget = {
						"moduleConfig": {},
						"devices": [],
						"module": "StateList",
						"title": ""
					}
					
					// define functions which devices shall be adapted
					const widgetFunctions = [
						{ 'function': 'light', 'icon': 'mdi-lightbulb-on', 'column': 1 },
						{ 'function': 'blind', 'icon': 'mdi-window-shutter-open', 'column': 2 },
						{ 'function': 'heating', 'icon': 'mdi-radiator', 'column': 2 },
						{ 'function': 'window', 'icon': 'mdi-window-closed-variant', 'column': 2 }
					];
					
					// add devices
					widgetFunctions.forEach(func => {
						let widget = _clone(tplWidget);
						widget.id = uuid();
						widget.title = i18n.t(func.function);
						widget.icon = func.icon;
						
						devices[func.function] = devices[func.function] || [];
						for (let device of devices[func.function]) {
							
							if (!device.states) {
								continue;
							}
							
							let d = {
								"type": "device",
								"deviceId": device.id
							}
							
							if (device.function === 'light') {
								d = {
									...d,
									"showState": false,
									"actionElement": "SwitchAction", 
									"primaryStateKey": device.states && device.states.power ? 'power' : (device.states && device.states.level ? 'level' : '')
								}
							}
							else if (device.function === 'blind') {
								d = {
									...d,
									"showState": false,
									"actionElement": "BlindLevelAction", 
									"primaryStateKey": "level"
								}
								
							}
							else if (device.function === 'heating') {
								d = {
									...d,
									"showState": true,
									"primaryStateKey": "temperature",
									"secondaryStatesKeys": [
										"setTemperature",
										"humidity"
									]
								}
								
							}
							else if (device.function === 'window') {
								d = {
									...d,
									"primaryStateKey": "open"
								}
								
							}
							
							widget.devices.push(d);
						}
						
						layout[0].tabs[0].columns[func.column].push(widget);
					});
					
					// save
					const socket = Connection.getConnection;
					const promises = [
						socket.setState(config.get('NODE_LAYOUT'), JSON.stringify(layout)),
						socket.setState(config.get('NODE_DEVICES'), JSON.stringify(deviceList))
					];
					
					Promise.all(promises)
						.then(() => window.location.reload())
						.catch(err => console.error(err));
				})
				.catch(err => {
					console.error(err);
					Log._('Joyride', 'IMPORT_DEVICES: ' + err.message, 'error');
				});
		}
	},
	{
		style: { maxWidth: 'inherit' },
		target: 'body',
		placement: 'center',
		title: '#jarvis.title',
		content: <LinearProgress />,
		buttons: false
	}
];


