import Connection from '../../../controller/Connection'
import { allSettled } from '../../../helpers/utils'


/**
 *
 */
export function setPlayer(playerNode) {
}

/**
 *
 */
export function getPlayers(adapter) {
	let root = adapter + '._playing';
	
	return new Promise(resolve => {
		const socket = Connection.connection;
		socket.getObjectView('system', 'channel', { startkey: root, endkey: root + '.\u9999' }).then(devices => {
			
			let promises = [];
			for (let device of devices) {
				
				// get device states
				promises.push(new Promise((resolve, reject) => {
					socket.getStates(device.id + '.*').then(states => {
						if (states[device.id + '._Controls.playback.play'] && states[device.id + '.player'] && states[device.id + '.player'].val) {
							resolve({
								'id': device.id.replace(root + '.', ''),
								'name': states[device.id + '.player'].val
							});
						}
						else {
							reject();
						}
					});
				}));
			}
			
			allSettled(promises).then(devices => {
				let players = [];
				devices.forEach(device => device.status === 'fulfilled' && players.push(device.value));
				resolve(players);
			});
		});
	});
}

/**
 *
 */
function convertToSeconds(val) {
	return parseInt(val) * 60;
}

/*
 *
 */
export default {
	"_namespace": "",
	
	// information
	"artist": "._playing.{PLAYER}.Metadata.grandparentTitle",
	"album": "._playing.{PLAYER}.Metadata.parentTitle",
	"track": "._playing.{PLAYER}.Metadata.title",
	"year": "._playing.{PLAYER}.Metadata.year",
	"cover": ".events.thumb",
	
	// actions
	"play": {
		"state": "._playing.{PLAYER}.playing", // isPlaying
		"action": "._playing.{PLAYER}._Controls.playback.play"
	},
	"pause": {
		"action": "._playing.{PLAYER}._Controls.playback.pause"
	},
	"resume": {
		"action": "._playing.{PLAYER}._Controls.playback.play"
	},
	"stop": {
		"action": "._playing.{PLAYER}._Controls.playback.stop"
	},
	"previous": {
		"action": "._playing.{PLAYER}._Controls.playback.skipPrevious"
	},
	"next": {
		"action": "._playing.{PLAYER}._Controls.playback.skipNext"
	},
	
	// attributes
	"progress": {
		"state": null
	},
	"duration": {
		"state": "._playing.{PLAYER}.Metadata.duration",
		"convert": convertToSeconds
	},
	
	// options
	"repeat": {
		"state": "._playing.{PLAYER}._Controls.playback.setRepeat",
		"action": "._playing.{PLAYER}._Controls.playback.setRepeat",
		"display": {
			"off": "off",
			"item": "item",
			"all": "all"
		}
	},
	"shuffle": {
		"state": "._playing.{PLAYER}._Controls.playback.setShuffle",
		"action": "._playing.{PLAYER}._Controls.playback.setShuffle",
		"display": {
			"off": "0",
			"on": "1"
		}
	},
	"volume": {
		"state": "._playing.{PLAYER}._Controls.playback.setVolume",
		"action": "._playing.{PLAYER}._Controls.playback.setVolume"
	}
}
