import React, { useState, useEffect, useRef, useCallback } from 'react'
import clsx from 'clsx'

import config from '../../config/config'
import Cache from '../../controller/Cache'
import Events from '../../controller/Events'
import Settings from '../../controller/Settings'
import useWindowDimensions from '../../helpers/useWindowDimensions'

import Icon from '../../components/Icon'
import { TopBarIcons } from '../../pages/TopBar/TopBar'

import MuiTabs from '@material-ui/core/Tabs'
import MuiTab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	icon: {
		...theme.components.icon,
		color: '#fff',
		//paddingBottom: '6px',
		margin: '0 !important',
		width: '1em',
		height: '1em',
		display: 'inline-block',
		fontSize: '1.5rem',
		transition: 'fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
		flexShrink: 0,
		userSelect: 'none'
	},
	tabs: {
		backgroundColor: theme.palette.background.main,
		borderBottom: '5px solid ' + theme.palette.background.default,
		paddingLeft: theme.spacing(),
		color: '#fff',
		width: 'calc(100% - ' + theme.spacing() + 'px)',
		zIndex: 99,
	},
	fixed: {
		position: 'fixed',
		top: 0
	},
	indicator: {
		backgroundColor: '#fff',
	},
	fullscreen: {
		//marginLeft: -theme.spacing(),
		//marginTop: '-4px',
		position: 'relative',
		zIndex: 1001,
		overflow: 'hidden'
	},
	grid: {
		//paddingBottom: '30px'
	},
	TopBarIcons: {
		display: 'flex',
		backgroundColor: theme.palette.background.main,
		borderBottom: '5px solid ' + theme.palette.background.default,
		color: '#fff',
		paddingRight: theme.spacing(),
	}
}));



export function getTabs() {
	const layout = Settings.getLayout();
	return (layout && layout[0] && layout[0].tabs) || [];
}

// get initial selected tab
export function getInitialTab() {
	const tabs = getTabs();
	const url = window.location.href;
	let tabId = null;
	
	if (url.indexOf('tab=') > -1) {
		const paramStart = url.indexOf('tab=')+4;
		const param = url.indexOf('&', paramStart) === -1 ? url.substr(paramStart) : url.substr(paramStart, url.length - url.indexOf('&', paramStart) + 4);
		let [, tabIndex] = param.split('-');
		
		tabIndex = parseInt(tabIndex);
		tabId = (tabs[tabIndex] && tabs[tabIndex].id) || '';
	}
	else if (Cache.get('tab')) {
		tabId = Cache.get('tab');
		
		// tab exists
		if (tabs.findIndex(t => t.id === tabId) === -1) {
			tabId = null;
		}
	}
	
	// return
	return tabId || (tabs && tabs[0] && tabs[0].id) || null;
}

export default function Tabs(props) {
	const classes = useStyles();
	
	const tabs = getTabs();
	
	// tab
	const selectedWidget = useRef(null);
	const [selectedTab, setSelectedTab] = useState();
	const handleChange = (event, tab) => {
		const events = new Events();
		events.emit('tabs.navigate', tabs[tab]);
	}
	
	useEffect(() => {
		const events = new Events();
		
		// listen for changes via event
		const handleEvent = (t, w = null) => {
			
			// change window.location
			const delimiter = config.get('URL_PARAM_DELIMITER');
			const url = window.location.href;
			const tabKey = t.title.toLowerCase().replace(/ /g, '') + '-' + t.index;
			
			if (url.indexOf('tab=') === -1) {
				window.history.pushState('', '', url + (url.indexOf(delimiter) === -1 ? delimiter : '&') + 'tab=' + tabKey);
			}
			else {
				const allParams = url.substr(url.indexOf(delimiter)+1).split('&').filter(param => param.indexOf('tab=') === -1); // filter out current tab selection
				window.history.pushState('', '', url.substr(0, url.indexOf(delimiter)) + (allParams.length > 0 ? (delimiter + allParams.join('&') + '&') : delimiter) + 'tab=' + tabKey);
			}
			
			// focus widget
			if (w) {
				selectedWidget.current = w;
				events.emit('widget.navigate', selectedWidget.current);
			}
			
			setSelectedTab(t.id);
			Cache.set('tab', t.id);
		}
		
		// listen for changes via url
		const handleUrl = () => {
			const initialTab = getInitialTab();
			setSelectedTab(initialTab);
		}
		
		// tab
		events.on('tabs.navigate', handleEvent);
		events.on('url.tab', handleUrl);
		
		// initial tab
		handleUrl();
		
		return () => {
			events.off('tabs.navigate', handleEvent);
			events.off('url.tab', handleEvent);
		}
		
	}, []);
	
	useEffect(() => {
		if (selectedWidget.current !== null) {
			const events = new Events();
			events.emit('widget.navigate', selectedWidget.current);
			selectedWidget.current = null;
		}
		
	}, [selectedTab]);
	
	// scroll
	const [fixed, setFixed] = useState(false);
	
	let tabBarProps = useRef({});
	const handleScroll = useCallback(() => {
		const threshold = (tabBarProps.current.top + tabBarProps.current.height - 53);
		const fixed = window.scrollY >= threshold;
		
		setFixed(prevFixed => {
			if (prevFixed !== fixed) {
				document.getElementById('root').style.paddingTop = fixed ? (threshold-5-8) + 'px' : 0;
				return fixed;
			}
			
			return prevFixed;
		});
		
	}, [tabBarProps]);
	
	useEffect(() => {
		const tabsElement = document.getElementById('Tabs');
		
		tabBarProps.current = !tabsElement ? {} : {
			top: tabsElement.offsetTop,
			height: tabsElement.offsetHeight
		}
		
		window.addEventListener('scroll', handleScroll);
		return () => window.removeEventListener('scroll', handleScroll, 0);
		
	}, [handleScroll]);
	
	// render tabs
	const renderedTabs = tabs.map((tab, i) => {
		tab.index = i;
		
		return (
			<MuiTab
				key={'tab' + i}
				className={'jarvis-Tab jarvis-Tab-' + tab.title.toLowerCase().replace(/ /g, '')}
				icon={<Icon
						icon={tab.icon}
						className={classes.icon}
						/>}
				label={!fixed && Settings.getSetting('tabsHideLabels') !== true && tab.title ? tab.title : null}
				/>
		);
	});
	
	const val = tabs.findIndex(tab => tab.id === selectedTab);
	return (
		<>
			{tabs.length > 1 && (
			<MuiTabs
				id="Tabs"
				value={val > 0 ? val : 0}
				onChange={handleChange}
				variant="scrollable"
				classes={{
					root: clsx('jarvis-Tabs', classes.tabs, fixed && classes.fixed),
					indicator: classes.indicator
				}}>
				
				{renderedTabs}
			</MuiTabs>)}
			
			{Settings.get('hideTopBar') === true && (
			<div className={classes.TopBarIcons}>
				<TopBarIcons {...props} />
			</div>)}
		</>
	);
}


export function TabPanel(props) {
	const { children, id, fullscreen } = props;
	const classes = useStyles();
	
	
	// tab
	const [selectedTab, setSelectedTab] = useState();
	
	useEffect(() => {
		const events = new Events();
		
		// listen for changes via event
		const handleEvent = t => setSelectedTab(t.id);
		
		// listen for changes via url
		const handleUrl = () => {
			const initialTab = getInitialTab();
			setSelectedTab(initialTab);
		}
		
		// tab
		events.on('tabs.navigate', handleEvent);
		events.on('url.tab', handleUrl);
		
		// initial tab
		handleUrl();
		
		return () => {
			events.off('tabs.navigate', handleEvent);
			events.off('url.tab', handleEvent);
		}
		
	}, []);
	
	// get panel height based on viewport
	const { height: windowHeight } = useWindowDimensions();
	const [panelHeight, setPanelHeight] = useState(windowHeight);
	
	useEffect(() => {
		const AppBarHeight = document.getElementById('AppBar').offsetHeight;
		setPanelHeight(windowHeight - AppBarHeight);
		
	}, [windowHeight]);
	
	/*
	 * Some components need to be visible upon loading, so proper initialise.
	 * Due to the 'hidden' attribute though, they might be hidden.
	 * This is a workaround to initially show everything for a short period of time and then hide according to tab settings.
	 *
	 */
	const [hidden, setHidden] = useState(null);
	const timerRef = useRef(null);
	useEffect(() => {
		timerRef.current = setTimeout(() => setHidden(selectedTab !== id), 10);
		return () => clearTimeout(timerRef.current);
		
	}, [selectedTab, id]);
	
	
	return (
		<Box
			role='tabpanel'
			hidden={hidden === null ? false : hidden}
			id={'tabpanel-' + id}
			className={clsx('jarvis-TabPanel', fullscreen ? classes.fullscreen : classes.grid)}
			style={{ visibility: hidden === null ? 'hidden' : 'visible', minHeight: panelHeight + 'px' }}
			>
			
			{children}
		</Box>
	);
}
